<template>
  <div class="classList-container">
    <Table
      ref="selection"
      no-data-text="暂无数据"
      :columns="columnsList"
      height="325"
      :data="classList">
    </Table>
  </div>
</template>

<script>
export default {
  name: 'index',
  props: {
    classList: Array
  },
  data () {
    return {
      columnsList: [
        {
          title: '班级名称',
          key: 'name',
          width: '200',
          align: 'center'
        },
        {
          title: '授课类型',
          key: 'classType',
          align: 'center',
          render: (h, params) => {
            let classType = params.row.classType
            let classTypeName = ''
            switch (classType) {
              case 1:
                classTypeName = '在线'
                break
              case 2:
                classTypeName = '双师'
                break
              case 4:
                classTypeName = '面授'
                break
            }
            return h('span', {}, classTypeName)
          }
        },
        {
          title: '年份',
          key: 'year',
          align: 'center'
        },
        {
          title: '学期',
          key: 'termName',
          align: 'center'
        },
        {
          title: '年级',
          key: 'gradeName',
          align: 'center'
        },
        {
          title: '学科',
          key: 'subjectNames',
          align: 'center'
        },
        {
          title: '开课日期',
          key: 'startDate',
          align: 'center',
          render: (h, params) => {
            return h('span', {}, params.row.startDate.slice(0, 11))
          }
        },
        {
          title: '结束日期',
          key: 'endDate',
          align: 'center',
          render: (h, params) => {
            return h('span', {}, params.row.endDate.slice(0, 11))
          }
        },
        {
          title: '总金额',
          key: 'price',
          align: 'center'
        },
        {
          title: '教师',
          key: 'teacherNames',
          align: 'center'
        },
        {
          title: '教室',
          key: 'classroomName',
          align: 'center'
        }
      ]
    }
  }
}
</script>

