<template>
  <div class="layout">
    <div class="header">
      <h1 class="title">攒班配置</h1>
      <Button type="default" @click="openBatchImportHandler" class="btn">
        <Icon type="md-download" />
        批量导入
      </Button>
      <Button type="primary" style="margin-right: 10px;" @click="createCuanbanClass" class="btn">
        <Icon type="md-add" />
        新增班级
      </Button>
      <!-- <Button type="primary" style="margin-right: 10px;" @click="openStudentListHandler" class="btn">
        学员名单
      </Button> -->
    </div>
    <!-- 班级查询(含：攒班类型卡片) -->
    <div class="layout-card">
      <ClassSearch
        ref="classSearchComponent"
        @queryParams="getQueryParams"
      />
    </div>
    <!-- 攒班列表 -->
    <div class="cuanban-table">
       <CuanbanTable ref="cuanbanTableRef" :queryParams='queryParams' @updateCuanbanDemand="updateCuanbanDemand"></CuanbanTable>
    </div>
    <!-- 新增班级 -->
    <CreateClass
      ref="childCreateClass"
      :oldFormData="oldCuanbanDemand"
      :type="createClassType"
      :title="createClassTypeName"
      @updateTableData="updateTableData"
    />
    <!-- 学员名单 -->
    <StudentList ref="childStudentList" :city-code="queryParams.cityCode"></StudentList>
    <!-- 批量导入 -->
    <BatchImportWrapper
      ref="childBatchImportWrapper"
      @updateTableData="updateTableData"
      :cityCode="queryParams.cityCode"
    ></BatchImportWrapper>
  </div>
</template>

<script>
import CreateClass from './createClass'
import BatchImportWrapper from './batchImportWrapper'
import ErrorTargetTable from './errorTargetTable'
import StudentList from './studentList'
import ClassSearch from './classSearch'
import CuanbanTable from './cuanban-table'
export default {
  name: 'cuanbanConfig',
  components: {
    CreateClass,
    BatchImportWrapper,
    ErrorTargetTable,
    StudentList,
    ClassSearch,
    CuanbanTable
  },
  data () {
    return {
      oldCuanbanDemand: {}, // 旧攒班信息-获取攒班id
      createClassType: 1, // 1-新增---2-修改
      createClassTypeName: '', // 新增或修改班级组件title
      queryParams: {} // 请求参数
      // formData: {
      //   cityCode: '', // 城市分校
      //   year: new Date().getFullYear() + '', // 必填, // 年份
      //   serviceCenterId: '', // 服务中心ID
      //   termId: '', // 学期
      //   gradeId: '', // 年级
      //   subjectId: '', // 学科
      //   classLevel: '', // 班次级别
      //   status: '', // 攒班状态
      //   classtimeTimeClassify: '', // 上课时间
      //   classtimeTimePeriods: '', // 上课时段
      //   intention: '', // 意向度
      //   payStatus: '', // 缴费状态
      //   registStatus: '', // 报名状态
      //   creator: '', // 创建人
      //   receptionAdvisorEmpNo: '', // 接待顾问
      //   name: '', // 学员姓名
      //   phone: '', // 手机号
      //   studentCard: '', // 学员编号
      //   time: '',
      //   startCreateDate: '',
      //   endCreateDate: ''
      // }
    }
  },
  created () {

  },
  mounted () {

  },
  methods: {
    // 新增班级 模态窗
    openAddClassHandler () {
      this.$refs.childCreateClass.addClassHandler()
    },
    // 学员列表 engine函数
    openStudentListHandler () {
      this.$refs.childStudentList.studentListHandler()
    },
    // 批量导入 处理函数
    openBatchImportHandler () {
      this.$refs.childBatchImportWrapper.batchImportWrapperHandler()
    },
    // 新增攒班班级 处理函数
    createCuanbanClass () {
      this.openAddClassHandler()
      this.createClassType = 1
      this.createClassTypeName = '新增班级'
    },
    // 更新攒班需求
    updateCuanbanDemand (data) {
      console.log(data, '----')
      this.oldCuanbanDemand = data // 传：攒班id
      this.openAddClassHandler()
      this.createClassType = 2
      this.createClassTypeName = '修改班级'
    },
    // 刷新首页数据
    updateTableData () {
      this.$refs.classSearchComponent.getAssembleTotal()
      this.$refs.cuanbanTableRef.queryTableData(this.$refs.classSearchComponent.formData)
    },
    getQueryParams (params, status, isClear) {
      this.queryParams = { ...params, '_status': status, '_clear': isClear }
    }
  }
}
</script>

