<template>
    <!-- 修改考勤 -->
    <div>
        <p class="mgb-24">
            <span class="mgl-30">试听课程: {{auditionInfo.className || '--'}}</span>
            <span class="mgl-30">课次: {{auditionInfo.curriculumNum || '--'}}</span>
            <span class="mgl-30">主讲老师: {{auditionInfo.teacherName || '--'}}</span>
        </p>
        <p class="mgb-24">
            <span class="mgl-30">上课时间: {{auditionInfo.classTimeVal || '--'}}</span>
            <span class="mgl-30">服务中心: {{auditionInfo.serviceCenter || '--'}}</span>
            <span class="mgl-30">教室: {{auditionInfo.classroom || '--'}}</span>
        </p>
        <Form :label-width="95" inline>
            <FormItem label="考勤状态: ">
                <RadioGroup v-model="formValidate.isAttend">
                    <Radio :label="1">准时上课</Radio>
                    <Radio :label="2">迟到</Radio>
                    <Radio :label="0">缺勤</Radio>
                </RadioGroup>
            </FormItem>
            <div></div>
            <FormItem label="是否有ITS考勤: ">{{ auditionInfo.isItsAttendance === 1 ? '有' : '无' }}</FormItem>
            <FormItem label="考勤时间: ">{{ auditionInfo.attendanceTime || '--' }}</FormItem>
            <FormItem label="下次跟进时间: ">
                <DatePicker
                type="datetime" placeholder="请选择" style="width: 200px;"
                format="yyyy-MM-dd HH:mm:ss"
                v-model="formValidate.nextFollowTimeVal"
                :options="dateOptions"
                @on-change="dateChange($event, 'nextFollowTime')"
                ></DatePicker>
            </FormItem>
            <FormItem label="修改原因: " required>
                <Input v-model="formValidate.reason" type="textarea" placeholder="请输入" style="width: 300px" />
            </FormItem>
        </Form>
        <Row type="flex" justify="center" :gutter="20">
            <Col>
                <Button type="primary" @click="confirm" :loading="btnLoading">提交</Button>
            </Col>
            <Col>
                <Button @click="cancel">取消</Button>
            </Col>
        </Row>
    </div>
</template>

<script>

import { modifyAttendance } from '@/api/audition'

export default {
  name: 'ChangeAttendance',
  props: {
    auditionInfo: {
      // 试听信息
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data () {
    return {
      formValidate: {
        nextFollowTime: '', // 下次跟进时间
        nextFollowTimeVal: '', // 下次跟进时间
        reason: '', // 取消原因
        isAttend: 1 // 是否考勤：1 准时上课， 2迟到， 0缺勤
      },
      dateOptions: {
        disabledDate (date) {
          return date && date.valueOf() < Date.now() - 86400000
        }
      },
      btnLoading: false
    }
  },
  mounted () {
    this.formValidate.isAttend = this.auditionInfo.isAttendance || 0
  },
  methods: {
    dateChange (val, typeName) {
      this.formValidate[typeName] = val
    },
    async confirm () {
      if (!this.formValidate.reason) {
        this.$Message.warning({ content: '请填写修改原因' })
        return
      }
      try {
        const params = {
          ...this.formValidate,
          appointmentId: this.auditionInfo.id,
          auditionId: this.auditionInfo.auditionId,
          studentUid: this.auditionInfo.uid,
          curriculumNum: this.auditionInfo.curriculumNum,
          classId: this.auditionInfo.classId
        }
        this.btnLoading = true
        await modifyAttendance(params)
        this.btnLoading = false
        this.$emit('success')
        this.$emit('cancel')
      } catch (error) {
        this.btnLoading = false
        this.$Message.error({
          content: '修改考勤失败: ' + error.message,
          duration: 5
        })
      }
    },
    cancel () {
      this.$emit('cancel')
    }
  }
}
</script>

<style lang="less" scoped>
.mgl-30 {
  margin-left: 30px;
}

.mgb-24 {
  margin-bottom: 24px;
}
</style>
