          key: 'creatorName'
          // width: 100
        },
        {
          title: '创建时间',
          key: 'createDate'
          // width: 150
        },
        {
          title: '操作',
          key: 'operate',
          width: 80,
          // fixed: 'right',
          render: (h, { row }) => {
            if (!row.assembleList || row.assembleList.length === 0) {
              // let isApply = String(row.isShowApply) === '1' // 报名按钮是否置灰
              return h('a', {
                style: {
                  // fontSize: '14px',
                  lineHeight: '22px',
                  display: 'inline-block',
                  color: '#0f62fe',
                  textDecoration: 'none'
                },
                on: {
                  click: () => {
                    this.choiceBtnText = '报名'
                    this.rowData = row
                    this.rowData = { ...row, cityCode: this.queryParams.cityCode, authDeptId: this.userInfo.authDeptId, authDeptName: this.userInfo.authDeptName }
                    this.$refs['SearchApplyStuRef'].open(this.rowData)
                  }
                }
              }, '报名')
            }
          }
        }
      ],
      cuanbanFail: [
        {
          type: 'expand',
          width: 50,
          // fixed: 'left',
          render: (h, { row }) => {
            if (row.assembleList && row.assembleList && row.assembleList.length > 0) {
              return h(cuanbanTableExpendVue, {
                props: {
                  tableColumnsKey: 'cuanbanFail',
                  tableData: row.assembleList
                }
              })
            }
          }
        },
        {
          title: '班级名称',
          key: 'name',
          width: 200,
          // fixed: 'left',
          render: (h, { row }) => {
            let className = ''
            if (row.productType === '1') {
              className = ('【' + row.subjectName + '】' || '') + (row.gradeName || '') + (row.seasonName || '') + (row.classModelLevelName || '')
            } else if (row.productType === '2') {
              className = ('【' + row.subjectName + '】' || '') + (row.gradeName || '') + (row.classLevelName || '')
            }
            if (row.assembleList && row.assembleList && row.assembleList.length > 0) {
              return h('span',
                className
              )
            } else {
              return h('a',
                {
                  style: {
                    color: '#276BFC',
                    cursor: 'pointer'
                  },
                  on: {
                    click: () => {
                      this.openClassDetail(row.classId)
                    }
                  }
                },
                className
              )
            }
          }
        },
        {
          title: '攒班进度',
          key: 'assemblePlan',
          // width: 100,
          render: (h, { row }) => {
            if (row.assembleList && row.assembleList.length > 0) {
              return h('span', {
                style: {
                  color: '#393939'
                }
              }, row.assemblePlan ? row.assemblePlan : '--')
            } else {
              return h('a',
                {
                  style: {
                    color: '#276BFC',
                    cursor: 'pointer'
                  },
                  on: {
                    click: () => {
                      this.stuId = row.groupWorkId
                      this.classId = row.classId
                      this.$refs.childStudentList.studentListHandler()
                    }
                  }
                },
                row.assemblePlan ? row.assemblePlan : '--'
              )
            }
          }
        },
        {
          title: '产品类型',
          key: 'productTypeName',
          width: 100
        },
        {
          title: '授课类型',
          key: 'categoryType',
          // width: 100,
          render: (h, { row }) => {
            // categoryMap
            return h('span', this.categoryMap[row.categoryType])
          }
        },
        {
          title: '主讲老师',
          key: 'teacherEmpName'
          // width: 100
        },
        {
          title: '上课时间',
          key: 'className',
          // width: 150,
          render: (h, { row }) => {
            return h('span', {
            }, row.classTimeTypeName + row.period + row.classTime)
          }
        },
        {
          title: '服务中心',
          // width: 150,
          key: 'serviceCenterName'
        },
        {
          title: '教学点',
          // width: 150,
          key: 'teachingPointName'
        },
        {
          title: '课程次数',
          // width: 100,
          key: 'curriculumCount'
        },
        {
          title: '课程金额',
          key: 'price'
          // width: 150
        },
        {
          title: '攒班截止时间',
          key: 'assembleEndTime'
          // width: 170
        },
        {
          title: '创建人',
          key: 'creatorName'
          // width: 100
        },
        {
          title: '创建时间',
          key: 'createDate'
          // width: 150
        }
      ],
      classDetail: {
        show: false,
        id: ''
      },
      tableData: {},
      selectItemId: [],
      classUrlMap: {
        beta: '//one-beta.speiyou.cn/home?platform=course&self=1&openType=self',
        dev: '//one-beta.speiyou.cn/home?platform=course&self=1&openType=self',
        prod: '//one.speiyou.cn/home/?platform=course&self=1&openType=self'
      },
      selectItem: [],
      categoryMap: { 1: '在线', '2': '双师', '4': '面授' },
      classId: '',
      stuId: '',
      groupWorkId: '',
      openClassModal: false,
      isRegistTime: false,
      openClassRow: {},
      selectAllItemNum: 0,
      selectAllItem: [],
      selectAllItemId: [],
      newQueryParams: {
        pageNo: 1,
        pageSize: 10
      },
      currentpage: 1
    }
  },
  watch: {
    'queryParams._status': {
      handler (val, old) {
        if (val === old || !this.queryParams.cityCode) return
        this.newQueryParams = { ...this.newQueryParams, ...this.queryParams }
        if (this.queryParams._clear) {
          this.newQueryParams = {
            ...this.newQueryParams,
            pageNo: 1,
            pageSize: 10
          }
        }
        this.queryTableData(this.newQueryParams)
      }
    }
  },
  computed: {
    ...mapState('roleRelativeData', ['userInfo']),
    getColumns () {
      return function (status) {
        return this[this.statusMap[status]]
      }
    }
  },
  created () {

  },
  methods: {
    copy () { // 复制
      copyText(this.currentClassId)
    },
    queryTableData (params) {
      this.tableLoading = true
      this.selectItemId = []
      this.selectItem = []
      this.selectItemNum = 0
      this.selectAllItemId = []
      this.selectAllItem = []
      this.selectAllItemNum = 0
      getAssemblePage(params).then(res => {
        if (res) {
          let data = res && res.records
          // let data = this.tableData.records
          data = data.map(item => {
            item._expanded = false
            return item
          })
          this.tableData = { ...res, records: data }
          // this.tableData = { ...this.tableData, records: data }
          this.tableLoading = false
        } else {
          this.tableData = {}
        }
      }).catch(error => {
        this.tableData = {}
        this.$Message.error({ content: '查询攒班详情失败: ' + error.message, duration: 5 })
      })
    },
    // 是否能符合批量上线的条件
    isOnline (type) {
      let list = type === 'all' ? this.selectAllItem : this.selectItem
      let status = list.every(item => {
        return item.status === 0 // 0:待上线
      })
      return status
    },
    openAllOnlineModal (type) {
      let _this = this
      if (type !== 'all' && this.selectItemId.length === 0) { // 不是all
        return
      }
      if (type === 'all') {
        this.setSelectItem()
      }
      if ((type !== 'all' && this.selectItemNum === 0) || (type === 'all' && this.selectAllItemNum === 0)) {
        this.$Message.warning('当前无可以上线课程')
        return
      }
      if (!this.isOnline(type)) {
        this.$Message.warning('当前选中的课程包含已经开班的课程，无法再次开班')
        return
      }
      // 选择的项目是否能够批量上线
      this.$Modal.confirm({
        title: `共${type === 'all' ? this.selectAllItemNum : this.selectItemNum}项，确定班级上线？`,
        content: '<p style="font-size:16px;line-height:24px;">上线后，班级会展示在小程序上，且课程信息不可修改，请核对并确认</p>',
        onOk: () => {
          _this.online(type === 'all' ? _this.selectAllItemId : _this.selectItemId, type === 'all' ? 5000 : 3000)
        },
        okText: '确认上线'
      })
    },
    handleSelect (selection, row) {
      let selNum = selection.length > 0 ? selection.length : 0
      this.selectItemNum = selNum
      this.selectItemId = selection && selection.map(item => { return item.id })
      this.selectItem = selection
    },
    openClassDetail (id) {
      this.classDetail.show = true
      this.classDetail.id = id
      this.classDetail.cityId = id
    },
    setClassName (row, index) {
      // 通过自己的逻辑返回一个class或者空 作用:没有子数据隐藏下拉按钮
      return this.status !== 0 && row.assembleList && row.assembleList.length > 0 ? '' : 'expend'
    },
    changePage (page) {
      this.newQueryParams['pageNo'] = page
      this.queryTableData(this.newQueryParams)
    },
    changePageSize (size) {
      this.newQueryParams['pageSize'] = size
      this.queryTableData(this.newQueryParams)
    },
    // 点击全选的时候 获取当前数据
    setSelectItem () {
      this.selectAllItemNum = this.tableData && this.tableData.records && this.tableData.records.length
      this.selectAllItem = this.tableData && this.tableData.records
      this.selectAllItemId = this.tableData && this.tableData.records.map(item => { return item.id })
    },
    // 点击新增班级
    handelAddClass () {
      this.$refs['createClassRef'].addClassHandler()
    },
    // 上线
    online (ids, time) {
      let { empNo } = this.userInfo
      let params = {
        empNo,
        cityCode: this.queryParams.cityCode,
        activityIds: ids
      }
      activityOnline(params).then(res => {
        this.$Message.success({
          content: '上线中...',
          duration: 5
        })
        this.tableLoading = true
        this.repateQuery(time)
      }).catch(err => {
        this.$Message.error({
          content: '上线失败:' + err.message,
          duration: 5
        })
        this.tableLoading = false
      })
    },
    // 开班
    openClass (id) {
      let { empNo } = this.userInfo
      let params = {
        groupWorkId: id,
        empNo,
        cityCode: this.queryParams.cityCode
      }
      confirmOpenClass(params).then(res => {
        this.$Message.success({
          content: '开班成功',
          duration: 5
        })
        this.openClassModal = false
        this.repateQuery()
      }).catch(err => {
        this.$Message.error({
          content: '开班失败:' + err.message,
          duration: 5
        })
        this.openClassModal = true
      })
    },
    queryTime (classId) {
      let { empNo } = this.userInfo
      let params = {
        cityCode: this.queryParams.cityCode,
        empNo,
        classId
      }
      getRegistTimeByClass(params).then(res => {
        if (res && res.isRegist) {
          this.openClassModal = true
          this.isRegistTime = true
          this.currentClassId = classId
        } else {
          this.isRegistTime = false
          this.openClassModal = true
          this.currentClassId = classId
        }
      }).catch(err => {
        this.$Message.error({
          content: '查询网报时间失败:' + err.message,
          duration: 5
        })
      })
    },
    // 全部上线
    allOnline () {
      let { cityCode, empNo } = this.userInfo
      cityCode = this.queryParams.cityCode
      let params = {
        empNo,
        cityCode
      }
      activityOnlineAll(params).then(res => {
        this.$Message.success({
          content: '上线中...',
          duration: 5
        })
        this.repateQuery(5000)
      }).catch(err => {
        this.$Message.error({
          content: '上线失败:' + err.message,
          duration: 5
        })
      })
    },
    openCourse () {
      window.open(this.classUrlMap[ENV])
    },
    repateQuery (time) {
      // 重新请求
      let timer = null
      if (time) {
        let _this = this
        let queryParam = { ...this.newQueryParams }
        timer = setTimeout(() => {
          _this.queryTableData(queryParam)
          _this.$parent.$refs['classSearchComponent'].getAssembleTotal()
        }, time)
      } else {
        clearTimeout(timer)
        this.queryTableData(this.newQueryParams)
        this.$parent.$refs['classSearchComponent'].getAssembleTotal()
      }
    },
    getSuccessInfo (status) {
      if (status) {
        this.repateQuery(3000)
      }
    },
    onSuccess () {
      this.repateQuery()
    },
    comfirmOpenClass () {
      this.openClass(this.openClassRow.groupWorkId)
    },
    goCourse () {
      this.openCourse()
    },
    close () {
      this.openClassModal = false
    }
  }
}
</script>

