import axios from '../libs/api.request'

/**
 * 根据服务中心id查询综合信息
 * @param params
 * @returns {*}
 */
export function getAggregateInfo (params) {
  return axios.request({
    url: 'serviceCenter/getAggregateInfo',
    method: 'get',
    params
  })
}

/**
 * 根据服务中心id查询综合信息
 * @param params
 * @returns {*}
 */
export function getEvaluateList (params) {
  return axios.request({
    url: 'serviceCenter/getEvaluateList',
    method: 'post',
    data: params
  })
}
