// 攒班相关通用数据

import { abilityStudentLevel } from '@/api/student'

export default {
  data () {
    return {
      cuanbanStatusMap: { // 攒班状态
        1: '待评估',
        2: '评估中',
        3: '开班成功',
        4: '开班失败',
        5: '攒班成功',
        6: '攒班失败',
        7: '取消'
      },
      // 报名状态
      registStatusMap: {
        0: '未报名',
        1: '已报名',
        2: '报名失败'
      },
      // 缴费状态
      payStatusMap: {
        0: '未缴费',
        1: '已缴费'
      },
      // 是否关班
      classStatusMap: {
        0: '--',
        1: '否',
        2: '是'
      },
      // 意向度
      intentionList: ['高', '中', '低'],
      cuanbanStatusList: [
        { label: '待评估', value: 1 },
        { label: '评估中', value: 2 },
        { label: '开班成功', value: 3 },
        { label: '开班失败', value: 4 },
        { label: '攒班成功', value: 5 },
        { label: '攒班失败', value: 6 },
        { label: '取消', value: 7 }
      ],
      // 攒班班级报名状态
      registStatusList: [
        { label: '未报名', value: 0 },
        { label: '已报名', value: 1 },
        { label: '报名失败', value: 2 }
      ],
      // 攒班班级缴费状态
      payStatusList: [
        { label: '未缴费', value: 0 },
        { label: '已缴费', value: 1 }
      ],
      // 审批状态
      approvalStatusList: [
        { label: '未审批', value: 0 },
        { label: '已审批', value: 1 }
      ],
      openClassStatusList: [
        { label: '开班成功', value: 1 },
        { label: '开班失败', value: 2 }
      ]

    }
  },
  methods: {
    // select@on-change, 对应Id设置name
    handleSelectChange (data, name) {
      if (data) {
        this.formData[name] = data.label
      } else {
        this.formData[name] = ''
      }
    },
    //  设置学员入学测试成绩
    setStudentLevel (data) {
      return new Promise(async (resolve) => {
        let promises = []
        for (let i = 0; i < data.length; i++) {
          let item = data[i]
          const params = {
            gradeId: item.gradeId,
            subjectId: item.subjectId,
            uid: item.uid,
            levelDegree: item.classLevel,
            cityCode: item.cityCode
          }
          promises.push(abilityStudentLevel(params))
          // try {
          //   const params = {
          //     gradeId: item.gradeId,
          //     subjectId: item.subjectId,
          //     uid: item.uid,
          //     levelDegree: item.classLevel
          //   }
          //   let diagnosisScore = await abilityStudentLevel(params)
          //   item.diagnosisScore = diagnosisScore ? 1 : 2
          // } catch (error) {
          //   item.diagnosisScore = 3 // 获取失败
          //   this.$Message.error({ content: `获取学员入学测试成绩失败: ${error.message}` })
          // }
        }
        Promise.all(promises).then(res => {
          data.forEach((item, index) => {
            item.diagnosisScore = res[index] ? 1 : 2
          })
          resolve(data)
        }).catch(error => {
          resolve([])
          this.$Message.error({ content: `获取学员成绩失败: ${error.message}`, duration: 5 })
        })
      })
    },
    // 获取学员诊断成绩
    async abilityStudentLevel (params) {
      try {
        const data = await abilityStudentLevel(params)
        return data
      } catch (error) {
        this.$Message.error({ content: `获取学员成绩失败: ${error.message}`, duration: 5 })
      }
    }
  }
}
