<template>
  <!-- 开班评估审批 -->
  <div>
    <h3 style="margin-bottom: 20px;">意向攒班需求</h3>
    <Form
      inline
      :label-width="90"
      ref="formValidate"
      :model="formData"
      :rules="ruleValidate"
    >
      <FormItem
        label="年份"
        prop="year"
      >
        <Select
          v-model="formData.year"
          style="width: 150px;"
          clearable
          :disabled="type !== 1"
        >
          <Option
            v-for="(item,index) in yearList"
            :value="item"
            :key="index"
          >{{ item }}</Option>
        </Select>
      </FormItem>
      <FormItem
        label="意向学期"
        prop="termId"
      >
        <Select
          v-model="formData.termId"
          style="width: 150px;"
          clearable
          label-in-value
          :disabled="type !== 1"
          @on-change="handleSelectChange($event, 'termName')"
        >
          <Option
            v-for="(item,index) in termList.slice(0, 4)"
            :value="item.standardId"
            :key="index"
          >{{ item.standardName }}</Option>
        </Select>
      </FormItem>
      <FormItem
        label="年级"
        prop="gradeId"
      >
        <Select
          v-model="formData.gradeId"
          style="width: 150px;"
          clearable
          label-in-value
          :disabled="type !== 1"
          @on-change="handleSelectChange($event, 'gradeName')"
        >
          <Option
            v-for="(item,index) in gradeList"
            :value="item.standardId"
            :key="index"
          >{{ item.standardName }}</Option>
        </Select>
      </FormItem>
      <FormItem
        label="意向学科"
        prop="subjectId"
      >
        <Select
          v-model="formData.subjectId"
          style="width: 150px;"
          clearable
          label-in-value
          :disabled="type !== 1"
          @on-change="handleSelectChange($event, 'subjectName')"
        >
          <Option
            v-for="(item,index) in subjectList"
            :value="item.schoolId"
            :key="index"
          >{{ item.customName }}</Option>
        </Select>
      </FormItem>
      <FormItem
        label="班次级别"
        prop="classLevel"
      >
        <Select
          v-model="formData.classLevel"
          style="width: 150px;"
          clearable
          label-in-value
          :disabled="type !== 1"
          @on-change="handleSelectChange($event, 'classLevelName')"
        >
          <Option
            v-for="(item,index) in classLevelList"
            :value="item.schoolId"
            :key="index"
          >{{ item.name }}</Option>
        </Select>
      </FormItem>
      <FormItem
          label="授课类型"
          prop="classType"
      >
        <Select
          v-model="formData.classType"
          style="width: 150px;"
          :disabled="type !== 1"
        >
          <Option
              v-for="item in classTypeList"
              :value="item.id"
              :key="item.id"
              :disabled="!$haveAuthority('roleRelativeData',classTypeRuleMap[item.id])"
            >{{ item.name }}</Option>
        </Select>
      </FormItem>
      <template v-if="formData.classType===2||formData.classType===4">
        <FormItem
        label="意向服务中心"
        prop="serviceCenterId"
        >
          <Select
            v-model="formData.serviceCenterId"
            style="width: 150px;"
            clearable
            filterable
            :remote-method="q => getServiceCenterList(q, formData.cityCode)"
            label-in-value
            :disabled="type !== 1"
            @on-change="handleSelectChange($event, 'serviceCenterName')"
          >
            <Option
              v-for="(item,index) in serviceCenterList"
              :value="item.schoolId"
              :key="index"
            >{{ item.name }}</Option>
          </Select>
        </FormItem>
      </template>
      <FormItem
        label="意向上课时间"
        prop="classtimeTimeClassify"
      >
        <Select
          v-model="formData.classtimeTimeClassify"
          style="width: 150px;"
          clearable
          :disabled="type !== 1"
        >
          <Option
            v-for="(item,index) in timeTypeList"
            :value="item.name"
            :key="index"
          >{{ item.name }}</Option>
        </Select>
      </FormItem>
      <FormItem
        label="意向上课时段"
        prop="classtimeTimePeriod"
      >
        <Select
          v-model="formData.classtimeTimePeriod"
          style="width: 150px;"
          clearable
          :disabled="type !== 1"
        >
          <Option
            v-for="(item,index) in timeList"
            :value="item.name"
            :key="index"
          >{{ item.name }}</Option>
        </Select>
      </FormItem>
    </Form>
    <h3>
      意向攒班需求名单
      <span style="color: red;">特别提醒：请参照开班年级限额勾选攒班学员数量，避免攒班成功用户不能报入目标班次！</span>
    </h3>
    <h4 style="margin-top: 20px;margin-bottom: 10px;">意向人数: <span style="color: red;">{{ studentDemandListSorted.length }}</span></h4>
    <Table
      border
      :columns="columns"
      :data="studentDemandListSorted"
      :loading="tableLoading"
    ></Table>

    <div
      v-if="type !== 1"
      style="margin: 20px 0;padding-left: 40px;"
    >
      <div
        v-for="(item, index) in assessLogs"
        :key="index"
        style="margin-bottom: 10px;"
      >
        <template v-if="item.logType === 'add'">
          <h3 style="margin-left: -20px;margin-bottom: 10px;">发起记录</h3>
          <Row>
            <Col span="4">发起人: {{ item.creatorName || '--' }}</Col>
            <Col span="4">发起人工号: {{ item.creator || '--' }}</Col>
            <Col
              span="16"
              style="margin-bottom: 10px;"
            >申请时间: {{ item.createDate }}</Col>
            <Col span="24">备注: {{ item.remark || '--'}}</Col>
          </Row>
        </template>
        <template v-else>
          <h3 style="margin: 20px 0 10px -20px;">审批记录</h3>
          <Row>
            <Col span="4">审批人: {{ item.creatorName || '--' }}</Col>
            <Col span="4">审批人工号: {{ item.creator || '--' }}</Col>
            <Col
              span="16"
              style="margin-bottom: 10px;"
            >审批时间: {{ item.createDate }}</Col>
            <Col
              span="4"
              style="margin-bottom: 10px;"
            >开班状态: {{ item.logType === 'success' ? '开班成功' : item.logType === 'fail' ? '开班失败' : '--' }}</Col>
            <Col span="16">开班班级ID: {{ item.assembleClassId || '--' }}</Col>
            <Col span="24">{{ item.logType === 'success' ? '备注': '失败原因'}}: {{ item.approvalRemark || '--'}}</Col>
          </Row>
        </template>
      </div>
    </div>

    <Form
      inline
      :label-width="90"
      ref="formValidate2"
      :model="formData"
      :rules="ruleValidate"
      style="margin: 20px 0;"
    >

      <FormItem
        label="审批人"
        prop="receptionEmpNo"
        v-if="type === 1"
      >
        <Select
          v-model="formData.receptionEmpNo"
          filterable
          clearable
          remote
          :remote-method="q => getAdvisorEmpNos(q, formData.cityCode)"
          label-in-value
          style="width: 150px;"
          @on-change="handleSelectChange($event, 'receptionEmpName')"
        >
          <Option
            v-for="item in advisorEmpNos"
            :value="item.empNo"
            :key="item.empNo"
          >{{item.name}}-{{item.empNo}}</Option>
        </Select>
      </FormItem>
      <!-- 查看 -->
      <template v-if="type === 3">
        <FormItem
          label="开班状态"
          prop="status"
        >
          <Select
            v-model="formData.status"
            style="width: 150px;"
            @on-change="handleStatusChange"
          >
            <Option
              v-for="(item,index) in openClassStatusList"
              :value="item.value"
              :key="index"
            >{{ item.label }}</Option>
          </Select>
        </FormItem>
        <FormItem
          label="开班班级ID"
          prop="classId"
          v-if="formData.status === 1"
        >
          <Input
            v-model="formData.classId"
            clearable
          />
        </FormItem>

      </template>
      <!-- 开班成功-备注 -->
      <FormItem
        label="备注"
        v-if="(type === 1 || type === 3) && formData.status === 1"
        prop="remark"
        class="disabled-required"
      >
        <Input
          style="width: 740px;"
          v-model="formData.remark"
          type="textarea"
          :autosize="{minRows: 3,maxRows: 10}"
          placeholder="请输入"
          :maxlength="200"
        ></Input>
      </FormItem>
      <!-- 开班失败-失败原因 -->
      <FormItem
        label="失败原因"
        v-if="(type === 1 || type === 3) && formData.status === 2"
        prop="failReason"
      >
        <Input
          style="width: 740px;"
          v-model="formData.failReason"
          type="textarea"
          :autosize="{minRows: 3,maxRows: 10}"
          placeholder="请输入"
          :maxlength="200"
        ></Input>
      </FormItem>
    </Form>

    <Row
      type="flex"
      justify="center"
      :gutter="20"
      v-if="type ===1 || type === 3"
    >
      <Col>
      <Button
        type="primary"
        @click="confirm"
        :loading="confirmLoading"
      >{{ type === 1 ? '提交申请': type === 3? '审批': ''}}</Button>
      </Col>
      <Col>
      <Button
        :loading="confirmLoading"
        @click="$emit('close')"
      >取消</Button>
      </Col>
    </Row>
  </div>
</template>

<script>
import { TalDecodeText } from '@peiyou/tal-element'
import baseDataSearchMixin from './baseDataSearchMixin'
import GroupMultiple from '@/utils/group'
import cuanbanMixin from './cuanbanMixin'
import { submit, queryDetailById, operate } from '@/api/assembleClass'
export default {
  name: 'OpenClassAeeseeApproval',
  mixins: [baseDataSearchMixin, cuanbanMixin],
  props: {
    parentFormData: {
      type: Object,
      default: () => {
        return {}
      }
    },
    studentDemandList: { // 学员
      type: Array,
      default: () => {
        return []
      }
    },
    type: { // 类型 1新建审批 2查看 3审批
      type: Number,
      default: 1
    },
    // 开班评估ID
    accessId: {
      type: String,
      default: ''
    },
    classTypeList: { // 授课类型
      type: Array,
      default: () => {
        return []
      }
    },
    classTypeRuleMap: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data () {
    return {
      formData: {
        cityCode: '',
        year: new Date().getFullYear() + '', // 必填, // 年份
        termId: '', // 学期
        termName: '',
        gradeId: '', // 年级
        gradeName: '', // 年级
        subjectId: '', // 学科
        subjectName: '', // 学科
        classLevel: '', // 班次级别
        classLevelName: '', // 班次级别
        serviceCenterId: '', // 服务中心
        serviceCenterName: '', // 服务中心
        classtimeTimeClassify: '', // 上课时间
        classtimeTimePeriod: '', // 上课时段
        receptionEmpNo: '', // 审批人员工编号
        receptionEmpName: '', // 审批人员工姓名
        classId: '', // 开班班级ID
        status: null, // 开班状态 1成功 2失败， 失败是备注必填
        remark: '', // 备注
        failReason: '', // 失败原因
        classType: null
      },
      ruleValidate: {
        year: [
          { required: true, message: '请选择年份', trigger: 'change' }
        ],
        termId: [
          { required: true, message: '请选择学期', trigger: 'change' }
        ],
        gradeId: [
          { required: true, type: 'number', message: '请选择年级', trigger: 'change' }
        ],
        subjectId: [
          { required: true, message: '请选择意向学科', trigger: 'change' }
        ],
        classLevel: [
          { required: true, message: '请选择班次级别', trigger: 'change' }
        ],
        serviceCenterId: [
          { required: true, message: '请选择意向服务中心', trigger: 'change' }
        ],
        classtimeTimeClassify: [
          { required: true, message: '请选择意向上课时间', trigger: 'change' }
        ],
        classtimeTimePeriod: [
          { required: true, message: '请选择意向上课时段', trigger: 'change' }
        ],
        receptionEmpNo: [
          { required: true, message: '请选择审批人', trigger: 'change' }
        ],
        status: [
          { required: true, type: 'number', message: '请选择开班状态', trigger: 'change' }
        ],
        classId: [
          { required: true, message: '请输入开班班级ID', trigger: 'change' }
        ],
        failReason: [
          { required: true, message: '请输入失败原因', trigger: 'change' },
          { maxlength: 200, message: '最多输入200字符', trigger: 'change' }
        ]
      },
      columns: [
        {
          title: '学员姓名',
          key: 'name',
          align: 'center'
        },
        {
          title: '联系电话',
          key: 'phone',
          align: 'center',
          width: 120,
          render: (h, params) => {
            return h(TalDecodeText, {
              props: {
                uid: params.row.uid,
                encodeText: params.row.phone,
                type: 'phone'
              }
            })
          }
        },
        {
          title: '学员编号',
          key: 'studentCard',
          align: 'center'
        },
        {
          title: '创建时间',
          key: 'createDate',
          align: 'center'
        },
        {
          title: '意向学期',
          key: 'termName',
          align: 'center'
        },
        {
          title: '意向学科',
          key: 'subjectName',
          align: 'center'
        },
        {
          title: '年级',
          key: 'gradeName',
          align: 'center'
        },
        {
          title: '班次级别',
          key: 'classLevelName',
          align: 'center'
        },
        {
          title: '授课类型',
          key: 'classTypeName',
          align: 'center'
        },
        {
          title: '意向服务中心',
          key: 'serviceCenterName',
          align: 'center'
        }, {
          title: '意向上课时间',
          key: 'classtimeTimeClassify',
          align: 'center'
        }, {
          title: '意向上课时段',
          key: 'classtimeTimePeriod',
          align: 'center'
        }, {
          title: '意向度',
          key: 'intention',
          align: 'center'
        }, {
          title: '入学测试成绩',
          key: 'diagnosisScore',
          align: 'center',
          render: (h, params) => {
            const text = params.row.diagnosisScore === 1 ? '符合' : '不符合'
            return h('span', text)
          }
        }, {
          title: '攒班状态',
          key: 'status',
          align: 'center',
          render: (h, params) => {
            return h('span', this.cuanbanStatusMap[params.row.status])
          }
        }
      ],
      tableData: [],
      tableLoading: false,
      studentDemandListSorted: [], // 排序后的学员需求
      confirmLoading: false, // 提交中
      assessLogs: []// 操作记录
    }
  },
  mounted () {
    Object.assign(this.formData, { cityCode: this.parentFormData.cityCode })
    this.initFormOptions()

    // 新建审批
    if (this.type === 1) {
      this.groupStudentDemandList()
    }

    if (this.type === 2 || this.type === 3) {
      this.queryDetailById()
    }
  },
  methods: {
    handleStatusChange (val) {
      console.log('val', val)
      this.$nextTick(() => {
        this.$refs.formValidate2.resetFields()
        this.formData.status = val
      })
    },
    // 获取评估详情历史
    async queryDetailById () {
      try {
        const params = {
          accessId: this.accessId,
          cityCode: this.formData.cityCode
        }
        this.tableLoading = true
        const res = await queryDetailById(params)

        Object.assign(this.formData, res, {
          assembleClassDemandIds: null,
          assessLogs: null
        })

        if (this.type === 3) { // 审批
          this.formData.status = 1
          this.formData.remark = ''
        }

        this.serviceCenterList = [
          { schoolId: res.serviceCenterId, name: res.serviceCenterName }
        ]

        const studentDemandListSorted = await this.setStudentLevel(res.assembleClassDemandIds || [])
        this.studentDemandListSorted = studentDemandListSorted
        this.assessLogs = Object.freeze(res.assessLogs)
      } catch (error) {
        this.$Message.error({ content: `获取评估详情失败: ${error.message}`, duration: 5 })
      }

      this.tableLoading = false
    },
    // 学员需求池分组
    groupStudentDemandList () {
      // 根据属性给学员分组
      const groupList = new GroupMultiple(
        this.studentDemandList,
        ['year', 'termId', 'gradeId', 'subjectId', 'classLevel', 'serviceCenterId', 'classtimeTimeClassify', 'classtimeTimePeriod']).group().sort((a, b) => b.value.length - a.value.length)
      const keys = groupList[0].value[0]
      // 学员排序
      this.studentDemandListSorted = []
      groupList.forEach(item => {
        this.studentDemandListSorted.push(...item.value)
      })

      Object.assign(this.formData, {
        year: keys.year,
        termId: keys.termId,
        termName: keys.termName,
        subjectId: keys.subjectId,
        subjectName: keys.subjectName,
        gradeId: keys.gradeId,
        gradeName: keys.gradeName,
        classLevel: keys.classLevel,
        classLevelName: keys.classLevelName,
        serviceCenterId: keys.serviceCenterId,
        serviceCenterName: keys.serviceCenterName,
        classtimeTimePeriod: keys.classtimeTimePeriod.split(',')[0],
        classtimeTimeClassify: keys.classtimeTimeClassify.split(',')[0],
        remark: '',
        assembleClassDemandIds: this.studentDemandListSorted.map(item => item.id), // 需求ids
        intentionNumber: this.studentDemandListSorted.length, // 学员数量
        classType: keys.classType
      })
      this.serviceCenterList = [{
        schoolId: this.formData.serviceCenterId,
        name: this.formData.serviceCenterName
      }]
    },
    // 提交申请
    apply () {
      this.$refs.formValidate.validate((valid) => {
        if (valid) {
          this.$refs.formValidate2.validate((_valid) => {
            if (_valid) {
              this.confirmLoading = true
              const params = Object.assign({}, this.formData, {
                receptionEmpName: this.formData.receptionEmpName.split('-')[0]
              })
              console.log(params)
              submit(params).then(() => {
                setTimeout(() => {
                  this.$Message.success({ content: '提交成功' })
                  this.$emit('close')
                  this.$emit('success')
                  this.confirmLoading = false
                }, 1000)
              }).catch(error => {
                this.confirmLoading = false
                this.$Message.error({ content: `提交失败: ${error.message}`, duration: 5 })
              })
            }
          })
        }
      })
    },
    // 审批
    approval () {
      this.$refs.formValidate.validate((valid) => {
        if (valid) {
          this.$refs.formValidate2.validate((_valid) => {
            if (_valid) {
              let remark = ''
              if (this.formData.status === 2) {
                remark = this.formData.failReason
              } else {
                remark = this.formData.remark
              }

              this.confirmLoading = true
              const params = {
                accessId: this.formData.id,
                status: this.formData.status,
                remark,
                classId: this.formData.classId,
                cityCode: this.formData.cityCode
              }
              console.log(params)
              operate(params).then(() => {
                setTimeout(() => {
                  this.$Message.success({ content: '审批成功' })
                  this.$emit('close')
                  this.$emit('success')
                  this.confirmLoading = false
                }, 1000)
              }).catch(error => {
                this.confirmLoading = false
                this.$Message.error({ content: `审批失败: ${error.message}`, duration: 5 })
              })
            }
          })
        }
      })
    },
    confirm () {
      if (this.type === 1) {
        this.apply()
      } else if (this.type === 3) {
        this.approval()
      }
    },
    initFormOptions () {
      this.getBaseDataTermList()
      this.getYears()
      this.getSubjectList()
      this.getGradeList()
      this.getTimeList()
      this.getTimeTypeList()
      this.queryLevelDegreeList()
    }
  }
}
</script>

<style lang="less" scoped>
/deep/ .ivu-form-item-label {
  white-space: nowrap;
}

/deep/ .disabled-required .ivu-form-item-label::before {
  display: none;
}
</style>
