<template>
  <!-- 攒班管理 -->
  <div style="width: 100%;padding: 10px;">
    <Form
      class="mrg-b filter-search"
      ref="form"
      :model="formData"
      label-position="right"
      :label-width="15"
      inline
      style="padding: 10px 0;background: #fff;"
    >
      <div class="class_top clearfix">
        <FormItem
          required
          label=" "
        >
          <Select
            v-model="formData.cityCode"
            placeholder="城市"
            filterable
            style="width: 120px;"
            @on-change="onCityChange"
          >
            <Option
              v-for="(option,index) in cityList"
              :value="option.id"
              :key="index"
            >{{option.name}}</Option>
          </Select>
        </FormItem>
        <FormItem
          label=" "
          required
        >
          <Select
            v-model="formData.classType"
            placeholder="授课类型"
            style="width: 120px;"
          >
            <Option
                v-for="item in classTypeList"
                :value="item.id"
                :key="item.id"
                :disabled="!$haveAuthority('roleRelativeData',classTypeRuleMap[item.id])"
              >{{ item.name }}</Option>
          </Select>
        </FormItem>
        <template v-if="formData.classType===2||formData.classType===4">
          <FormItem>
            <Select
              ref="serviceCenterRef"
              placeholder="意向服务中心"
              v-model="formData.serviceCenterId"
              clearable
              style="width:150px"
              filterable
              :remote-method="q => getServiceCenterList(q, formData.cityCode)"
            >
              <Option
                v-for="(item,index) in serviceCenterList"
                :value="item.schoolId"
                :key="index"
              >{{ item.name }}</Option>
            </Select>
          </FormItem>
        </template>
        <FormItem>
          <Select
            v-model="formData.year"
            style="width: 120px;"
            placeholder="攒班年份"
            clearable
          >
            <Option
              v-for="(item,index) in yearList"
              :value="item"
              :key="index"
            >{{ item }}</Option>
          </Select>
        </FormItem>
        <FormItem>
          <Select
            v-model="formData.termId"
            style="width: 120px;"
            placeholder="意向学期"
            clearable
          >
            <Option
              v-for="(item,index) in termList.slice(0,4)"
              :value="item.standardId"
              :key="index"
            >{{ item.standardName }}</Option>
          </Select>
        </FormItem>
        <FormItem>
          <Select
            v-model="formData.gradeId"
            style="width: 120px;"
            clearable
            placeholder="年级"
          >
            <Option
              v-for="(item,index) in gradeList"
              :value="item.standardId"
              :key="index"
            >{{ item.standardName }}</Option>
          </Select>
        </FormItem>
        <FormItem>
          <Select
            v-model="formData.subjectId"
            style="width: 120px;"
            clearable
            placeholder="意向学科"
          >
            <Option
              v-for="(item,index) in subjectList"
              :value="item.schoolId"
              :key="index"
            >{{ item.customName }}</Option>
          </Select>
        </FormItem>
        <FormItem>
          <Select
            v-model="formData.classLevel"
            style="width: 120px;"
            clearable
            placeholder="班次级别"
          >
            <Option
              v-for="(item,index) in classLevelList"
              :value="item.schoolId"
              :key="index"
            >{{ item.name }}</Option>
          </Select>
        </FormItem>
        <template v-if="activeTab === 'demandPool'">
          <FormItem>
            <Select
              v-model="formData.status"
              style="width: 120px;"
              clearable
              placeholder="攒班状态"
            >
              <Option
                v-for="(item,index) in cuanbanStatusList"
                :value="item.value"
                :key="index"
              >{{ item.label }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select
              v-model="formData.classtimeTimeClassify"
              style="width: 120px;"
              clearable
              placeholder="意向上课时间"
            >
              <Option
                v-for="(item,index) in timeTypeList"
                :value="item.name"
                :key="index"
              >{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select
              v-model="formData.classtimeTimePeriod"
              style="width: 120px;"
              clearable
              placeholder="意向上课时段"
            >
              <Option
                v-for="(item,index) in timeList"
                :value="item.name"
                :key="index"
              >{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select
              v-model="formData.intention"
              style="width: 120px;"
              clearable
              placeholder="意向度"
            >
              <Option
                v-for="(item,index) in intentionList"
                :value="item"
                :key="index"
              >{{ item }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select
              v-model="formData.registStatus"
              style="width: 140px;"
              clearable
              placeholder="攒班班级报名状态"
            >
              <Option
                v-for="(item,index) in registStatusList"
                :value="item.value"
                :key="index"
              >{{ item.label }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select
              v-model="formData.payStatus"
              style="width: 140px;"
              clearable
              placeholder="攒班班级缴费状态"
            >
              <Option
                v-for="(item,index) in payStatusList"
                :value="item.value"
                :key="index"
              >{{ item.label }}</Option>
            </Select>
          </FormItem>

          <FormItem>
            <Select
              v-model="formData.creator"
              filterable
              clearable
              remote
              :remote-method="q => getAdvisorEmpNos(q, formData.cityCode)"
              label-in-value
              style="width: 150px;"
              placeholder="创建人"
            >
              <Option
                v-for="item in advisorEmpNos"
                :value="item.empNo"
                :key="item.empNo"
              >{{item.remark}}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <DatePicker
              placeholder="创建时间"
              type="daterange"
              :editable="false"
              v-model="formData.time"
              style="width: 200px"
              @on-change="optDateChange($event, 'startCreateDate', 'endCreateDate')"
            ></DatePicker>
          </FormItem>
          <FormItem>
            <Select
              v-model="formData.receptionAdvisorEmpNo"
              filterable
              clearable
              remote
              :remote-method="q => getAdvisorEmpNos(q, formData.cityCode)"
              label-in-value
              style="width: 150px;"
              placeholder="接待顾问"
            >
              <Option
                v-for="item in advisorEmpNos"
                :value="item.empNo"
                :key="item.empNo"
              >{{item.name}}-{{item.empNo}}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Input
              placeholder="学员姓名"
              v-model="formData.name"
              clearable
            />
          </FormItem>
          <FormItem>
            <Input
              number
              placeholder="学员手机号"
              v-model="formData.phone"
              clearable
            />
          </FormItem>
          <FormItem>
            <Input
              placeholder="学员编号"
              v-model="formData.studentCard"
              clearable
            />
          </FormItem>
        </template>
        <div style="text-align: right; margin-bottom: 10px;">
          <Button
            type="primary"
            style="margin-right: 10px;"
            @click="search"
          >查询</Button>
          <Button
            type="primary"
            style="margin-right: 10px;"
            @click="showQrcode"
          >分校攒班二维码</Button>
          <Button
            type="primary"
            style="margin-right: 10px;"
            @click="createCuanbanType = 1; createCuanbanTypeName = '新增攒班需求'; showCreateCuanbanModal = true"
          >新增</Button>
        </div>
      </div>
    </Form>

    <div style="background: #fff;margin-top: 20px;padding: 10px;">
      <Tabs
        type="card"
        :animated="false"
        v-model="activeTab"
      >
        <TabPane
          label="攒班需求池"
          name="demandPool"
        >
          <DemandPool
            ref="demandPool"
            :parentFormData="formData"
            @updateCuanbanDemand="updateCuanbanDemand"
            @checkCuanbanDemand="checkCuanbanDemand"
            @submitOpenClassAssessApproval="handleSubmitOpenClassAssessApproval"
          />
        </TabPane>
        <TabPane
          label="开班评估列表"
          name="openClassAssessList"
        >
          <OpenClassAssessList
            ref="openClassAssessList"
            :parentFormData="formData"
            @check="handleCheckAssess"
            @approval="handleApprovalAssess"
          />
        </TabPane>
        <TabPane
          label="攒班班级列表"
          name="cuanbanList"
        >
          <CuanbanList
            ref="cuanbanList"
            :parentFormData="formData"
            @showClassDetail="showClassDetail"
            @showTeacherDetail="showTeacherDetail"
          />
        </TabPane>
      </Tabs>
    </div>

    <!-- 新增预约 -->
    <Modal
      :mask-closable="false"
      :title="createCuanbanTypeName"
      v-model="showCreateCuanbanModal"
      :footer-hide="true"
      width="1050px"
    >
      <CreateCuanban
        v-if="showCreateCuanbanModal"
        @close="showCreateCuanbanModal = false"
        :type="createCuanbanType"
        :oldFormData="oldCuanbanDemand"
        :classTypeList="classTypeList"
        :classTypeRuleMap="classTypeRuleMap"
        @success="handleCreateCuanbanSuccess"
      />
    </Modal>
    <TalClassDetail
      :class-id="classDetail.id"
      :show="classDetail.show"
      @close="classDetail.show = false"
    ></TalClassDetail>
    <TalTeacherDetails
      ref="teacherRef"
      :schoolCode="teacherDetail.cityCode"
      :teacherId="teacherDetail.id"
      :type="teacherDetail.type"
    >
    </TalTeacherDetails>

    <!-- 提交攒班评估 -->
    <Modal
      :mask-closable="false"
      title="开班评估审批"
      v-model="openClassAssessApproval.showModal"
      :footer-hide="true"
      width="1050px"
    >
      <OpenClassAssessApproval
        v-if="openClassAssessApproval.showModal"
        :type="openClassAssessApproval.type"
        :parentFormData="formData"
        :accessId="openClassAssessApproval.accessId"
        :studentDemandList="openClassAssessApproval.studentDemandList"
        :classTypeRuleMap="classTypeRuleMap"
        @close="openClassAssessApproval.showModal = false"
        :classTypeList="classTypeList"
        @success="search"
      />
    </Modal>

    <!-- 分校攒班二维码 -->
    <Modal
      :mask-closable="false"
      title="分校攒班二维码"
      v-model="qrCode.showModal"
      :footer-hide="true"
      width="350px"
    >
      <div class="qr-wrap">
        <img
          class="qr-bg"
          src="../../../assets/images/cuanban-qr-bg.png"
          alt=""
        >
        <img
          class="qr-img"
          :src="qrCode.activityCodeUrl"
          alt=""
        >
      </div>
    </Modal>
  </div>
</template>

<script>
import ClassDetail from '../../../mixins/classDetail'
import TeacherDetail from '../../../mixins/teacherDetail'
import DemandPool from './demandPool'
import OpenClassAssessList from './openClassAssessList'
import CuanbanList from './cuanbanList'
import CreateCuanban from './createCuanban'
import OpenClassAssessApproval from './openClassAssessApproval'
import BaseDataSearchMixin from './baseDataSearchMixin'
import cuanbanMixin from './cuanbanMixin'
import { queryServiceCenterByEmpNo } from '@/api/clazz'
import { getAssembleClassActivity } from '@/api/assembleClass'
import { getClassType } from '@/api/bd'
import Cookie from 'js-cookie'
export default {
  name: 'cuanbanMgt',
  mixins: [BaseDataSearchMixin, ClassDetail, TeacherDetail, cuanbanMixin],
  components: {
    DemandPool,
    OpenClassAssessList,
    CuanbanList,
    CreateCuanban,
    OpenClassAssessApproval
  },
  data () {
    return {
      showCreateCuanbanModal: false, // 新增攒班需求
      setDefaultServiceCenter: true,
      formData: {
        cityCode: '', // 城市分校
        year: new Date().getFullYear() + '', // 必填, // 年份
        serviceCenterId: '', // 服务中心ID
        termId: '', // 学期
        gradeId: '', // 年级
        subjectId: '', // 学科
        classLevel: '', // 班次级别
        status: '', // 攒班状态
        classtimeTimeClassify: '', // 上课时间
        classtimeTimePeriods: '', // 上课时段
        intention: '', // 意向度
        payStatus: '', // 缴费状态
        registStatus: '', // 报名状态
        creator: '', // 创建人
        receptionAdvisorEmpNo: '', // 接待顾问
        name: '', // 学员姓名
        phone: '', // 手机号
        studentCard: '', // 学员编号
        time: '',
        startCreateDate: '',
        endCreateDate: '',
        classType: 1
      },
      activeTab: 'demandPool', // 激活tab
      createCuanbanType: 1, // 创建攒班组件类型 1新增 2修改 3查看
      createCuanbanTypeName: '', // 攒班组件title
      oldCuanbanDemand: {}, // 旧攒班需求
      openClassAssessApproval: { //  开班评估审批
        showModal: false,
        studentDemandList: [],
        accessId: '', // 开班评估id
        type: 1 // 1提交 2查看
      },
      qrCode: { // 分校攒班二维码
        showModal: false,
        activityCodeUrl: ''
      },
      classTypeList: [], // 授课类型列表
      classTypeRuleMap: {
        1: 'operative/cuanbanMgt/zaixian',
        2: 'operative/cuanbanMgt/shuangshi',
        4: 'operative/cuanbanMgt/mianshou'
      }
    }
  },
  mounted () {
    this.initCityList()
  },
  methods: {
    // 日期范围选择
    optDateChange (val, start, end) {
      this.formData[start] = val[0]
      this.formData[end] = val[1]
    },
    // 分校攒班二维码展示
    async showQrcode () {
      try {
        const data = await getAssembleClassActivity({ cityCode: this.formData.cityCode })
        Object.assign(this.qrCode, data)
        this.qrCode.showModal = true
      } catch (error) {
        this.$Message.error({ content: `获取分校攒班二维码失败: ${error.message}`, duration: 5 })
      }
    },
    // 开班评估审批
    handleApprovalAssess (id) {
      this.openClassAssessApproval.accessId = id
      this.openClassAssessApproval.type = 3
      this.openClassAssessApproval.showModal = true
    },
    // 查看开班评估审批
    handleCheckAssess (id) {
      this.openClassAssessApproval.accessId = id
      this.openClassAssessApproval.type = 2
      this.openClassAssessApproval.showModal = true
    },
    // 提交开班审批评估
    handleSubmitOpenClassAssessApproval (data) {
      this.openClassAssessApproval.type = 1
      this.openClassAssessApproval.studentDemandList = data
      this.openClassAssessApproval.showModal = true
    },
    // 新增，修改攒班需求成功
    handleCreateCuanbanSuccess () {
      if (this.activeTab === 'demandPool') {
        this.search()
      }
    },
    // 查看攒班需求
    checkCuanbanDemand (data) {
      this.oldCuanbanDemand = data
      this.createCuanbanType = 3
      this.createCuanbanTypeName = '查看攒班需求'
      this.showCreateCuanbanModal = true
    },
    // 更新攒班需求
    updateCuanbanDemand (data) {
      this.oldCuanbanDemand = data
      this.createCuanbanType = 2
      this.createCuanbanTypeName = '修改攒班需求'
      this.showCreateCuanbanModal = true
    },
    // 查询
    search () {
      this.$refs[this.activeTab].search()
    },
    async getCurrentserviceCenter () {
      try {
        const data = await queryServiceCenterByEmpNo({
          cityCode: this.formData.cityCode,
          empNo: JSON.parse(Cookie.get('columbusUserInfo')).id
        })
        if (data.serviceCenterId && data.serviceCenterName) {
          this.serviceCenterList = [
            { schoolId: data.serviceCenterId, name: data.serviceCenterName }
          ]
          this.formData.serviceCenterId = data.serviceCenterId
        }
      } catch (error) {
        this.$Message.error({ content: '获取当前顾问所在服务中心失败: ' + error.message, duration: 5 })
      }
    },
    onCityChange (val) {
      this.formData.cityCode = val
      this.initOtherFrmOptions()
    },
    // 初始化
    async initCityList () {
      await this.getCityList()
      this.getCurrentserviceCenter()
      this.initOtherFrmOptions()
    },
    // 初始化表单筛选项
    initOtherFrmOptions () {
      this.getBaseDataTermList()
      this.getYears()
      this.getSubjectList()
      this.getGradeList()
      this.queryLevelDegreeList()
      this.getTimeTypeList()
      this.getTimeList()
      this.getClassTypeList()
    },
    getClassTypeList () {
      getClassType({}).then(res => {
        this.classTypeList = res
      }).catch(err => {
        this.$Message.warning({ content: '获取授课类型失败:' + err.message, duration: 5 })
      })
    }
  }
}
</script>

<style lang="less" scoped>
/deep/ .filter-search .ivu-form-item-required .ivu-form-item-label {
  width: 10px !important;
  transform: translate(5px);
}

.qr-wrap {
  position: relative;
  .qr-bg {
    width: 100%;
  }
  .qr-img {
    position: absolute;
    right: 10px;
    bottom: 20px;
    width: 80px;
    height: 80px;
  }
}
</style>
