<template>
  <div class="wholeSearch">
    <div class="formLayout">
      <Form
        class="form-container"
        :model="formData"
        :label-width="100"
        inline
      >
        <FormItem label="分校">
          <Select
            class="iptWidth"
            v-model="formData.cityCode"
            filterable
            @on-change="initCityList"
          >
            <Option v-for="(option,index) in cityList" :value="option.id" :key="index">{{option.name}}</Option>
          </Select>
        </FormItem>
        <FormItem label="班级名称">
          <Input
            placeholder="学科/年级/班型/学季"
            v-model="formData.name"
            class="iptWidth">
          </Input>
        </FormItem>
        <FormItem label="产品类型">
          <Select
            class="iptWidth"
            v-model="formData.productType"
            clearable
          >
            <Option :value="'1'">长期班</Option>
            <Option :value="'2'">短期班</Option>
          </Select>
        </FormItem>
        <FormItem label="授课类型">
          <Select
            class="iptWidth"
            v-model="formData.categoryType"
            clearable
          >
            <Option :value="1">在线</Option>
            <Option :value="2">双师</Option>
            <Option :value="4">面授</Option>
          </Select>
        </FormItem>
        <FormItem label="服务中心">
          <Select
            clearable
            filterable
            v-model="formData.serviceCenterId"
            @on-change="changeService"
            :remote-method="q => getServiceCenterList(q, formData.cityCode)"
            class="iptWidth"
          >
            <Option
              v-for="(item,index) in serviceCenterList"
              :key="index"
              :value="item.schoolId"
            >{{ item.name }}</Option>
          </Select>
        </FormItem>
        <FormItem label="教学点">
          <Select
            clearable
            filterable
            v-model="formData.teachingPointCode"
            :disabled="!formData.serviceCenterId"
            class="iptWidth"
          >
            <Option
              v-for="(item,index) in venueList"
              :key="index"
              :value="item.schoolId"
            >{{ item.name }}</Option>
          </Select>
        </FormItem>
        <FormItem label="主讲老师">
          <Select
            filterable
            clearable
            label-in-value
            placeholder="请搜索选择"
            :loading="loadingTeacherName"
            :remote-method="remoteListTeacherName"
            v-model="formData.teacherEmpNo"
            class="iptWidth"
          >
            <Option
              v-for="(item, index) in teacherNameList"
              :value="item.empNo"
              :label="item.realName"
              :key="index"
            >
              <div slot>{{`${item.realName} (${item.empNo})`}}</div>
            </Option>
          </Select>
        </FormItem>
        <FormItem label="上课星期">
          <Select
            class="iptWidth"
            v-model="formData.classTimeTypeName"
            clearable
          >
            <Option
              v-for="(item, index) in timeTypeList"
              :key="index"
              :value="item"
            >{{ item }}</Option>
          </Select>
        </FormItem>
        <FormItem label="上课时段">
          <Select
            class="iptWidth"
            v-model="formData.period"
            clearable
          >
            <Option
              v-for="item in timeList"
              :key="item.id"
              :value="item.name"
            >{{ item.name }}</Option>
          </Select>
        </FormItem>
        <FormItem class="btnArea">
          <Button
            class="searchBtn"
            type="primary"
            @click="assembleSearch"
          >查询</Button>
          <Button
            type="default"
            @click="resetField"
          >重置</Button>
        </FormItem>
      </Form>
    </div>
    <div class="cardWrapper">
      <div ref="card"
        class="task-type-card"
        @mouseenter="hover = true"
        @mouseleave="hover = false"
        @click="handleCardClick(1)"
        :class="{'active': activeCard === 1}"
      >
        <p class="typeName">
          待上线
          <Tooltip transfer placement="right">
            <Icon type="ios-information-circle-outline" />
            <div slot="content">
              <p>课程上线后，用户可以进行攒班</p>
            </div>
          </Tooltip>
        </p>
        <div class="typeNum">{{this.stayOnlineTotal}}</div>
      </div>
      <div
        ref="card"
        class="task-type-card"
        @mouseenter="hover = true"
        @mouseleave="hover = false"
        @click="handleCardClick(2)"
        :class="{'active': activeCard === 2}"
      >
        <p class="typeName">
          攒班中
          <Tooltip transfer placement="right">
            <Icon type="ios-information-circle-outline" />
            <div slot="content">
              <p>正在攒班的班级</p>
            </div>
          </Tooltip>
        </p>
        <div class="typeNum">{{this.assembleInTotal}}</div>
      </div>
      <div ref="card"
        class="task-type-card"
        @mouseenter="hover = true"
        @mouseleave="hover = false"
        @click="handleCardClick(3)"
        :class="{'active': activeCard === 3}"
      >
        <p class="typeName">
          攒班成功
          <Tooltip transfer placement="right">
            <Icon type="ios-information-circle-outline" />
            <div slot="content">
              <p>攒班人数已满，需要学科正式开班</p>
            </div>
          </Tooltip>
        </p>
        <div class="typeNum">{{this.assembleSuccessTotal}}</div>
      </div>
      <div ref="card"
        class="task-type-card"
        @mouseenter="hover = true"
        @mouseleave="hover = false"
        @click="handleCardClick(4)"
        :class="{'active': activeCard === 4}"
      >
        <p class="typeName">
          开班成功
          <Tooltip transfer placement="right">
            <Icon type="ios-information-circle-outline" />
            <div slot="content">
              <p>开班成功后，用户开始缴费</p>
            </div>
          </Tooltip>
        </p>
        <div class="typeNum">{{this.openClassSuccess}}</div>
      </div>
      <div ref="card"
        class="task-type-card"
        @mouseenter="hover = true"
        @mouseleave="hover = false"
        @click="handleCardClick(5)"
        :class="{'active': activeCard === 5}"
      >
        <p class="typeName">
          攒班失败
          <Tooltip transfer placement="right">
            <Icon type="ios-information-circle-outline" />
            <div slot="content">
              <p>截止日期内，未攒班用户的班级</p>
            </div>
          </Tooltip>
        </p>
        <div class="typeNum">{{this.assembleFail}}</div>
      </div>
    </div>
  </div>
</template>

<script>
import { queryServiceCenterByEmpNo } from '@/api/clazz'
import { getTotalList } from '@/api/assembleClass'
import BaseDataSearchMixin from './baseDataSearchMixin'
// import { mapState } from 'vuex'
import Cookie from 'js-cookie'
export default {
  name: 'classSearch',
  mixins: [BaseDataSearchMixin],
  data () {
    return {
      cityList: [], // 城市分校
      hover: false,
      hover2: false,
      activeCard: 1, // 点击选中的攒班类型卡片 默认：1-待上线
      stayOnlineTotal: 0, // 1-待上线
      assembleInTotal: 0, // 2-攒班中
      assembleSuccessTotal: 0, // 3-攒班成功
      openClassSuccess: 0, // 4-开班成功
      assembleFail: 0, // 5-攒班失败
      formData: {
        name: '', // 班级名称 模糊搜索
        categoryType: '', // 授课类型
        productType: '', // 产品类型
        serviceCenterId: '', // 服务中心ID
        teachingPointCode: '', // 教学点ID
        teacherEmpNo: '', // 主讲老师员工编号
        classTimeTypeId: '', // 上课时间类型Id 例：周五 - 5
        classTimeTypeName: '', // 上课时间类型名称
        period: '', // 上课时段
        cityCode: '', // 必传
        queryType: 1 // 必传 查询类型 1-待上线 2-攒班中 3-攒班成功 4-开班成功 5-攒班失败 ｜默认展示：1-待上线
        // pageSize: 10, // 必传 每页条数
        // pageNo: 1 // 必传 页码
      },
      serviceCenterList: [], // 服务中心
      venueList: [], // 教学点
      teacherNameList: [], // 主讲老师姓名
      loadingTeacherName: false, // 主讲老师加载
      timeTypeList: ['周一', '周二', '周三', '周四', '周五', '周六', '周日'], // 上课时间(星期)
      classTimeTypeIdMap: { // 上课时间类型ID映射
        '周一': 1,
        '周二': 2,
        '周三': 3,
        '周四': 4,
        '周五': 5,
        '周六': 6,
        '周日': 7
      },
      timeList: [] // 上课时段
    }
  },
  async mounted () {
    await this.getCityListHandler(data => {
      this.formData.cityCode = data[0].id
    })
    this.initCityList()
  },
  methods: {

    // 初始化
    async initCityList () {
      try {
        await this.getCurrentserviceCenter()
        this.assembleSearch()
        this.getTimeList()
        if (this.formData.serviceCenterId) {
          this.getVenueListHandler({ cityCode: this.formData.cityCode, serviceId: this.formData.serviceCenterId }).then(res => {
            this.venueList = res
          }).catch(err => {
            this.$Message.warning('获取教学点失败:' + err.message)
          })
        }
      } catch (error) {
        this.$Message.error({ content: '获取上课时段失败: ' + error.message, duration: 5 })
      }
    },
    // 查询攒班列表总数
    getAssembleTotal () {
      let formData = {
        ...this.formData,
        cityCode: this.formData.cityCode
      }
      getTotalList(formData).then(data => {
        if (data) {
          this.stayOnlineTotal = data.stayOnlineTotal
          this.assembleInTotal = data.assembleInTotal
          this.assembleSuccessTotal = data.assembleSuccessTotal
          this.openClassSuccess = data.openClassSuccess
          this.assembleFail = data.assembleFail
        } else {
          this.stayOnlineTotal = 0
          this.assembleInTotal = 0
          this.assembleSuccessTotal = 0
          this.openClassSuccess = 0
          this.assembleFail = 0
        }
      }).catch(err => {
        this.$Message.error({ content: '获取攒班类型数量失败: ' + err.message, duration: 5 })
        this.stayOnlineTotal = 0
        this.assembleInTotal = 0
        this.assembleSuccessTotal = 0
        this.openClassSuccess = 0
        this.assembleFail = 0
      })
    },
    // 获取当前用户服务中心
    async getCurrentserviceCenter () {
      try {
        const data = await queryServiceCenterByEmpNo({
          cityCode: this.formData.cityCode,
          empNo: JSON.parse(Cookie.get('columbusUserInfo')).id
        })
        if (data.serviceCenterId && data.serviceCenterName) {
          this.serviceCenterList = [
            { schoolId: data.serviceCenterId, name: data.serviceCenterName }
          ]
          this.formData.serviceCenterId = data.serviceCenterId
        }
      } catch (error) {
        this.$Message.error({ content: '获取当前顾问所在服务中心失败: ' + error.message, duration: 5 })
      }
    },
    // 改变服务中心
    changeService (val) {
      this.venueList = []
      this.formData.teachingPointCode = ''
      if (!val) {
        return false
      }
      const req = {
        cityCode: this.formData.cityCode,
        serviceId: this.formData.serviceCenterId
      }
      this.getVenueListHandler(req).then(res => {
        this.venueList = res
      }).catch(err => {
        this.$Message.warning('获取教学点失败:' + err.message)
      })
    },
    // 模糊查询主讲老师
    remoteListTeacherName (name) {
      clearTimeout(this.lazyTimer)
      this.lazyTimer = setTimeout(() => {
        this.getTeacher(name)
      }, 400)
    },
    // 主讲老师搜索
    getTeacher (query) {
      query = query.trim()
      this.loadingTeacherName = true
      this.teacherNameList = []
      let req = {
        sysName: query,
        teacherType: 0,
        cityCode: this.formData.cityCode,
        businessType: this.formData.categoryType,
        pageNo: 1,
        pageSize: 20
      }
      this.getTeacherByNameFuzzyHandler(req).then(res => {
        if (res.records && res.records.length) {
          this.teacherNameList = res.records
        }
        this.loadingTeacherName = false
      }).catch(err => {
        this.loadingTeacherName = false
        this.$Message.error('获取主讲老师姓名失败:' + err.message)
      })
    },
    // 查询
    async assembleSearch (isClearPageSize = false) {
      this.formData.classTimeTypeId = this.classTimeTypeIdMap[this.formData.classTimeTypeName]
      this.getAssembleTotal(this.formData)
      // this.getPage()
      this.$emit('queryParams', this.formData, new Date(), isClearPageSize)
    },
    // 分页查询攒班列表
    // getPage () {
    //   this.formData.classTimeTypeId = this.classTimeTypeIdMap[this.formData.classTimeTypeName]
    //   const params = this.formData
    //   getAssemblePage(params).then(res => {
    //     if (res) {
    //       this.tableData = res
    //     } else {
    //       this.tableData = {}
    //     }
    //   }).catch(error => {
    //     this.tableData = {}
    //     this.$Message.error({ content: '查询攒班详情失败: ' + error.message, duration: 5 })
    //   })
    // },
    // 重置
    resetField () {
      this.formData.name = '' // 班级名称 模糊搜索
      this.formData.categoryType = '' // 授课类型
      this.formData.teachingPointCode = '' // 教学点ID
      this.formData.teacherEmpNo = '' // 主讲老师ID
      this.formData.classTimeTypeId = '' // 上课时间(星期)
      this.formData.classTimeTypeName = '' // 上课时间(星期)
      this.formData.period = '' // 上课时段
      this.formData.queryType = 1 // 默认查询：1-待上线
      this.activeCard = 1 // 当前选中卡片切换成默认：1-待上线
      this.assembleSearch(true)
    },
    // 攒班类型卡片点击处理函数
    handleCardClick (index) {
      this.activeCard = index
      this.formData.queryType = index
      this.$emit('queryParams', this.formData, new Date(), true)
    }
  }
}
</script>

