  &:hover {
    box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.06);
  }

  &.active {
    border-color: #3384ff;
    color: #3384ff;
  }

  .typeName {
    font-size: 14px;
  }

  .typeNum {
    font-size: 20px;
    font-family: DIN Alternate;
    display: flex;
    align-items: baseline;
