<!--试听管理-->
<template>
  <div style="width: 100%; padding: 10px">
    <Form class="mrg-b filter-search" ref="form" :model="formData" label-position="right" :label-width="15" inline style="padding: 10px 0; background: #fff">
      <div class="class_top clearfix">
        <template v-if="activeTabName === 'reservationRecord'">
          <FormItem required label=" ">
            <Select placeholder="城市" v-model="formData.cityCode" filterable style="width: 120px" @on-change="cityYearChange">
              <Option v-for="(option, index) in allCityList" :value="option.id" :key="index">{{ option.name }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.areaId" @on-change="changeArea()" clearable style="width: 150px" filterable placeholder="区域">
              <Option v-for="(item, index) in areaList" :value="item.schoolId" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.serviceCenterId" clearable style="width: 150px" filterable placeholder="服务中心">
              <Option v-for="(item, index) in serviceList" :value="item.schoolId" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.year" style="width: 120px" clearable placeholder="年份">
              <Option v-for="(item, index) in yearList" :value="item" :key="index">{{ item }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.termId" style="width: 120px" @on-change="changeTerm" clearable placeholder="试听学期">
              <Option v-for="(item, index) in termList" :value="item.id" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.gradeTypeId" style="width: 120px" @on-change="gradeTypeChange" clearable placeholder="试听年部">
              <Option v-for="(item, index) in gradeTypeList" :value="item.id" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.gradeId" style="width: 120px" @on-change="changeClasses" clearable placeholder="试听年级">
              <Option v-for="(item, index) in gradeList" :value="item.id" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.subjectId" style="width: 120px" @on-change="changeSubject" clearable placeholder="试听学科">
              <Option v-for="(item, index) in subjectList" :value="item.id" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.planAdvisorNo" filterable clearable remote :remote-method="getTreatAdvisorEmpNos" label-in-value style="width: 150px" :loading="loading1" placeholder="预约顾问">
              <Option v-for="item in treatAdvisorEmpNos" :value="item.userWorkCode" :key="item.userWorkCode">{{ item.userName }} - {{ item.userWorkCode }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.treatAdvisorNo" filterable clearable remote :remote-method="getTreatAdvisorEmpNos" label-in-value style="width: 150px" :loading="loading1" placeholder="接待顾问">
              <Option v-for="item in treatAdvisorEmpNos" :value="item.userWorkCode" :key="item.userWorkCode">{{ item.userName }} - {{ item.userWorkCode }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.stuIntention" style="width: 120px" @on-change="changeSubject" clearable placeholder="用户意向">
              <Option v-for="(item, index) in conditions.stuIntention" :value="item" :key="index">{{ item }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.channelId" style="width: 120px" clearable placeholder="渠道标签">
              <Option v-for="(item, index) in diagnosisChannels" :value="item.id" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.classType" style="width: 120px" clearable placeholder="授课类型">
              <Option :value="1">在线</Option>
              <Option :value="4">面授</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.isRegist" style="width: 120px" @on-change="changeSubject" clearable placeholder="是否报名">
              <Option v-for="(item, index) in conditions.isRegist" :value="item.id" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.isPay" style="width: 120px" @on-change="changeSubject" clearable placeholder="是否缴费">
              <Option v-for="(item, index) in conditions.isPay" :value="item.id" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.appointmentStatus" style="width: 120px" @on-change="changeSubject" clearable placeholder="是否取消">
              <Option v-for="(item, index) in conditions.appointmentStatus" :value="item.id" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>
          <FormItem>
            <Select v-model="formData.isAttendance" style="width: 120px" @on-change="changeSubject" clearable placeholder="考勤状态">
              <Option v-for="(item, index) in conditions.attendanceStatus" :value="item.id" :key="index">{{ item.name }}</Option>
            </Select>
          </FormItem>

          <FormItem>
            <DatePicker type="daterange" :editable="false" v-model="formData.createTime" style="width: 200px" @on-change="
                optDateChange($event, 'createStartTime', 'createEndTime')
              " placeholder="创建时间"></DatePicker>
          </FormItem>
          <FormItem>
            <DatePicker type="daterange" :editable="false" v-model="formData.auditionTime" style="width: 200px" placeholder="试听时间" @on-change="
                optDateChange($event, 'auditionStartTime', 'auditionEndTime')
              "></DatePicker>
          </FormItem>
        </template>
        <FormItem>
          <Input v-model="formData.stuRealName" clearable placeholder="学员姓名" />
        </FormItem>
        <FormItem>
          <Input number v-model="formData.phone" clearable placeholder="学员手机号" />
        </FormItem>
        <FormItem>
          <Input v-model="formData.stuCode" number clearable placeholder="学员编号" />
        </FormItem>
        <template v-if="activeTabName !== 'reservationRecord'">
          <FormItem>
            <DatePicker type="daterange" :editable="false" v-model="formData.applyTime" style="width: 200px"
              @on-change="optDateChange($event, 'createTime', 'endTime')" placeholder="申请时间">
            </DatePicker>
         </FormItem>
        </template>
        <div style="text-align: right; margin-bottom: 10px; margin-top: -30px">
          <Button v-if="roleId === 5" type="primary" style="margin-right: 10px" @click="createRrCode">试听老带新二维码</Button>
          <Button type="primary" style="margin-right: 10px" @click="onSearch">查询</Button>
          <Button v-if="activeTabName === 'reservationRecord'" type="primary" style="margin-right: 10px" :loading="btnLoading" @click="exportDataClick">导出数据</Button>
          <Button v-if="activeTabName === 'applyRecord'" type="primary" style="margin-right: 10px" :loading="btnLoading" @click="exportApplyDataClick">导出数据</Button>
        </div>
      </div>
    </Form>
    <div style="background: #fff;margin-top: 20px;padding: 10px;">
      <Tabs type="card" :animated="false" name="audition" v-model="activeTabName">
        <TabPane :label="customTabPaneLabel" name="applyRecord">
          <div class="flex">
            <span style="margin-right: 10px;">跟进状态: </span>
            <CheckboxGroup v-model="formData.applyStatusList" @on-change="onSearch">
                <Checkbox :label="0"><span>未跟进</span></Checkbox>
                <Checkbox :label="3"><span>跟进中</span></Checkbox>
                <Checkbox :label="2"><span>跟进结束</span></Checkbox>
            </CheckboxGroup>
          </div>
          <div class="flex">
            <span style="margin-right: 10px;">排序列表: </span>
            <Select style="width: 150px;" v-model="formData.orderSortType" @on-change="onSearch">
              <Option v-for="(item, index) in sortTypeList" :key="index" :value="item.value">{{item.label}}</Option>
            </Select>
          </div>
          <ApplyRecord :formData="formData" ref="applyRecord"></ApplyRecord>
        </TabPane>
        <TabPane label="试听预约记录" name="reservationRecord">
          <ReservationRecord :formData="formData" ref="reservationRecord"></ReservationRecord>
        </TabPane>
      </Tabs>
    </div>
    <Modal
      :mask-closable="false"
      title="老带新二维码"
      v-model="qrCode.showModal"
      :footer-hide="true"
      width="350px"
    >
      <div >
        <!-- <p style="text-align: center;font-size: 14px; font-weight: bold;margin-bottom: 10px;">学而思路杏杏老师邀请您参加免费试听</p> -->
        <img style="width: 100%;" :src="qrCode.activityCodeUrl" alt="">
      </div>
    </Modal>
  </div>
</template>

<script>
import commonFormSearchMixins from '@/mixins/commonFormSearch'
import { condition, getUserList, queryDiagnosisChannels } from '@/api/diagnosis'
import { dataPath, exportById, commonExportById, applyDataPath } from '@/api/audition'
import ReservationRecord from './reservation-record'
import ApplyRecord from './apply-record'
import { createRrCode } from '@/api/appointmentApply'
import { mapState } from 'vuex'
import Cookie from 'js-cookie'
export default {
  name: 'auditionMgt',
  mixins: [commonFormSearchMixins],
  components: {
    ReservationRecord,
    ApplyRecord
  },
  computed: {
    ...mapState({
      cityCode: (state) => state.roleRelativeData.userInfo.cityCode,
      sideMenuTasks: (state) => state.app.sideMenuTasks,
      roleId: (state) => state.roleRelativeData.userInfo.crmRoleId,
      deptId: (state) => state.roleRelativeData.userInfo.crmDeptId
    })
  },
  data () {
    return {
      conditions: {
        // 搜索条件
        stuIntention: [], // 用户意向
        isNewStudent: [], // 是否新学员
        isRegist: [], // 是否报名
        isPay: [], // 是否缴费
        attendanceStatus: [], // 考勤状态
        appointmentStatus: [] // 是否取消
      },
      setDefaultServiceCenter: true, // 是否带入默认服务中心、区域
      treatAdvisorEmpNos: [], // 接待顾问
      formData: {
        year: '',
        planAdvisorNo: '', // 预约顾问
        treatAdvisorNo: '', // 接待顾问
        stuIntention: '', // 用户意向
        isRegist: '', // 是否注册
        isPay: '', // 是否缴费
        appointmentStatus: '', // 是否取消
        isAttendance: '', // 是否出勤
        stuRealName: '', // 学员姓名
        phone: '', // 手机号
        stuCode: '', // 学员编号
        createTime: '',
        createStartTime: '', // 创建开始时间
        createEndTime: '', // 创建结束时间
        auditionTime: '',
        auditionStartTime: '', // 试听开始时间
        auditionEndTime: '', // 试听结束时间
        applyTime: '', // 申请时间
        channelId: '', // 渠道标签
        classType: '', // 授课类型
        orderSortType: 0, // 排序方式
        applyStatusList: [] // 跟进状态
      },
      btnLoading: false, // 导出loading
      diagnosisChannels: [],
      auditionInfo: {}, // 当前激活的试听信息
      taskNum: 0,
      customTabPaneLabel: (h) => {
        return h('div', [
          h('span', '试听申请记录'),
          h('Badge', {
            props: {
              count: this.$store.getters.getSideMenuTaskByRouterName('auditionMgt')
            }
          })
        ])
      },
      activeTabName: 'applyRecord', // 当前激活的tab
      sortTypeList: [
        { value: 1, label: '创建时间降序' },
        { value: 2, label: '创建时间升序' },
        { value: 3, label: '跟进时间降序' },
        { value: 4, label: '跟进时间升序' }
      ],
      qrCode: {
        // 老带新二维码
        showModal: false,
        activityCodeUrl: ''
      }
    }
  },
  mounted () {
    this.initView()
  },
  methods: {
    // 获取老带新二维码
    async createRrCode () {
      try {
        const data = await createRrCode({ cityCode: this.cityCode })
        this.qrCode.activityCodeUrl = data.data
        this.qrCode.showModal = true
      } catch (error) {
        this.$Message.error({
          content: `获取老带新二维码失败: ${error.message}`,
          duration: 5
        })
      }
    },
    onSearch () {
      this.$refs[this.activeTabName].search()
    },
    // 渠道标签列表
    queryDiagnosisChannels () {
      queryDiagnosisChannels({})
        .then((res) => {
          this.diagnosisChannels = Object.freeze(res)
        })
        .catch((err) => {
          this.$Message.error({ content: err.message, duration: 5 })
        })
    },
    // 导出数据
    exportDataClick () {
      let cityName = this.allCityList.filter(
        (item) => item.id === this.formData.cityCode
      )[0].name

      const params = Object.assign({}, this.formData, {
        areaCode: this.formData.cityCode,
        auditionStartTime: this.formData.auditionStartTime
          ? this.formData.auditionStartTime + ' ' + '00:00:00'
          : '',
        auditionEndTime: this.formData.auditionEndTime
          ? this.formData.auditionEndTime + ' ' + '23:59:59'
          : '',
        createStartTime: this.formData.createStartTime
          ? this.formData.createStartTime + ' ' + '00:00:00'
          : '',
        createEndTime: this.formData.createEndTime
          ? this.formData.createEndTime + ' ' + '23:59:59'
          : '',
        cityName,
        crmRoleId: this.$store.state.roleRelativeData.userInfo.crmRoleId
      })

      if (!params.createStartTime || !params.createEndTime) {
        return this.$Message.warning({
          content: '请选择创建时间，最多支持选择三个月',
          duration: 3
        })
      }

      this.btnLoading = true

      dataPath(params)
        .then(({ id }) => {
          this.$Message.info({
            content: '正在导出excel，请勿关闭当前标签页',
            duration: 5
          })
          // 记录当前时间
          let startTime = Date.now()
            ; (function getFilePath (id, that) {
            // 超过5分钟未导出提示重新下载
            if (Date.now() - startTime > 5 * 60 * 1000) {
              that.$Message.error({ content: '导出失败，请重试' })
              that.btnLoading = false
              return
            }
            exportById({ id })
              .then((data) => {
                if (data.filePath) {
                  setTimeout(() => {
                    window.open(data.filePath)
                    that.btnLoading = false
                  }, 500)
                } else {
                  that.timer = setTimeout(() => {
                    getFilePath(id, that)
                  }, 2000)
                }
              })
              .catch((err) => {
                that.btnLoading = false
                that.$Message.error({ content: err.message, duration: 5 })
              })
          })(id, this)
        })
        .catch((err) => {
          this.btnLoading = false
          this.$Message.error({ content: err.message, duration: 5 })
        })
    },
    // 试听申请记录 导出数据
    exportApplyDataClick () {
      const req = Object.assign({}, {
        cityCode: this.roleId === 2 ? this.cityCode : null,
        stuRealName: this.formData.stuRealName,
        phone: this.formData.phone,
        stuCode: this.formData.stuCode,
        empNo:
          this.roleId === 5
            ? JSON.parse(Cookie.get('columbusUserInfo')).id
            : null,
        deptId: this.roleId === 4 ? this.deptId : null,
        createTime: this.formData.createTime,
        endTime: this.formData.endTime
      })

      if (!req.createTime || !req.endTime) {
        return this.$Message.warning({
          content: '请选择申请时间，最多支持选择六个月',
          duration: 3
        })
      }

      this.btnLoading = true

      applyDataPath(req)
        .then(({ id }) => {
          this.$Message.info({
            content: '正在导出excel，请勿关闭当前标签页',
            duration: 5
          })
          // 记录当前时间
          let startTime = Date.now()
            ; (function getFilePath (id, that) {
            // 超过5分钟未导出提示重新下载
            if (Date.now() - startTime > 5 * 60 * 1000) {
              that.$Message.error({ content: '导出失败，请重试' })
              that.btnLoading = false
              return
            }
            commonExportById({
              id,
              queryId: 1471
            })
              .then((data) => {
                if (data.filePath) {
                  setTimeout(() => {
                    window.open(data.filePath)
                    that.btnLoading = false
                  }, 500)
                } else {
                  that.timer = setTimeout(() => {
                    getFilePath(id, that)
                  }, 2000)
                }
              })
              .catch((err) => {
                that.btnLoading = false
                that.$Message.error({ content: err.message, duration: 5 })
              })
          })(id, this)
        })
        .catch((err) => {
          this.btnLoading = false
          this.$Message.error({ content: err.message, duration: 5 })
        })
    },
    // 远程搜索接待顾问
    getTreatAdvisorEmpNos (userName) {
      const params = {
        userName
      }
      getUserList(params)
        .then((res) => {
          this.treatAdvisorEmpNos = Object.freeze(res)
        })
        .catch((err) => {
          this.$Message.error({ content: err.message, duration: 5 })
        })
    },
    // 获取搜索条件
    getCondition () {
      const params = {}
      condition(params)
        .then((res) => {
          this.conditions = Object.freeze(res)
        })
        .catch((err) => {
          this.$Message.error({ content: err.message, duration: 5 })
        })
    },
    // 初始化
    initView () {
      this.getCitys()
      this.getYears()
      this.getCondition()
      this.queryDiagnosisChannels()
    }
  }
}
</script>

<style lang="less" scoped>
/deep/ .filter-search .ivu-form-item-required .ivu-form-item-label {
  width: 10px !important;
  transform: translate(5px);
}

.flex {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}
</style>
