<template>
  <!-- 试听申请记录 -->
  <div>
    <Table border :loading="tableLoading" ref="selection" :columns="columns" :data="tableData"></Table>
    <div style="margin: 10px" :class="{ 'page-fix': tableData.length < 3 }">
      <div class="page-box" style="text-align: right">
        <Page :total="tableConfig.totalCount" :page-size="tableConfig.pageSize" :current.sync="tableConfig.pageNo" show-elevator show-sizer show-total @on-page-size-change="pageSizeChange" @on-change="changePage"></Page>
      </div>
    </div>

    <!-- 修改申请状态 -->
    <Modal :mask-closable="false" title="修改" :footer-hide="true" v-model="changeApplyStatusModal.show" width="560px">
      <ChangeApplyStatus v-if="changeApplyStatusModal.show"
        @close="changeApplyStatusModal.show=false"
        @success="refreshSearch"
        :applyInfo="changeApplyStatusModal" />
    </Modal>
  </div>
</template>

<script>
import CustomerMixin from '@/components/mixin/customerDetail'
import ChangeApplyStatus from './change-apply-status'
import { getApplyList } from '@/api/appointmentApply'
import { TalDecodeText } from '@peiyou/tal-element'
import { mapState } from 'vuex'
import Cookie from 'js-cookie'

export default {
  name: 'applyRecord',
  mixins: [CustomerMixin],
  props: {
    formData: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  computed: {
    ...mapState({
      deptId: (state) => state.roleRelativeData.userInfo.crmDeptId,
      roleId: (state) => state.roleRelativeData.userInfo.crmRoleId,
      cityCode: (state) => state.roleRelativeData.userInfo.cityCode
    })
  },
  components: {
    ChangeApplyStatus
  },
  data () {
    return {
      tableLoading: false,
      tableData: [],
      tableConfig: {
        pageSize: 10,
        pageNo: 1,
        totalCount: 0
      },
      changeApplyStatusModal: {
        show: false
      },
      auditionInfo: {}, // 当前激活的试听信息
      columns: [
        {
          title: '操作',
          align: 'center',
          width: '70',
          render: (h, params) => {
            return h(
              'a',
              {
                on: {
                  click: () => {
                    this.changeApplyStatus(params.row)
                  }
                }
              },
              '修改'
            )
          }
        },
        {
          title: '学员姓名',
          key: 'stuRealName',
          align: 'center',
          render: (h, params) => {
            const divs = [
              h(
                'a',
                {
                  on: {
                    click: () => {
                      const stu = {
                        card: params.row.stuCode,
                        cityCode: params.row.stuCityCode
                      }
                      this.goCustomerDetail(stu)
                    }
                  }
                },
                params.row.stuName || '--'
              ),
              h('Icon', {
                props: {
                  custom: 'iconfont icon-phone-fill'
                },
                style: {
                  cursor: 'pointer',
                  marginLeft: '5px',
                  display: this.callShow(params.row.stuCityCode) ? 'block' : 'none'
                },
                on: {
                  click: () => {
                    this.$store.state.app.callInstance.call(params.row.stuCityCode, params.row.pyUid)
                  }
                }
              })
            ]
            return h('div', {
              style: {
                display: 'flex',
                alignItems: 'center'
              }
            }, divs)
          }
        },
        {
          title: '联系电话',
          key: 'phone',
          align: 'center',
          width: 120,
          render: (h, params) => {
            return h(TalDecodeText, {
              props: {
                uid: params.row.pyUid,
                encodeText: params.row.phone,
                type: 'phone'
              }
            })
          }
        },
        {
          title: '维护顾问',
          key: 'maintainerName',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.maintainerName || '--')
          }
        },
        {
          title: '跟进状态(一级)',
          key: 'applyStatus',
          align: 'center',
          render: (h, params) => {
            return h('span', this.applyStatusMap[params.row.applyStatus] || '--')
          }
        },
        {
          title: '跟进状态(二级)',
          key: 'applySecondStatus',
          align: 'center',
          render: (h, params) => {
            return h('span', this.applySecondStatusMap[params.row.applySecondStatus] || '--')
          }
        },
        {
          title: '最近跟进时间',
          key: 'followTime',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.followTime || '--')
          }
        },
        {
          title: '跟进次数',
          key: 'modifyCount',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.modifyCount || '--')
          }
        },
        {
          title: '渠道链接',
          key: 'applyChannel',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.applyChannel || '--')
          }
        },
        {
          title: '申请时间',
          key: 'createTime',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.createTime || '--')
          }
        },
        {
          title: '期望试听时间',
          key: 'subTime',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.subTime || '--')
          }
        },
        {
          title: '推荐顾问',
          key: 'referrerName',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.referrerName || '--')
          }
        },
        {
          title: '推荐学员',
          key: 'referrerStuName',
          align: 'center',
          render: (h, params) => {
            return params.row.referrerStuCode
              ? h(
                'a',
                {
                  on: {
                    click: () => {
                      const stu = {
                        card: params.row.referrerStuCode,
                        cityCode: params.row.referrerStuCityCode
                      }
                      this.goCustomerDetail(stu)
                    }
                  }
                },
                params.row.referrerStuName || '无'
              )
              : h('span', params.row.referrerStuName || '无')
          }
        },
        {
          title: '推荐学员编号',
          key: 'referrerStuName',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.referrerStuCode || '')
          }
        },
        {
          title: '意向服务中心',
          key: 'intendedServiceCenter',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.intendedServiceCenter || '--')
          }
        },
        {
          title: '年份',
          key: 'year',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.year || '--')
          }
        },
        {
          title: '年级',
          key: 'gradeName',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.gradeName || '--')
          }
        },
        {
          title: '学科',
          key: 'subject',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.subject || '--')
          }
        },
        {
          title: '授课类型',
          key: 'classType',
          align: 'center',
          render: (h, params) => {
            return h('span', this.classTypeMap[params.row.classType] || '--')
          }
        },
        {
          title: '是否跟进',
          key: 'classType',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.modifyCount > 0 ? '是' : '否')
          }
        },
        {
          title: '是否跟进',
          key: 'modifyCount',
          align: 'center',
          render: (h, params) => {
            return h(
              'span',
              params.row.modifyCount > 0 ? '是' : '否'
            )
          }
        }
      ],

      applyStatusMap: { // 一级跟进状态
        0: '未跟进',
        1: '已预约',
        2: '跟进结束',
        3: '跟进中'
      },
      applySecondStatusMap: { // 二级跟进状态
        1: '继续跟进',
        2: '未接通',
        3: '无意向',
        4: '已用其他账号预约',
        5: '已转化无需预约',
        6: '无匹配课程'
      },
      classTypeMap: {
        1: '在线',
        2: '双师',
        4: '面授'
      }

    }
  },
  methods: {
    callShow (cityCode) { // 外呼组件是否灰度显示
      const callGrayCity = this.$store.state.app.callGrayCity
      if ((!callGrayCity || !callGrayCity.length || callGrayCity.includes(cityCode)) && this.$store.state.roleRelativeData.roleIds.includes(this.$store.state.roleRelativeData.userInfo.crmRoleId)) {
        return true
      }
      return false
    },
    changeApplyStatus (row) {
      this.changeApplyStatusModal.show = true
      this.changeApplyStatusModal = Object.assign({}, row, { show: true })
    },
    // 更新查询
    refreshSearch () {
      setTimeout(() => {
        this.getTableData()
      }, 1500)
    },
    search () {
      this.tableConfig.pageNo = 1
      this.tableConfig.pageSize = 10
      this.getTableData()
    },
    // 分页
    changePage (num) {
      this.tableConfig.pageNo = num
      this.getTableData()
    },
    pageSizeChange (size) {
      this.tableConfig.pageNo = 1
      this.tableConfig.pageSize = size
      this.getTableData()
    },
    // 散诊记录查询
    getTableData () {
      const params = {
        isFollowFlag: 1, // 是否显示跟进次数
        cityCode: this.roleId === 2 ? this.cityCode : null,
        stuRealName: this.formData.stuRealName,
        phone: this.formData.phone,
        stuCode: this.formData.stuCode,
        createTime: this.formData.createTime,
        endTime: this.formData.endTime,
        empNo:
          this.roleId === 5
            ? JSON.parse(Cookie.get('columbusUserInfo')).id
            : null,
        deptId: this.roleId === 4 ? this.deptId : null,
        orderSortType: this.formData.orderSortType,
        applyStatusList: this.formData.applyStatusList,
        ...this.tableConfig
      }

      this.tableLoading = true
      getApplyList(params)
        .then((res) => {
          this.tableConfig.totalCount = res.totalCount
          this.tableData = Object.freeze(res.records) || []
        })
        .catch((err) => {
          this.tableConfig.totalCount = 0
          this.$Message.error({ content: err.message, duration: 5 })
        })
        .finally(() => {
          this.tableLoading = false
        })
    }
  }
}
</script>

<style lang="less" scoped>
</style>
