<template>
  <Table
    border
    :loading="loading"
    :columns="detailCols"
    :data="stuList"
  ></Table>
</template>
<script>
import CustomerMixin from '@/components/mixin/customerDetail'
export default {
  name: 'StuDetailExpand',
  mixins: [ CustomerMixin ],
  props: {
    stuList: Array,
    loading: Boolean
  },
  data () {
    return {
      detailCols: [
        {
          title: '序号',
          type: 'index',
          align: 'center'
        },
        {
          title: '学员姓名',
          key: 'name',
          align: 'center',
          render: (h, params) => {
            return params.row.stuLoginName ? h('a', {
              on: {
                click: () => {
                  const stu = {
                    card: params.row.stuLoginName,
                    cityCode: params.row.areaCode
                  }
                  this.goCustomerDetail(stu)
                }
              }
            }, params.row.name || '--') : h('span', params.row.name || '--')
          }
        },
        {
          title: '学员编号',
          key: 'stuLoginName',
          align: 'center'
        },
        {
          title: '是否攒班',
          key: 'isAssemble',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.isAssemble ? '是' : '否')
          }
        },
        {
          title: '报名状态',
          key: 'registStatus',
          align: 'center',
          render: (h, params) => {
            return h('span', this.registStatusMap[params.row.registStatus] || '--')
          }
        },
        {
          title: '缴费状态',
          key: 'payStatus',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.payStatus ? '已缴费' : '未缴费')
          }
        },
        {
          title: '报名人',
          key: 'creator',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.creator === null ? '--' : (params.row.creator || '用户'))
          }
        },
        {
          title: '报名渠道',
          key: 'regWay',
          align: 'center',
          render: (h, params) => {
            return h('span', this.regWayMap[params.row.regWay] || '--')
          }
        },
        {
          title: '报名时间',
          key: 'regTime',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.regTime || '--')
          }
        },
        {
          title: '缴费时间',
          key: 'payTime',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.payTime || '--')
          }
        },
        {
          title: '操作',
          key: 'address',
          align: 'center',
          render: (h, params) => {
            return params.row.registStatus ? '' : h('a', {
              on: {
                click: () => {
                  this.$emit('apply', params.row)
                  // this.applyParams.classId = params.row.classId
                  // this.applyParams.studentId = params.row.studentId
                  // this.applyParams.cityCode = params.row.areaCode
                  // this.applyShow = true
                }
              }
            }, '报名')
          }
        }
      ],
      registStatusMap: {
        0: '未报名',
        1: '已报名',
        2: '报名失败'
      },
      regWayMap: {
        0: '前台报名',
        1: '网上报名',
        2: '电话报名',
        3: 'App', // Android
        4: 'App', // IOS
        5: 'CRM报名',
        6: '微信活动',
        7: '微信',
        8: '百度直达号'
      }
    }
  }
}
</script>
