<template>
  <Modal
    v-model="schoolEditState"
    title="编辑学校人数"
    :mask-closable="false"
    :closable="true"
    @on-visible-change="handleEvents"
    width="420"
    :footer-hide="true"
    class="server-wrapper"
  >
    <div class="school-edit-container">
      <div class="" style="margin-bottom: 20px">
        <span style="margin-right: 80px">学校：{{schoolData.name}}</span>
        <span>年部：{{schoolData.levelName}}</span>
      </div>
      <Form ref="schoolEdit" label-position="left">
        <Row class="row grade-item" type="flex" v-for="(item,index) in schoolList" :key="index">
          <Col style="width: 280px;">
            <FormItem :label-width="100" :label="item.gradeName" :class="{'item-error':item.showMark}" >
              <Input type="number" v-model="item.gradeNumber" width="180" placeholder="请输入" @on-change="handleRule(item)" />
              <span class="error-text">请正确填写数量</span>
              <div class="select-icon">*</div>
            </FormItem>
          </Col>
        </Row>
        <Row class="row grade-item" type="flex">
          <Col style="width: 280px;margin-bottom: 20px;">
            <FormItem :label-width="100" label="学校总人数">
              <Input type="text" disabled v-model="total" width="180" />
            </FormItem>
          </Col>
        </Row>
      </Form>
      <Row style="text-align: right;padding-right: 30px;">
        <Button type="primary" style="margin-right: 20px;" @click="handleSubmitSchoolNum">提交</Button>
        <Button @click="handleCancle">取消</Button>
      </Row>
    </div>

  </Modal>
</template>

<script>
import {
  searchSchoolGrade,
  saveSchoolGradeNums
} from '@/api/operative'
export default {
  name: 'index',
  props: ['schoolData'],
  data () {
    return {
      schoolList: []
    }
  },
  methods: {
    handleEvents: function (state) {
      if (state) {
        searchSchoolGrade(this.schoolData.params).then(res => {
          if (res.rlt !== true) return
          let schoolList = res.data.schoolGradeNumDtoList
          schoolList.forEach(item => {
            item.showMark = false
            item.gradeNumber = item.gradeNumber ? item.gradeNumber : ''
          })
          this.schoolList = schoolList
        }).catch((err) => {
          this.$Message.error(err.message)
        })
      }
    },
    handleSubmitSchoolNum: function () {
      let mark = true
      this.schoolList.forEach(item => {
        if (!(item.gradeNumber >= 1)) {
          item.showMark = true
          mark = false
        }
      })
      if (!mark) return
      const params = {
        ...this.schoolData.params,
        gradeTypeName: this.schoolData.levelName, // 年部名称
        schoolName: this.schoolData.name, // 学校名称
        schoolGradeNumDtoList: this.schoolList
      }
      saveSchoolGradeNums(params).then(res => {
        if (res.rlt === true) {
          this.$Message.success('保存成功')
          this.$store.state.service.schoolEditState = false
          if (this.schoolData.checkAllGroupIds.length > 0 && this.schoolData.checkAllGroupIds.includes(this.schoolData.params.schoolId)) {
            this.$emit('resetStaticies')
          }
        }
      }).catch((err) => {
        this.$Message.error(err.message)
      })
    },
    // 取消
    handleCancle: function () {
      this.$store.state.service.schoolEditState = false
    },
    // 检验数量
    handleRule: function (item) {
      if (item.showMark && item.gradeNumber >= 0) {
        item.showMark = false
      }
    }
  },
  computed: {
    schoolEditState: {
      get () {
        return this.$store.state.service.schoolEditState
      },
      set (val) {
        this.$store.state.service.schoolEditState = val
      }
    },
    total () {
      const reducer = (accumulator, currentValue) => Number(accumulator) + Number(currentValue)
      return this.schoolList.map(item => item.gradeNumber).reduce(reducer, 0)
    }

  }
}
</script>

<style lang="less">
  .school-edit-container{
    padding-left: 40px;
    .grade-item{
      position: relative;
      .ivu-form-item{
        margin-bottom: 0;
      }
      .ivu-form-item-label{
        padding-left: 10px;
      }
      .ivu-form-item-content{
        position: inherit;
      }
      .error-text{
        visibility: hidden;
        color: #ed4014;
        float: left;
        height: 20px;
        line-height: 20px;
      }
      .item-error{
        .error-text{
          visibility: visible;
        }
        .ivu-input{
          border-color: #ed4014;
        }
      }
      .select-icon{
        color: #ed4014;
        position: absolute;
        left: 0;
        top: 4px;
        font-size: 16px;
      }
    }
  }
</style>
