<template>
  <!-- 开班评估列表 -->
  <div>
    <Form
      inline
      :label-width="80"
    >
      <FormItem label="审批状态">
        <Select
          style="width: 150px;"
          clearable
          v-model="formData.status"
        >
          <Option
            v-for="item in approvalStatusList"
            :value="item.value"
            :key="item.value"
          >{{ item.label }}</Option>
        </Select>
      </FormItem>
      <FormItem label="审批人">
        <Select
          v-model="formData.receptionEmpNo"
          filterable
          clearable
          remote
          :remote-method="q => getAdvisorEmpNos(q, parentFormData.cityCode)"
          label-in-value
          style="width: 150px;"
        >
          <Option
            v-for="item in advisorEmpNos"
            :value="item.empNo"
            :key="item.empNo"
          >{{item.name}}-{{item.empNo}}</Option>
        </Select>
      </FormItem>
    </Form>

    <!-- 列表 -->
    <Table
      border
      ref="selection"
      :columns="columns"
      :data="tableData"
      :loading="tableLoading"
    ></Table>

    <!-- 分页 -->
    <div
      style="margin: 10px;"
      class="flex-page"
    >
      <Page
        :total="searchData.totalCount"
        :page-size="searchData.pageSize"
        :current.sync="searchData.pageNo"
        show-elevator
        show-sizer
        show-total
        transfer
        @on-page-size-change="pageSizeChange"
        @on-change="changePage"
      ></Page>
    </div>
  </div>
</template>

<script>
import { findList } from '@/api/assembleClass'
import baseDataSearchMixin from './baseDataSearchMixin'
import cuanbanMixin from './cuanbanMixin'
export default {
  name: 'OpenClassAssess',
  mixins: [baseDataSearchMixin, cuanbanMixin],
  props: {
    parentFormData: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  data () {
    return {
      formData: {
        receptionEmpNo: '', // 审批人
        status: '' // 审批状态
      },
      columns: [
        {
          title: '操作',
          key: 'name',
          align: 'center',
          render: (h, params) => {
            // type代表拼团 status代表攒班
            let isDisable = params.row.type === 1 ? true : params.row.status !== 0
            return h('div', [
              h('a', {
                on: {
                  click: () => {
                    this.$emit('check', params.row.id)
                  }
                }
              }, '查看'),
              h('br'),
              !isDisable ? h('a', {
                on: {
                  click: () => {
                    this.$emit('approval', params.row.id)
                  }
                }
              }, '审批') : h('span', { style: { color: 'gray' } }, '审批')
            ])
          }
        },
        {
          title: '操作人',
          key: 'creatorName',
          align: 'center'
        },
        {
          title: '操作时间',
          key: 'createDate',
          align: 'center'
        },
        {
          title: '年份',
          key: 'year',
          align: 'center'
        },
        {
          title: '年级',
          key: 'gradeName',
          align: 'center'
        },
        {
          title: '意向学科',
          key: 'subjectName',
          align: 'center'
        }, {
          title: '意向学期',
          key: 'termName',
          align: 'center'
        }, {
          title: '班次级别',
          key: 'classLevelName',
          align: 'center'
        }, {
          title: '授课类型',
          key: 'classTypeName',
          align: 'center'
        }, {
          title: '意向服务中心',
          key: 'serviceCenterName',
          align: 'center'
        }, {
          title: '意向上课时间',
          key: 'classtimeTimeClassify',
          align: 'center'
        }, {
          title: '意向上课时段',
          key: 'classtimeTimePeriod',
          align: 'center'
        }, {
          title: '审批状态',
          key: 'status',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.status === 0 ? '未审批' : '已审批')
          }
        }, {
          title: '开班状态',
          key: 'status',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.status === 1 ? '开班成功' : params.row.status === 2 ? '开班失败' : '--')
          }
        }, {
          title: '开班班级ID',
          key: 'assembleClassId',
          align: 'center'
        }, {
          title: '审批人',
          key: 'receptionEmpName',
          align: 'center'
        }
      ],
      tableLoading: false,
      tableData: [],
      searchData: {
        totalCount: 0,
        pageSize: 10,
        pageNo: 1
      }
    }
  },
  watch: {
    formData: {
      deep: true,
      handler: function () {
        this.search()
      }
    }
  },
  methods: {
    // 获取开班评估列表
    async search () {
      try {
        const params = Object.assign({}, this.searchData, this.formData, {
          cityCode: this.parentFormData.cityCode,
          year: this.parentFormData.year,
          termId: this.parentFormData.termId,
          subjectId: this.parentFormData.subjectId,
          gradeId: this.parentFormData.gradeId,
          classLevel: this.parentFormData.classLevel,
          serviceCenterId: this.parentFormData.serviceCenterId,
          classtimeTimePeriod: this.parentFormData.classtimeTimePeriod,
          classtimeTimeClassify: this.parentFormData.classtimeTimeClassify,
          classType: this.parentFormData.classType
        })
        if (params.classType === 1 || !params.classType) {
          delete params.serviceCenterId
        }
        this.tableLoading = true
        const res = await findList(params)
        this.tableData = Object.freeze(res.records)
        this.searchData.totalCount = res.totalCount
      } catch (error) {
        this.$Message.error({ content: `获取评估列表失败: ${error.message}`, duration: 5 })
      }
      this.tableLoading = false
    },
    // 分页
    changePage (num) {
      this.searchData.pageNo = num
      this.search()
    },
    pageSizeChange (size) {
      this.searchData.pageNo = 1
      this.searchData.pageSize = size
      this.search()
    }
  }
}
</script>

<style lang="less" scoped>
.flex-page {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
</style>
