/**
 * 不包含ploatform
 */
import HttpRequest from './axios.operative'
import { runEnv } from './util'
const env = runEnv()
let baseURL = ''
switch (env) {
  case 'dev':
    baseURL = `/`
    break
  case 'external':
    baseURL = `http://beta-gateway-one.speiyou.cn/`
    break
  case 'beta':
    // baseURL = `http://auth.one.speiyou.net/employee/getRoleResources-=`
    baseURL = `http://gateway.one.speiyou.net/`
    break
  case 'prod':
    baseURL = `https://gateway-one.speiyou.cn/`
    break
}
sessionStorage.setItem('currentEnv', env)
const Axios = new HttpRequest({
  baseURL: baseURL,
  withCredentials: true, // 跨域请求时发送 cookies
  timeout: 30000 // request timeout
})
export default Axios
