<template>
  <!-- 试听预约记录 -->
  <div>
    <Table
      border
      :loading="tableLoading"
      ref="selection"
      :columns="columns"
      :data="tableData"
    >
    </Table>
    <div style="margin: 10px" :class="{ 'page-fix': tableData.length < 3 }">
      <div class="page-box" style="text-align: right">
        <Page
          :total="tableConfig.totalCount"
          :page-size="tableConfig.pageSize"
          :current.sync="tableConfig.pageNo"
          show-elevator
          show-sizer
          show-total
          @on-page-size-change="pageSizeChange"
          @on-change="changePage"
        >
        </Page>
      </div>
    </div>
    <!-- 班级详情 -->
    <tal-class-detail
      :class-id="classDetail.id"
      :show="classDetail.show"
      @close="classDetail.show = false"
    >
    </tal-class-detail>

    <!-- 跟进记录 -->
      <Modal
        :mask-closable="false"
        title="试听跟进"
        v-model="followUp.showModal"
        :footer-hide="true"
        width="760px"
      >
        <FollowRecord
          v-if="followUp.showModal"
          :appointmentId="followUp.id"
          :studentDetail="stuDetail"
          :operationType="1"
          :appointmentApplyId="followUp.appointmentApplyId"
          @success="refreshSearch"
          @close="followUp.showModal = false"
        >
        </FollowRecord>
      </Modal>

      <!-- 取消试听 -->
      <Modal
        :mask-closable="false"
        title="取消试听"
        :footer-hide="true"
        v-model="cancelAuditionModal.show"
        width="460px"
      >
        <CancelAudition
          v-if="cancelAuditionModal.show"
          @success="refreshSearch"
          @cancel="cancelAuditionModal.show = false"
          v-bind="cancelAuditionModal"
          :operationType="1"
        >
        </CancelAudition>
      </Modal>

      <!-- 修改考勤 -->
      <Modal
        :mask-closable="false"
        title="修改考勤"
        :footer-hide="true"
        v-model="changeAttendanceModal.show"
        width="560px"
      >
        <ChangeAttendance
          v-if="changeAttendanceModal.show"
          :auditionInfo="changeAttendanceModal"
          @success="refreshSearch"
          @cancel="changeAttendanceModal.show = false"
        >
        </ChangeAttendance>
      </Modal>

  </div>
</template>

<script>
import CustomerMixin from '@/components/mixin/customerDetail'
import classDetailMixin from '@/mixins/classDetail'
import CancelAudition from './cancelAudition'
import ChangeAttendance from './changeAttendance'
import FollowRecord from '@/components/appointment/followRecord.vue'
import { auditionAppointmentList } from '@/api/audition'
import { TalDecodeText } from '@peiyou/tal-element'
export default {
  name: 'reservationRecord',
  mixins: [CustomerMixin, classDetailMixin],
  props: {
    formData: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  components: {
    FollowRecord,
    CancelAudition,
    ChangeAttendance
  },
  data () {
    return {
      tableLoading: false,
      tableData: [],
      tableConfig: {
        pageSize: 10,
        pageNo: 1,
        totalCount: 0
      },
      stuDetail: {
        uid: '',
        card: '',
        cityCode: '',
        areaCode: '',
        realName: '',
        gradeName: ''
      },
      followUp: {
        showModal: false,
        id: false,
        operateType: 1 // 试听
      },

      cancelAuditionModal: {
        show: false
      },
      changeAttendanceModal: {
        show: false
      },
      // 考勤状态
      attendenceStatus: {
        0: '缺勤',
        1: '准时上课',
        2: '迟到'
      },
      auditionInfo: {}, // 当前激活的试听信息
      columns: Object.freeze([
        {
          title: '操作',
          align: 'center',
          width: '70',
          render: (h, params) => {
            return h('div', [
              h(
                'a',
                {
                  on: {
                    click: () => {
                      this.stuDetail.uid = params.row.pyUid
                      this.stuDetail.card = params.row.stuCode
                      this.stuDetail.realName = params.row.stuRealName
                      this.stuDetail.cityCode = this.stuDetail.areaCode = this.formData.cityCode
                      this.stuDetail.gradeName = params.row.grade
                      this.followUp.id = params.row.id
                      this.followUp.appointmentApplyId = params.row.appointmentApplyId
                      this.followUp.showModal = true
                    }
                  }
                },
                '跟进'
              ),
              h('p'),
              // appointmentStatus 5 已取消
              params.row.appointmentStatus === 5 ||
                params.row.curriculumStatus === 1
                ? h('span', { style: { color: 'gray' } }, '取消试听')
                : h(
                  'a',
                  {
                    on: {
                      click: () => {
                        this.cancelAudition(params.row)
                      }
                    }
                  },
                  '取消试听'
                ),
              h('p'),
              // isAttendanceRecordModified 1 已修改 0 未修改
              params.row.isAttendanceRecordModified === 1
                ? h('span', { style: { color: 'gray' } }, '修改考勤')
                : h(
                  'a',
                  {
                    on: {
                      click: () => {
                        this.changeAttendance(params.row)
                      }
                    }
                  },
                  '修改考勤'
                ),
              h('br'),
              params.row.evaluationUrl
                ? h(
                  'a',
                  {
                    attrs: {
                      href: params.row.evaluationUrl
                    }
                  },
                  '试听反馈表'
                )
                : h('span', { style: { color: 'gray' } }, '试听反馈表')
            ])
          }
        },
        {
          title: '学员姓名',
          key: 'stuRealName',
          align: 'center',
          render: (h, params) => {
            const divs = [
              h(
                'a',
                {
                  on: {
                    click: () => {
                      const stu = {
                        card: params.row.stuCode,
                        cityCode: params.row.areaCode
                      }
                      this.goCustomerDetail(stu)
                    }
                  }
                },
                params.row.stuRealName || '无'
              ),
              h('Icon', {
                props: {
                  custom: 'iconfont icon-phone-fill'
                },
                style: {
                  cursor: 'pointer',
                  marginLeft: '5px',
                  display: this.callShow(params.row.areaCode) ? 'block' : 'none'
                },
                on: {
                  click: () => {
                    this.$store.state.app.callInstance.call(params.row.stuCityCode, params.row.pyUid)
                  }
                }
              })
            ]
            return h('div', {
              style: {
                display: 'flex',
                alignItems: 'center'
              }
            }, divs)
          }
        },
        {
          title: '联系电话',
          key: 'phone',
          align: 'center',
          width: 120,
          render: (h, params) => {
            return h(TalDecodeText, {
              props: {
                uid: params.row.pyUid,
                encodeText: params.row.phone,
                type: 'phone'
              }
            })
          }
        },
        {
          title: '绑定顾问',
          key: 'belongAdvisorName',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.belongAdvisorName || '无')
          }
        },
        {
          title: '预约顾问',
          key: 'planAdvisorName',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.planAdvisorName || '无')
          }
        },
        {
          title: '接待顾问',
          key: 'treatAdvisorName',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.treatAdvisorName || '无')
          }
        },
        {
          title: '渠道链接',
          key: 'applyChannel',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.applyChannel || '无')
          }
        },
        {
          title: '是否领取伴手礼',
          key: 'isGift',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.isGift === 1 ? '是' : '否')
          }
        },
        {
          title: '用户意向',
          key: 'stuIntention',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.stuIntention || '无')
          }
        },
        {
          title: '渠道标签',
          key: 'channel',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.channel || '无')
          }
        },
        {
          title: '创建时间',
          key: 'createTime',
          align: 'center',
          width: 100,
          render: (h, params) => {
            return h('span', params.row.createTime || '无')
          }
        },
        {
          title: '是否取消',
          key: 'appointmentStatus',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.appointmentStatus === 5 ? '是' : '否')
          }
        },
        {
          title: '考勤状态',
          key: 'isAttendance',
          align: 'center',
          render: (h, params) => {
            const attendenceName =
              this.attendenceStatus[params.row.isAttendance] || '--'
            return h('span', attendenceName)
          }
        },
        {
          title: '年份',
          key: 'year',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.year || '--')
          }
        },
        {
          title: '试听学期',
          key: 'term',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.term || '--')
          }
        },
        {
          title: '试听学科',
          key: 'subject',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.subject || '--')
          }
        },
        {
          title: '试听年级',
          key: 'grade',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.grade || '--')
          }
        },
        {
          title: '试听课程',
          key: 'className',
          align: 'center',
          render: (h, params) => {
            return params.row.className
              ? h(
                'a',
                {
                  on: {
                    click: () => {
                      this.showClassDetail(params.row.classId)
                    }
                  }
                },
                params.row.className
              )
              : h('span', '--')
          }
        },
        {
          title: '试听课次',
          key: 'curriculumNum',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.curriculumNum || '--')
          }
        },
        {
          title: '上课时间',
          key: 'classTimeVal',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.classTimeVal || '--')
          }
        },
        {
          title: '试听服务中心',
          key: 'serviceCenter',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.serviceCenter || '--')
          }
        },
        {
          title: '是否报名同学科长期班',
          key: 'isRegist',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.isRegist === 1 ? '是' : '否')
          }
        },
        {
          title: '是否缴费同学科长期班',
          key: 'isPay',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.isPay === 1 ? '是' : '否')
          }
        },
        {
          title: '跟进次数',
          key: 'followCount',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.followCount)
          }
        }
      ])
    }
  },
  methods: {
    callShow (cityCode) { // 外呼组件是否灰度显示
      const callGrayCity = this.$store.state.app.callGrayCity
      if ((!callGrayCity || !callGrayCity.length || callGrayCity.includes(cityCode)) && this.$store.state.roleRelativeData.roleIds.includes(this.$store.state.roleRelativeData.userInfo.crmRoleId)) {
        return true
      }
      return false
    },
    // 取消试听
    cancelAudition (item) {
      this.cancelAuditionModal = Object.assign(this.cancelAuditionModal, item, {
        appointmentId: item.id
      })
      this.cancelAuditionModal.show = true
    },
    // 修改考勤
    changeAttendance (item) {
      this.changeAttendanceModal = Object.assign({}, item, {
        show: true,
        auditionId: item.businessId,
        uid: item.pyUid
      })
    },
    // 更新查询
    refreshSearch () {
      this.followUp.showModal = false
      setTimeout(() => {
        this.getTableData()
      }, 1500)
    },
    search () {
      if (!this.formData.cityCode) {
        return this.$Message.warning({ content: '请选择城市', duration: 5 })
      }
      this.tableConfig.pageNo = 1
      this.tableConfig.pageSize = 10
      this.getTableData()
    },
    // 分页
    changePage (num) {
      this.tableConfig.pageNo = num
      this.getTableData()
    },
    pageSizeChange (size) {
      this.tableConfig.pageNo = 1
      this.tableConfig.pageSize = size
      this.getTableData()
    },
    // 散诊记录查询
    getTableData () {
      // 删除试听申请记录对于申请时间的查询参数
      if (this.formData.hasOwnProperty('applyTime')) {
        delete this.formData.applyTime
        delete this.formData.createTime
        delete this.formData.endTime
      }
      const params = Object.assign({}, this.formData, this.tableConfig, {
        areaCode: this.formData.cityCode,
        auditionStartTime: this.formData.auditionStartTime
          ? this.formData.auditionStartTime + ' ' + '00:00:00'
          : '',
        auditionEndTime: this.formData.auditionEndTime
          ? this.formData.auditionEndTime + ' ' + '23:59:59'
          : '',
        createStartTime: this.formData.createStartTime
          ? this.formData.createStartTime + ' ' + '00:00:00'
          : '',
        createEndTime: this.formData.createEndTime
          ? this.formData.createEndTime + ' ' + '23:59:59'
          : ''
      })
      this.tableLoading = true
      auditionAppointmentList(params)
        .then((res) => {
          this.tableConfig.totalCount = res.totalCount
          this.tableData = Object.freeze(res.auditionAppointmentResps) || []
        })
        .catch((err) => {
          this.tableConfig.totalCount = 0
          this.$Message.error({ content: err.message, duration: 5 })
        })
        .finally(() => {
          this.tableLoading = false
        })
    }
  }
}
</script>

<style lang="less" scoped>
</style>
