<template>
  <!-- 攒班需求池 -->
  <div>
    <div
      class="flex"
      style="margin-bottom: 10px;"
    >
      <i-switch
        v-model="formData.enable"
        size="large"
      >
        <span slot="open">开</span>
        <span slot="close">关</span>
      </i-switch>
      <span style="margin: 0 10px;font-size: 16px;font-weight: bold;">快速聚类</span>
      <template v-if="formData.enable">
        <CheckboxGroup v-model="formData.checkbox">
          <Checkbox
            :label="item.value"
            v-for="(item, index) in options"
            :disabled="item.disabled"
            :key="index"
          >{{ item.label }}</Checkbox>
        </CheckboxGroup>
      </template>
    </div>
    <!-- 列表 -->
    <Table
      border
      ref="selection"
      :columns="columns"
      :data="tableData"
      @on-selection-change="onSelectionChange"
      :loading="tableLoading"
    ></Table>

    <!-- 分页 -->
    <div
      style="margin: 10px;"
      class="flex-page"
    >
      <Button
        :disabled="rowChecked.length === 0"
        @click="confirm"
      >提交开班评估</Button>
      <Page
        :total="searchData.totalCount"
        :page-size="searchData.pageSize"
        :current.sync="searchData.pageNo"
        show-elevator
        show-sizer
        transfer
        show-total
        @on-page-size-change="pageSizeChange"
        @on-change="changePage"
      ></Page>
    </div>

    <!-- 取消攒班 -->
    <Modal
      :mask-closable="false"
      title="取消攒班"
      v-model="cancelCuanban.showModal"
      :footer-hide="true"
      width="450px"
    >
      <Form>
        <FormItem
          required
          label="取消原因"
        >
          <Input
            style="width: 420px;"
            v-model="cancelCuanban.remark"
            type="textarea"
            :autosize="{minRows: 3,maxRows: 10}"
            placeholder="请输入原因"
          ></Input>
        </FormItem>
      </Form>
      <Row
        type="flex"
        justify="center"
        :gutter="20"
      >
        <Col>
        <Button
          type="primary"
          @click="confirmCancelCuanban"
          :loading="cancelCuanban.confirmLoading"
        >提交</Button>
        </Col>
        <Col>
        <Button
          @click="cancelCuanban.showModal = false"
          :loading="cancelCuanban.confirmLoading"
        >取消</Button>
        </Col>
      </Row>
    </Modal>
  </div>
</template>

<script>
import CustomerMixin from '@/components/mixin/customerDetail'
import { TalDecodeText } from '@peiyou/tal-element'
import { queryAssembleClassDemandPage, cancelAssembleClassDemand } from '@/api/assembleClass'
import cuanbanMixin from './cuanbanMixin'
export default {
  name: 'DemandPool',
  mixins: [CustomerMixin, cuanbanMixin],
  props: {
    parentFormData: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  data () {
    return {
      showOpenClassAssessApproval: false, // 开班评估审批
      formData: {
        enable: false,
        checkbox: ['year', 'termId', 'gradeId', 'subjectId', 'classLevel']
      },
      // 快速聚类可选项
      options: [
        { label: '同年份', value: 'year', disabled: true },
        { label: '同意向学期', value: 'termId', disabled: true },
        { label: '同年级', value: 'gradeId', disabled: true },
        { label: '同意向学科', value: 'subjectId', disabled: true },
        { label: '同班次级别', value: 'classLevel', disabled: true },
        { label: '同意向服务中心', value: 'serviceCenterId' },
        { label: '同意向上课时间', value: 'classtimeTimeClassify' },
        { label: '同意向上课时段', value: 'classtimeTimePeriods' },
        { label: '同授课类型', value: 'classType' }
      ],
      columns: [
        {
          type: 'selection',
          width: 60,
          align: 'center'
        },
        {
          title: '操作',
          key: 'name',
          align: 'center',
          render: (h, params) => {
            let isStartDisable = params.row.type === 1 ? true : params.row.isStart !== 1
            let isEndDisable = params.row.type === 1 ? true : params.row.isEnd === 1
            return h('div', [
              h('a', {
                on: {
                  click: () => {
                    this.$emit('checkCuanbanDemand', { id: params.row.id, cityCode: params.row.cityCode, uid: params.row.uid })
                  }
                }
              }, '查看'),
              h('br'),
              // params.row.isStart === 1 || params.row.type === 0 ?
              !isStartDisable
                ? h('a', {
                  on: {
                    click: () => {
                      this.$emit('updateCuanbanDemand', { id: params.row.id, cityCode: params.row.cityCode, uid: params.row.uid })
                    }
                  }
                }, '修改') : h('span', {
                  style: {
                    color: 'gray'
                  }
                }, '修改'),
              h('br'),
              // params.row.isEnd === 1 || params.row.type === 1 ?
              isEndDisable
                ? h('span', {
                  style: {
                    color: 'gray'
                  }
                }, '取消') : h('a', {
                  on: {
                    click: () => {
                      this.cancelCuanban.remark = ''
                      this.cancelCuanban.id = params.row.id
                      this.cancelCuanban.showModal = true
                    }
                  }
                }, '取消')

            ])
          }
        },
        {
          title: '攒班状态',
          key: 'status',
          align: 'center',
          render: (h, params) => {
            return h('span', this.cuanbanStatusMap[params.row.status])
          }
        },
        {
          title: '学员姓名',
          key: 'name',
          align: 'center',
          render: (h, params) => {
            return h('a', {
              on: {
                click: () => {
                  const stu = {
                    card: params.row.studentCard,
                    cityCode: params.row.cityCode
                  }
                  this.goCustomerDetail(stu)
                }
              }
            }, params.row.name || '无')
          }
        },
        {
          title: '联系电话',
          key: 'phone',
          align: 'center',
          width: 120,
          render: (h, params) => {
            return h(TalDecodeText, {
              props: {
                uid: params.row.uid,
                encodeText: params.row.phone,
                type: 'phone'
              }
            })
          }
        },
        {
          title: '接待顾问',
          key: 'receptionAdvisorEmpName',
          align: 'center'
        },
        {
          title: '创建人',
          key: 'creatorName',
          align: 'center',
          render: (h, params) => {
            const creatorName = params.row.creatorName || '用户'
            return h('span', creatorName)
          }
        },
        {
          title: '创建时间',
          key: 'createDate',
          align: 'center'
        },
        {
          title: '推荐学员',
          key: 'recommendName',
          align: 'center',
          render: (h, params) => {
            return params.row.recommendCard ? h('a', {
              on: {
                click: () => {
                  const stu = {
                    card: params.row.recommendCard,
                    cityCode: params.row.cityCode
                  }
                  this.goCustomerDetail(stu)
                }
              }
            }, params.row.recommendName || '--') : h('span', params.row.recommendName || '--')
          }
        },
        {
          title: '攒班年份',
          key: 'year',
          align: 'center'
        }, {
          title: '意向学期',
          key: 'termName',
          align: 'center'
        }, {
          title: '年级',
          key: 'gradeName',
          align: 'center'
        }, {
          title: '意向学科',
          key: 'subjectName',
          align: 'center'
        }, {
          title: '班次级别',
          key: 'classLevelName',
          align: 'center'
        }, {
          title: '授课类型',
          key: 'classTypeName',
          align: 'center'
        }, {
          title: '意向服务中心',
          key: 'serviceCenterName',
          align: 'center'
        }, {
          title: '意向上课时间',
          key: 'classtimeTimeClassifys',
          align: 'center'
        }, {
          title: '意向上课时段',
          key: 'classtimeTimePeriods',
          align: 'center'
        }, {
          title: '意向度',
          key: 'intention',
          align: 'center'
        }, {
          title: '入学测试成绩',
          key: 'diagnosisScore',
          align: 'center',
          render: (h, params) => {
            const text = params.row.diagnosisScore === 1 ? '符合' : '不符合'
            return h('span', text)
          }
        }, {
          title: '攒班班级ID',
          key: 'assembleClassId',
          align: 'center'
        }, {
          title: '攒班班级报名状态',
          key: 'registStatus',
          align: 'center',
          render: (h, params) => {
            return h('span', this.registStatusMap[params.row.registStatus])
          }
        }, {
          title: '攒班班级缴费状态',
          key: 'payStatus',
          align: 'center',
          render: (h, params) => {
            return h('span', this.payStatusMap[params.row.payStatus])
          }
        }, {
          title: '开班失败原因',
          key: 'failureReason',
          align: 'center'
        }
      ],
      tableLoading: false,
      tableData: [],
      rowChecked: [], // 已选中学员
      searchData: { // 快速聚合搜索项
        totalCount: 0,
        pageSize: 10,
        pageNo: 1,
        isAggregation: 0,
        aggregationField: ''
      },
      // 取消攒班
      cancelCuanban: {
        id: '', // 攒班id
        showModal: false, // 取消攒班
        remark: '', // 取消攒班备注
        confirmLoading: false // 提交中
      },
      classTypeList: [] // 授课类型列表
    }
  },
  watch: {
    // 监听快速聚合变更
    formData: {
      deep: true,
      handler: function (newValue) {
        let checkbox = ''
        if (newValue.enable) {
          checkbox = newValue.checkbox.join(',')
        }
        this.searchData = {
          isAggregation: newValue.enable ? 1 : 0,
          aggregationField: checkbox
        }
        this.search()
      }
    }
  },
  methods: {
    // 取消攒班
    async confirmCancelCuanban () {
      try {
        if (!this.cancelCuanban.remark) {
          return this.$Message.error({ content: `请输入取消攒班原因` })
        }
        const params = {
          assembleClassDemandId: this.cancelCuanban.id,
          remark: this.cancelCuanban.remark,
          cityCode: this.parentFormData.cityCode
        }
        this.cancelCuanban.confirmLoading = true
        await cancelAssembleClassDemand(params)
        this.$Message.success({ content: '取消攒班成功' })
        this.search()
        this.cancelCuanban.showModal = false
      } catch (error) {
        console.log('error', error)
        this.$Message.error({ content: `取消攒班失败:` + error.message, duration: 5 })
      }
      this.cancelCuanban.confirmLoading = false
    },
    // 提交开班评估
    confirm () {
      const sutdentCards = new Set()
      this.rowChecked.forEach(item => sutdentCards.add(item.studentCard))

      // 同一学员重复勾选校验
      if (sutdentCards.size !== this.rowChecked.length) {
        this.$Message.warning({ content: '不可重复选择同一个学员', duration: 5 })
        return
      }

      if (!this.notSameTermCheck(this.rowChecked)) {
        this.$emit('submitOpenClassAssessApproval', this.rowChecked)
      }
    },
    // 检测选择的学员是否同年份、学期、年级、学科、班次级别
    notSameTermCheck (data) {
      if (data.length > 1) {
        const firstSelected = data[0]
        const lastSelected = data[data.length - 1]
        console.log(firstSelected, lastSelected)
        if (firstSelected._sameTag !== lastSelected._sameTag) {
          this.$Message.warning({ content: `请选择年份、学期、年期、学科、班次级别相同学员！`, duration: 5 })
          return true
        }
      }
    },
    // 查询
    async search () {
      try {
        const params = Object.assign({}, this.parentFormData, this.searchData, {
          startCreateDate: this.parentFormData.startCreateDate ? this.parentFormData.startCreateDate + ' 00:00:00' : '',
          endCreateDate: this.parentFormData.endCreateDate ? this.parentFormData.endCreateDate + ' 23:59:59' : ''
        })
        if (params.classType === 1 || !params.classType) {
          delete params.serviceCenterId
        }
        this.tableLoading = true
        const res = await queryAssembleClassDemandPage(params)
        res.records.forEach(async item => {
          if (item.type === 1) {
            item._disabled = true
          } else {
            if (item.isStart !== 1) {
              item._disabled = true
            } else {
              item._disabled = false
            }
          }
          item._sameTag = `${item.year}-${item.termId}-${item.gradeId}-${item.subjectId}-${item.classLevel}`
        })
        const records = await this.setStudentLevel(res.records)
        this.tableData = Object.freeze(records)
        this.searchData.totalCount = res.totalCount
      } catch (error) {
        this.$Message.error({ content: `查询需求池失败: ${error.message}`, duration: 5 })
      }

      this.tableLoading = false
    },
    // 勾选变更
    onSelectionChange (data) {
      if (this.rowChecked.length === 30) {
        this.$Message.warning({ content: '最多勾选30个学员', duration: 5 })
      }
      this.notSameTermCheck(data)

      this.rowChecked = [...data]
    },
    // 分页
    changePage (num) {
      this.searchData.pageNo = num
      this.search()
    },
    pageSizeChange (size) {
      this.searchData.pageNo = 1
      this.searchData.pageSize = size
      this.search()
    }
  }
}
</script>

<style lang="less" scoped>
.flex {
  display: flex;
  align-items: center;
}

.flex-page {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/deep/ .ivu-table .orange td {
  background-color: #ddd;
}
</style>
