<template>
  <div class="count-edit">
    <div>
      <InputNumber :class="{'error': isError}" v-if="isEdit" style="width: 65px;" size="small" :max="max" :min="min" v-model="count" @on-change="$emit('input', count)"></InputNumber>
      <span v-else style="width: 50px;">{{ count || '-' }}</span>
    </div>
    <template v-if="isEditSelf">
      <div class="btns">
        <template v-if="isEdit">
          <Icon class="btn checkmark" style="margin-right: 2px;" type="ios-checkmark-circle" @click="save" />
          <Icon class="btn close" type="ios-close-circle" @click="close" />
        </template>
        <Icon class="btn" v-else type="ios-create-outline" @click="isEdit = true" />
      </div>
    </template>
  </div>
</template>

<script>
export default {
  props: {
    min: {
      type: Number,
      default: 0
    },
    max: {
      type: Number,
      default: 999999999
    },
    initCount: {
      type: Number,
      default: 0
    },
    edit: {
      type: Boolean,
      default: false
    },
    isEditSelf: { // 有组件自身控制状态
      type: Boolean,
      default: true
    }
  },
  watch: {
    edit () {
      this.isEdit = this.edit
    },
    initCount () {
      this.count = this.initCount
    }
  },
  computed: {
    isError () {
      return this.count > this.max || this.count < this.min
    }
  },
  data () {
    return {
      count: 100,
      isEdit: false
    }
  },
  mounted () {
    this.count = this.initCount
  },
  methods: {
    save () {
      if (this.count < this.min) {
        this.reset()
        return this.$Message.warning({ content: `最小值为${this.min}` })
      }

      if (this.count > this.max) {
        this.reset()
        return this.$Message.warning({ content: `最大值为${this.max}` })
      }

      this.$emit('save', this.count)
      this.isEdit = false
    },
    close () {
      this.reset()
    },
    reset () {
      this.count = this.initCount
      this.isEdit = false
    }
  }
}
</script>

<style lang="less" scoped>
.count-edit{
  display: flex;
  align-items: center;
  justify-content: flex-end;
  font-size: 14px;
}
.btns{
  display: flex;
  align-items: center;
  margin-left: 6px;
}

.btn{
  cursor: pointer;
  font-size: 20px;
}

.close{
  color: #DD6146;
}

.checkmark{
  color: #5ec134;
}

.error{
  border-color: red !important;
  box-shadow: #ffdfdc 0 0 0 2px;
}
</style>
