<template>
  <Modal
    v-model="schoolState"
    ok-text="确定"
    cancel-text="取消"
    title="学校管理"
    :mask-closable="false"
    :closable="true"
    @on-visible-change="handleEvents"
    :footer-hide="true"
    width="80%"
  >
    <div class="school-manage">
      <div class="school-manage-left">
          <div class="manege-left-row select-box">
            <Select v-model="schoolCode" :disabled="true" class="select-active" style="width:130px;margin-right: 10px;" placeholder="请选择城市" @on-change="handleGetDistrict">
              <Option v-for="item in cityList" :value="item.id" :key="item.id">{{item.name}}</Option>
            </Select>
            <Select v-model="districtId" style="width:130px;margin-right: 10px;" placeholder="请选择地区" @on-change="handleDistrict">
              <Option v-for="item in districtList" :value="item.id" :key="item.id">{{item.name}}</Option>
            </Select>
            <Select v-model="level" style="width:130px" placeholder="年部">
              <Option v-for="item in levelList" :value="item.id" :key="item.id">{{item.name}}</Option>
            </Select>
            <div class="select-icon">*</div>
          </div>
        <div class="manege-left-row">
          <Input v-model="school" @on-enter="handleSearch()" placeholder="请选择学校" style="width:200px;margin-right: 20px;" />
          <Button type="primary" @click="handleSearch()">搜索</Button>
        </div>
        <div class="schoolList-box">
          <Table
            ref="selection"
            @on-select="handleSelect"
            @on-select-cancel="handleCancel"
            @on-select-all-cancel="handleCancleAll"
            @on-select-all="handleSelectAll"
            no-data-text="请根据“城市、区/县、年部”筛选或按“关键词”查找学校”"
            :columns="columns"
            height="400"
            :data="schoolList"
          >
          </Table>
          <!--总共的数量-->
          <div class="schoolList-total">共筛选出 <span style="color:#2d8cf0">{{total}}</span> 条记录</div>
          <!--分页-->
          <div class="" style="margin-top:20px;text-align: center">
            <Page
              :total="total"
              show-sizer
              :page-size-opts="[10,20,40]"
              @on-page-size-change="handleSizeChange"
              @on-change="handleSearch"
            />
          </div>
        </div>
      </div>
      <div class="school-manage-right">
        <div style="padding: 10px">
          <p style="font-size: 14px;">已选 <span style="color:#2d8cf0">{{selectList.length}}</span> 个学校
            <Poptip
              confirm
              transfer
              title="您确定要清除已选学校吗？"
              style="margin-left: 120px;"
              @on-ok="clearAll"
            >
              <span style="color: deepskyblue;cursor: pointer">清除全部</span>
            </Poptip>
          </p>
        </div>
        <div class="mess">
          <div v-for="(item,index) in selectList" :key="index" class="target-list-item">
            <span class="ivu-tag-text">{{item.name}}</span>
            <Icon @click="handleClear(item,index)" type="md-close" style="color: #979797;margin-left: 2px"/>
          </div>
        </div>
      </div>
    </div>
    <div style="text-align: center;">
      <Button style="margin-right:20px" @click="handleManageCancle">取消</Button>
      <Button @click="handleManageOk" type="primary">确定</Button>
    </div>
  </Modal>

</template>

<script>
import {
  getCityList,
  getDistrict,
  getGrade,
  getPageList,
  getSchoolBackList,
  bindSchool
} from '@/api/operative'
export default {
  name: 'index',
  data () {
    return {
      cityId: '',
      cityList: [], // 分校数据
      districtId: this.propDistrictId, // 地区
      districtList: [], // 地区数据
      level: '', // 年部
      levelList: [], // 年部数据
      school: '', // 学校模糊查询
      schoolNum: 0, // 已选择学校数量
      selectList: [],
      current: 1, // 页码
      size: 10,
      total: 0, // 学校的数量
      columns: [
        {
          type: 'selection',
          width: 60,
          align: 'center'
        },
        {
          title: '学校',
          key: 'name',
          align: 'left'
        },
        {
          title: '区/县',
          key: 'districtName',
          align: 'center'
        },
        {
          title: '年部',
          key: 'levelName',
          align: 'center'
        },
        {
          title: ` `,
          key: 'provinceName',
          align: 'center'
        }
      ],
      schoolList: [],
      schoolCode: this.propSchoolCode

    }
  },
  props: ['deptId', 'propDistrictId', 'propSchoolCode'],
  methods: {
    handleEvents: function (state) {
      if (state) {
        this.districtId = this.propDistrictId
        this.schoolCode = this.propSchoolCode
        this.handleCityList()// 获取分校
        this.handleSchoolBackList()// 学校回显
      }
    },
    // 点击确定
    handleManageOk: function () {
      if (this.selectList.length === 0) {
        this.$Message.warning('请先选择学校')
        return
      }
      let schoolIds = []
      this.selectList.forEach(item => {
        schoolIds.push(item.id)
      })
      let payloads = {
        deptId: this.deptId,
        schoolIds: schoolIds
      }
      bindSchool(payloads).then(res => {
        if (res.rlt !== true) return
        this.schoolList = []
        this.$emit('handleEimtSchoolList')
        this.$store.commit('service/HANDLE_SCHOOLMANAGE', false)
      }).catch((err) => {
        this.$Message.warning({ content: '绑定学校失败:' + err.message, duration: 5 })
      })
      this.schoolList = []
    },
    // 点击取消
    handleManageCancle: function () {
      this.schoolList = []
      this.$store.commit('service/HANDLE_SCHOOLMANAGE', false)
    },
    // 获取分校回显
    handleSchoolBackList () {
      getSchoolBackList({ schoolCode: this.schoolCode, deptId: this.deptId }).then(res => {
        if (res.rlt !== true || !res.data) return
        this.selectList = res.data
      }).catch((err) => {
        this.$Message.warning({ content: '获取学校数据失败:' + err.message, duration: 5 })
      })
    },
    // 获取分校
    handleCityList () {
      getCityList().then(res => {
        if (res.rlt !== true || !res.data) return
        this.cityId = res.data.find(item => item.id === this.schoolCode).cityId
        this.cityList = res.data
        this.handleGetDistrict(this.cityId, true)
        this.handleGetCityList(this.schoolCode)// 获取年部
      }).catch((err) => {
        this.$Message.warning({ content: '获取分校失败:' + err.message, duration: 5 })
      })
    },
    // 获取地区
    handleGetDistrict (value, mark) {
      let cityId
      if (mark) {
        cityId = value
      } else {
        let cityItem = this.cityList.find(item => item.id === value)
        cityId = cityItem && cityItem.cityId
        this.cityId = cityId
        this.handleGetCityList(value)// 获取年部
      }
      getDistrict({ cityId: cityId }).then(res => {
        if (res.rlt !== true || !res.data) return
        this.districtList = res.data
      }).catch((err) => {
        this.$Message.warning({ content: '获取地区失败:' + err.message, duration: 5 })
      })
    },
    // 改变地区
    handleDistrict: function (value) {
      this.districtId = value
    },
    // 获取年部
    handleGetCityList (schoolCode) {
      getGrade({ schoolCode: schoolCode, status: 0 }).then(res => {
        if (res.rlt !== true || !res.data) return
        this.level = res.data[0].id
        this.levelList = res.data
      }).catch((err) => {
        this.$Message.warning({ content: '获取年部失败:' + err.message, duration: 5 })
      })
    },
    // 分页查询学校
    handleSearch: function (current, size) {
      if (!current) {
        this.current = 1
        this.size = 10
        this.total = 0
      }
      let payloads = {
        schoolCode: this.schoolCode,
        cityId: this.cityId,
        districtId: this.districtId,
        level: this.level,
        likeName: this.school,
        status: 0,
        current: current || this.current,
        size: size || this.size
      }
      getPageList(payloads).then(res => {
        if (res.rlt !== true || !res.data) return
        this.schoolList = res.data && res.data.records
        this.schoolList.forEach(item => {
          item.districtName = item.district.name
          item.levelName = this.levelList.find(each => each.id === item.level).name
        })
        this.total = res.data.total
        // 遍历对勾
        if (this.selectList.length > 0) {
          this.selectList.forEach(item => {
            this.schoolList.forEach(each => {
              // eslint-disable-next-line no-unused-expressions
              each.id === item.id ? each._checked = true : false
            })
          })
        }
      }).catch((err) => {
        this.$Message.warning({ content: '分页查询学校失败:' + err.message, duration: 5 })
      })
    },
    // 单选
    handleSelect (selection, row) {
      this.schoolList.find(item => item.id === row.id)._checked = true
      this.selectList.push(row)
    },
    // 取消单选
    handleCancel (selection, row) {
      this.schoolList.find(item => item.id === row.id)._checked = false
      this.selectList = this.selectList.filter(item => item.id !== row.id)
    },
    // 全选
    handleSelectAll: function (selection) {
      this.selectList.forEach(item => {
        selection.forEach(each => {
          if (item.id === each.id) {
            each.hasChoose = true
          }
        })
      })
      this.selectList = this.selectList.concat(selection.filter(item => item.hasChoose !== true))
      this.schoolList.forEach(item => {
        item._checked = true
      })
    },
    // 取消全选
    handleCancleAll: function (selection) {
      this.schoolList = this.schoolList.slice()
      this.schoolList.forEach(item => {
        item._checked = false
      })
      this.selectList.forEach(item => {
        this.schoolList.forEach(each => {
          if (item.id === each.id) {
            item.leave = true
          }
        })
      })
      this.selectList = this.selectList.filter(item => !item.leave)
    },
    // 单个删除已选择学校
    handleClear: function (row, index) {
      this.schoolList = this.schoolList.slice()
      let thisItem = this.schoolList.find(item => item.id === row.id)
      if (thisItem) {
        thisItem._checked = false
      }
      this.selectList = this.selectList.filter(item => item.id !== row.id)
    },
    clearAll: function () {
      this.selectList = []
      this.schoolList = this.schoolList.slice()
      this.schoolList.forEach(item => {
        item._checked = false
      })
    },
    handleSizeChange: function (size) {
      this.size = size
      this.handleSearch(this.current, size)
    }
  },
  computed: {
    schoolState: {
      get () {
        return this.$store.state.service.schoolState
      },
      set (val) {
        this.$store.state.service.schoolState = val
      }
    }
  }
}
</script>

<style lang="less" scoped>
  .school-manage{

  }
  /deep/ .school-manage-left{
    width: 65%;
    display: inline-block;
    .manege-left-row{
      margin-bottom:20px;
    }
  }
  .school-manage-right{
    width: calc( 35% - 40px );
    margin-left: 40px;
    float: right;
    display: inline-block;
  }
  .schoolList-box{
    position: relative;
    .schoolList-total{
      position: absolute;;
      right: 10px;
      top:10px;
    }
  }
  .mess {
    padding: 5px 5px 5px 5px;
    height: 400px;
    overflow: auto;
    border: 1px solid gainsboro;
    margin-left: 2px;
    background: #F7F7F7;
  }
  .cess {
    padding: 5px 5px 5px 5px;
    margin-left: 2px
  }
  .target-list-item {
    display: inline-block;
    height: 30px;
    line-height: 30px;
    margin: 4px 8px 4px 0;
    padding: 0 8px;
    border: 1px solid #e8eaec;
    border-radius: 3px;
    background: #ffffff;
    font-size: 12px;
    vertical-align: middle;
    opacity: 1;
    overflow: hidden;
    cursor: pointer;
  }
  .select-box{
    position: relative;
    .select-active{
      .ivu-select-selection{
        padding-left: 10px;
      }
    }
    .select-icon{
      color: red;
      position: absolute;
      left: 8px;
      top: 8px;
      font-size: 16px;
    }
  }
</style>
