<template>
  <!-- 新建攒班需求 -->
  <div style="width: 100%;padding: 15px;">
    <!-- type: 1 新增攒班需求  -->
    <div
      class="stu-search"
      style="margin-bottom: 30px;"
      v-if="type === 1"
    >
      <TalStuSearch
        ref="stuSearch"
        :stu-card="queryData.card"
        :stu-city-code="queryData.cityCode"
        :showCrmInfo="false"
        @stuInfo="setStuInfo"
      ></TalStuSearch>
    </div>

    <p v-if="!formData.uid">请选择学员</p>
    <template v-if="formData.uid && (type === 1 || type == 2)">
      <Form
        inline
        :label-width="130"
        ref="formValidate"
        :model="formData"
        :rules="ruleValidate"
        style="margin-bottom: 20px;"
      >
        <FormItem
          label="年份"
          prop="year"
        >
          <Select
            v-model="formData.year"
            style="width: 150px;"
            clearable
          >
            <Option
              v-for="(item,index) in yearList.slice(1, -1)"
              :value="item"
              :key="index"
            >{{ item }}</Option>
          </Select>
        </FormItem>
        <FormItem
          label="意向学期"
          prop="termId"
        >
          <Select
            v-model="formData.termId"
            style="width: 150px;"
            clearable
            label-in-value
            @on-change="handleSelectChange($event, 'termName')"
          >
            <Option
              v-for="(item,index) in termList.slice(0, 4)"
              :value="item.standardId"
              :key="index"
            >{{ item.standardName }}</Option>
          </Select>
        </FormItem>
        <FormItem
          label="年级"
          prop="gradeId"
        >
          <Select
            v-model="formData.gradeId"
            style="width: 150px;"
            clearable
            label-in-value
            @on-change="handleSelectChange($event, 'gradeName')"
          >
            <Option
              v-for="(item,index) in gradeList"
              :value="item.standardId"
              :key="index"
            >{{ item.standardName }}</Option>
          </Select>
        </FormItem>
        <FormItem
          label="意向学科"
          prop="subjectId"
        >
          <Select
            v-model="formData.subjectId"
            style="width: 150px;"
            clearable
            label-in-value
            @on-change="handleSelectChange($event, 'subjectName')"
          >
            <Option
              v-for="(item,index) in subjectList"
              :value="item.schoolId"
              :key="index"
            >{{ item.customName }}</Option>
          </Select>
        </FormItem>
        <FormItem
          label="班次级别"
          prop="classLevel"
        >
          <Select
            v-model="formData.classLevel"
            style="width: 150px;"
            clearable
            label-in-value
            @on-change="handleSelectChange($event, 'classLevelName')"
          >
            <Option
              v-for="(item,index) in classLevelList"
              :value="item.schoolId"
              :key="index"
            >{{ item.name }}</Option>
          </Select>
        </FormItem>
        <FormItem>
          <p style="margin-left: -100px;position: relative;height: 30px;">
            <Spin
              size="large"
              fix
              v-if="diagnosisScore.spining === 1"
            ></Spin>
            <span v-if="diagnosisScore.spining === 2 && !diagnosisScore.value">入学测试成绩不符合，请及时联系学员进行诊断</span>
          </p>
        </FormItem>
        <FormItem
          label="授课类型"
          prop="classType"
        >
          <Select
            v-model="formData.classType"
            style="width: 150px;"
          >
            <Option
                v-for="item in classTypeList"
                :value="item.id"
                :key="item.id"
                :disabled="!$haveAuthority('roleRelativeData',classTypeRuleMap[item.id])"
              >{{ item.name }}</Option>
          </Select>
        </FormItem>
        <template v-if="formData.classType===2||formData.classType===4">
          <FormItem
            label="意向服务中心"
            prop="serviceCenterId"
          >
            <Select
              v-model="formData.serviceCenterId"
              style="width: 150px;"
              clearable
              filterable
              :remote-method="q => getServiceCenterList(q, formData.cityCode)"
              label-in-value
              @on-change="handleSelectChange($event, 'serviceCenterName')"
            >
              <Option
                v-for="(item,index) in serviceCenterList"
                :value="item.schoolId"
                :key="index"
              >{{ item.name }}</Option>
            </Select>
          </FormItem>
        </template>
        <FormItem
          label="意向上课时间"
          prop="classtimeTimeClassifys"
        >
          <Select
            v-model="formData.classtimeTimeClassifys"
            style="width: 150px;"
            clearable
            multiple
          >
            <Option
              v-for="(item,index) in timeTypeList"
              :value="item.name"
              :key="index"
            >{{ item.name }}</Option>
          </Select>
        </FormItem>
        <FormItem
          label="意向上课时段"
          prop="classtimeTimePeriods"
        >
          <Select
            v-model="formData.classtimeTimePeriods"
            style="width: 150px;"
            clearable
            multiple
          >
            <Option
              v-for="(item,index) in timeList"
              :value="item.name"
              :key="index"
            >{{ item.name }}</Option>
          </Select>
        </FormItem>
        <FormItem
          label="意向度"
          prop="intention"
        >
          <Select
            v-model="formData.intention"
            style="width: 150px;"
            clearable
          >
            <Option
              v-for="(item,index) in intentionList"
              :value="item"
              :key="index"
            >{{ item }}</Option>
          </Select>
        </FormItem>
        <FormItem
          label="推荐学员"
          prop="recommendName"
        >
          <Input
            v-model="formData.recommendName"
            placeholder="请输入"
            :maxlength="10"
            clearable
          />
        </FormItem>
        <FormItem
          label="推荐学员编号"
          prop="recommendCard"
        >
          <Input
            v-model="formData.recommendCard"
            placeholder="请输入"
            clearable
            :maxlength="20"
          />
        </FormItem>
        <br>
        <FormItem
          label="备注"
          prop="remark"
        >
          <Input
            style="width: 740px;"
            v-model="formData.remark"
            type="textarea"
            :autosize="{minRows: 3,maxRows: 10}"
            placeholder="请输入备注"
            :maxlength="100"
          ></Input>
        </FormItem>
      </Form>
      <Row
        type="flex"
        justify="center"
        :gutter="20"
      >
        <Col>
        <Button
          type="primary"
          @click="confirm"
          :loading="confirmLoading"
        >提交</Button>
        </Col>
        <Col>
        <Button
          @click="$emit('close')"
          :loading="confirmLoading"
        >取消</Button>
        </Col>
      </Row>
    </template>

    <!-- type: 2 修改攒班需求 -->
    <template v-if="type === 2 || type === 3">
      <h3 style="margin-bottom: 20px;">修改记录</h3>
      <div class="record">
        <Row
          v-for="(item, index) in updateRecords"
          :key="index"
        >
          <Col span="24">操作类型: {{ item.logTypeName }}</Col>
          <Col span="6">年份: {{ item.year }}</Col>
          <Col span="6">意向学期: {{ item.termName}}</Col>
          <Col span="6">年级: {{ item.gradeName}}</Col>
          <Col span="6">意向学科: {{ item.subjectName }}</Col>
          <Col span="6">班次级别: {{ item.classLevelName }}</Col>
          <Col span="6">授课类型: {{ item.classTypeName }}</Col>
          <Col span="6">意向服务中心: {{ item.serviceCenterName }}</Col>
          <Col span="6">意向上课时间: {{ item.classtimeTimeClassifys }}</Col>
          <Col span="6">意向上课时段: {{ item.classtimeTimePeriods }}</Col>
          <Col span="6">意向度: {{ item.intention }}</Col>
          <Col span="6">推荐学员: {{ item.recommendName }}</Col>
          <Col span="6">推荐学员编号: {{ item.recommendCard }}</Col>
          <Col span="24">备注: {{ item.remark }}</Col>
          <Col span="6">操作人: {{ item.creatorName }}</Col>
          <Col span="6">操作时间: {{ item.createDate }}</Col>

          <Col span="24">
          <Divider style="margin: 10px 0;background-color: #333;"></Divider>
          </Col>
        </Row>
      </div>
    </template>

  </div>

</template>

<script>
import getUserInfoByUid from '@/components/mixin/getUserInfoByUid'
import baseDataSearchMixin from './baseDataSearchMixin'
import cuanbanMixin from './cuanbanMixin'
import { addAssembleClassDemand, editAssembleClassDemand, getAssembleClassDemandInfoById } from '@/api/assembleClass'
import { abilityStudentLevel } from '@/api/student'
export default {
  name: 'createCuanban',
  mixins: [getUserInfoByUid, baseDataSearchMixin, cuanbanMixin],
  props: {
    type: { // 1 新增 2修改 3查看
      type: Number,
      default: 1
    },
    oldFormData: { // 旧表单数据
      type: Object,
      default: () => {
        return {}
      }
    },
    classTypeList: { // 授课类型列表
      type: Array,
      default: () => {
        return []
      }
    },
    classTypeRuleMap: { // 权限map
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  watch: {
    'formData.classLevel': async function () {
      if (this.formData.classLevel && this.formData.gradeId && this.formData.subjectId) {
        const params = {
          levelDegree: this.formData.classLevel,
          subjectId: this.formData.subjectId,
          gradeId: this.formData.gradeId,
          uid: this.formData.uid,
          cityCode: this.formData.cityCode
        }
        this.diagnosisScore.spining = 1
        const data = await this.abilityStudentLevel(params)
        this.diagnosisScore.value = data
        this.diagnosisScore.spining = 2
      }
    }
  },
  data () {
    return {
      formData: {
        cityCode: '',
        year: new Date().getFullYear() + '', // 必填, // 年份
        termId: '', // 学期
        termName: '', // 学期
        gradeId: '', // 年级
        gradeName: '', // 年级
        subjectId: '', // 学科
        subjectName: '', // 学科
        classLevel: '', // 班次级别
        classLevelName: '', // 班次级别
        serviceCenterId: '', // 服务中心
        serviceCenterName: '', // 服务中心
        classtimeTimeClassifys: '', // 上课时间
        classtimeTimePeriods: '', // 上课时段
        intention: '', // 意向度
        name: '', // 学员姓名
        uid: '', // 学员uid
        studentCard: '', //  学员编号
        remark: '', // 备注
        recommendName: '', // 推荐学员
        recommendCard: '', // 推荐学员编号
        classType: 1// 授课类型
      },
      ruleValidate: {
        year: [
          { required: true, message: '请选择年份', trigger: 'change' }
        ],
        termId: [
          { required: true, message: '请选择学期', trigger: 'change' }
        ],
        gradeId: [
          { required: true, type: 'number', message: '请选择年级', trigger: 'change' }
        ],
        subjectId: [
          { required: true, message: '请选择意向学科', trigger: 'change' }
        ],
        classLevel: [
          { required: true, message: '请选择班次级别', trigger: 'change' }
        ],
        serviceCenterId: [
          { required: true, message: '请选择意向服务中心', trigger: 'change' }
        ],
        classtimeTimeClassifys: [
          { required: true, type: 'array', message: '请选择意向服务中心', trigger: 'change' }
        ],
        classtimeTimePeriods: [
          { required: true, type: 'array', message: '请选择意向服务中心', trigger: 'change' }
        ],
        classType: [
          { required: true, type: 'number', message: '请选择授课类型', trigger: 'change' }
        ]
      },
      confirmLoading: false,
      updateRecords: [], // 修改日志
      diagnosisScore: {
        spining: 0,
        value: null
      }
    }
  },
  mounted () {
    if (this.type === 2 || this.type === 3) {
      Object.assign(this.formData, this.oldFormData)
      this.getAssembleClassDemandInfoById()
    }
    if (this.formData.cityCode) {
      this.initFormOptions()
    }
  },
  methods: {
    // 获取学员诊断成绩
    async abilityStudentLevel (params) {
      try {
        const data = await abilityStudentLevel(params)
        return data
      } catch (error) {
        this.$Message.error({ content: `获取学员成绩失败: ${error.message}`, duration: 5 })
      }
    },
    // 根据id获取需求详情
    async getAssembleClassDemandInfoById () {
      try {
        const params = {
          assembleClassDemandId: this.oldFormData.id,
          cityCode: this.oldFormData.cityCode
        }
        const res = await getAssembleClassDemandInfoById(params)
        let updateRecords = Object.freeze(res.logList)
        delete res.logList
        this.formData = Object.assign({}, res, {
          classtimeTimePeriods: res.classtimeTimePeriods.split(','),
          classtimeTimeClassifys: res.classtimeTimeClassifys.split(',')
        })
        this.serviceCenterList = [
          { schoolId: res.serviceCenterId, name: res.serviceCenterName }
        ]
        this.updateRecords = updateRecords
      } catch (error) {
        this.$Message.error({ content: `攒班需求详情获取失败: ${error.message}`, duration: 5 })
      }
    },
    initFormOptions () {
      this.getBaseDataTermList()
      this.getYears()
      this.getSubjectList()
      this.getGradeList()
      this.getTimeList()
      this.getTimeTypeList()
      this.queryLevelDegreeList()
    },
    confirm () {
      this.$refs.formValidate.validate((valid, ...rest) => {
        if (valid) {
          if (this.type === 1) this.addAssembleClassDemand()
          else this.editAssembleClassDemand()
        }
      })
    },
    // 新增攒班需求
    async addAssembleClassDemand () {
      try {
        this.confirmLoading = true
        const params = Object.assign({}, this.formData, {
          classtimeTimePeriods: this.formData.classtimeTimePeriods.join(','),
          classtimeTimeClassifys: this.formData.classtimeTimeClassifys.join(',')
        })
        if (params.classType === 1 || !params.classType) {
          delete params.serviceCenterId
        }
        await addAssembleClassDemand(params)
        setTimeout(() => {
          this.$Message.success({ content: '新增攒班需求成功' })
          this.confirmLoading = false
          this.$emit('close')
          this.$emit('success')
        }, 2000)
      } catch (error) {
        this.confirmLoading = false
        this.$Message.error({ content: `新增攒班需求失败: ${error.message}`, duration: 5 })
      }
    },
    // 修改攒班需求
    async editAssembleClassDemand () {
      try {
        this.confirmLoading = true
        const params = Object.assign({}, this.formData, {
          classtimeTimePeriods: this.formData.classtimeTimePeriods.join(','),
          classtimeTimeClassifys: this.formData.classtimeTimeClassifys.join(',')
        })
        if (params.classType === 1 || !params.classType) {
          delete params.serviceCenterId
        }
        await editAssembleClassDemand(params)
        setTimeout(() => {
          this.$Message.success({ content: '修改攒班需求成功' })
          this.confirmLoading = false
          this.$emit('close')
          this.$emit('success')
        }, 2000)
      } catch (error) {
        this.confirmLoading = false
        this.$Message.error({ content: `新增攒班需求失败: ${error.message}`, duration: 5 })
      }
    },
    setStuInfo (stuInfo) {
      this.formData.cityCode = stuInfo.cityCode
      this.formData.uid = stuInfo.uid
      this.formData.name = stuInfo.realName
      this.formData.studentCard = stuInfo.card
      this.formData.gradeId = stuInfo.gradeId
      this.formData.gradeName = stuInfo.gradeName
      this.initFormOptions()
    }
  }
}
</script>
<style lang="less" scoped>
/deep/ .record .ivu-row .ivu-col {
  margin: 5px 0;
}
#TalScoreRecording {
  overflow-x: hidden;
  padding: 10px;
}

.stu-search {
  /deep/ .ivu-col {
    white-space: nowrap;
  }
}
</style>
