              key: 'paymentTargetNum',
              align: 'right',
              minWidth: 100,
              render: (h, params) => {
                return h(CountEdit, {
                  props: {
                    max: 9999,
                    min: 0,
                    initCount:
                      params.row.trecruitmentTargetList[3].paymentTargetNum,
                    edit: params.row.trecruitmentTargetList[3].isEdit,
                    isEditSelf: false
                  },
                  on: {
                    input: (count) => {
                      params.row.trecruitmentTargetList[3].paymentTargetNum =
                        count
                    }
                  }
                })
              }
            },
            {
              title: '操作',
              key: 'addWechatNum',
              align: 'right',
              width: 120,
              render: (h, params) => {
                return h(EditButton, {
                  props: {
                    edit: params.row.trecruitmentTargetList[3].isEdit,
                    loading: params.row.trecruitmentTargetList[3].isLoading
                  },
                  on: {
                    edit: () => {
                      this.edit(params.row.trecruitmentTargetList[3])
                    },
                    cancel: () => {
                      this.cancel(params.row.trecruitmentTargetList[3])
                    },
                    save: async () => {
                      this.saveData(params.row.trecruitmentTargetList[3])
                    }
                  }
                })
              }
            }
          ]
        }
      ],
      tableData: [],
      tableConfig: {
        totalCount: 100,
        pageSize: 10,
        pageNo: 1,
        tableLoading: false
      },
      serviceCenterList: [], // 服务中心列表
      advisorEmpNos: [], // 顾问列表
      formData: {
        serviceCenterId: null,
        empNo: null
      }
    }
  },
  mounted () {
    if (this.type === 1) {
      this.columns = this.columns1
    } else {
      this.columns = this.columns2
    }
    this.initData()
  },
  methods: {
    // 初始化筛选数据
    async initData () {
      if (this.cityCode && this.roleId) {
        if (this.roleId === 2) {
          await this.queryDeptByAreaCode()
        }

        if (this.type === 2) {
          if (this.roleId === 2) {
            this.getAdvisorEmpNos()
          } else if (this.roleId === 4) {
            this.getAdvisorEmpNos(this.userInfo.crmDeptId)
          }
        }

        this.getTableData()
      }
    },
    edit (data) {
      this.$set(data, 'isEdit', true)
    },
    cancel (data) {
      this.$set(data, 'isEdit', false)
      this.resetData(data)
    },
    // 保存成功设置备份数据
    async saveData (data) {
      if (this.type === 1) {
        XesAnalyticsTrack('15-5014', {
          servicecenter_id: data.serviceCenterId,
          Service_Centre: data.serviceCenterName,
          month: data.month,
          aimType: '--'
        })
      } else if (this.type === 2) {
        XesAnalyticsTrack('15-5018', {
          servicecenter_id: data.serviceCenterId,
          Service_Centre: data.serviceCenterName,
          month: data.month,
          aimType: '--'
        })
      }

      try {
        const params = { ...data }
        this.$set(data, 'isLoading', true)
        this.$set(data, 'isEdit', true)
        const id = await edit(params)
        data.id = id
        data.newRecruitsNumBefore = data.newRecruitsNum
        data.convertProductNumBefore = data.convertProductNum
        data.addWechatNumBefore = data.addWechatNum
        data.paymentTargetNumBefore = data.paymentTargetNum
        this.$set(data, 'isEdit', false)
        this.$Message.success({ content: `设置成功` })
      } catch (error) {
        // this.resetData(data)
        this.$set(data, 'isEdit', true)
        this.$Message.error({
          content: `编辑数据失败: ${error.message}`,
          duration: 5
        })
      }
      this.$set(data, 'isLoading', false)
    },
    // 保存数据失败后恢复原始数据
    resetData (data) {
      data.newRecruitsNum = data.newRecruitsNumBefore
      data.convertProductNum = data.convertProductNumBefore
      data.addWechatNum = data.addWechatNumBefore
      data.paymentTargetNum = data.paymentTargetNumBefore
    },
    // 获取顾问
    getAdvisorEmpNos (deptId) {
      getUsersLevelInfo({
        areaCode: this.cityCode,
        roleId: 5,
        deptId
      })
        .then((data) => {
          this.advisorEmpNos = data.filter((item) => item.empNo)
        })
        .catch((err) => {
          this.$Message.error({
            content: '查询顾问级别列表失败:' + err.message,
            duration: 5
          })
        })
    },
    // 获取服务中心
    queryDeptByAreaCode () {
      return getDeptByAreaCode({
        areaCode: this.cityCode,
        isActive: 1 // 1 有效服务中心 0 全部
      })
        .then((data) => {
          this.serviceCenterList = Object.freeze(data)
          // if (this.serviceCenterList && this.serviceCenterList[0]) {
          //   if (this.type === 2) {
          //     this.formData.serviceCenterId = this.serviceCenterList[0].id
          //     this.$emit('advsior-deptid-change', this.serviceCenterList[0])
          //   }
          // }
        })
        .catch((err) => {
          this.$message.error('查询服务中心列表错误：' + err.message)
        })
    },
    // 切换顾问
    handleChangeAdvisor () {
      this.tableConfig.pageSize = 10
      this.tableConfig.pageNo = 1
      this.getTableData()
    },
    // 切换服务中心
    handleServiceCenterChange (data) {
      if (this.type === 2) {
        // 顾问目标
        let obj = {}
        if (data) {
          obj = this.serviceCenterList.filter((item) => item.id === data)[0]
        }
        this.$emit('advsior-deptid-change', obj)
        if (this.roleId === 2) {
          this.formData.empNo = ''
          this.getAdvisorEmpNos(obj.id)
        }
      }
      this.tableConfig.pageSize = 10
      this.tableConfig.pageNo = 1
      this.getTableData()
    },
    changePage (num) {
      this.tableConfig.pageNo = num
      this.getTableData()
    },
    pageSizeChange (size) {
      this.tableConfig.pageNo = 1
      this.tableConfig.pageSize = size
      this.getTableData()
    },
    async getTableData () {
      try {
        // 服务中心主管传当前服务中心id
        // 城市经理需默认传第一个服务中心
        const deptId =
          this.roleId === 4
            ? this.userInfo.crmDeptId
            : this.formData.serviceCenterId
        const params = {
          areaCode: this.cityCode,
          type: this.type,
          deptId: deptId,
          ...this.tableConfig,
          identityEmpNo: this.type === 2 ? this.formData.empNo : null
        }
        this.tableConfig.tableLoading = true
        const data = await getTargetByPage(params)
        const date = new Date()
        const year = date.getFullYear()
        const month = date.getMonth() + 1
        let beforeMonth = month - 1
        if (beforeMonth < 1) {
          beforeMonth = 12
        }
        let nextMonth = month + 1
        if (nextMonth > 12) nextMonth = 1
        let nextNextMonth = nextMonth + 1
        if (nextNextMonth > 12) nextNextMonth = 1

        this.columns[4].title = `${year}年${beforeMonth}月`
        this.columns[5].title = `${year}年${month}月`
        this.columns[6].title = `${year}年${nextMonth}月`
        this.columns[7].title = `${year}年${nextNextMonth}月`
        data.records.forEach((item) => {
          item.trecruitmentTargetList.forEach((item) => {
            item.isEdit = false
            item.isLoading = false
            item.newRecruitsNumBefore = item.newRecruitsNum
            item.convertProductNumBefore = item.convertProductNum
            item.addWechatNumBefore = item.addWechatNum
            item.paymentTargetNumBefore = item.paymentTargetNum
          })
        })
        this.tableData = data.records
        this.tableConfig.totalCount = data.totalCount
      } catch (error) {
        this.$Message.error({
          content: `获取目标失败: ${error.message}`,
          duration: 5
        })
      }
      this.tableConfig.tableLoading = false
    }
  }
}
</script>

