<template>
  <div style="width: 100%;padding: 15px">
    <Form ref="form" :model="formData" label-position="right" :label-width="100" inline>
      <FormItem label="城市：" required>
        <!--:disabled="allCityList.length === 1"-->
        <Select
          v-model="formData.cityCode"
          filterable
          style="width: 120px;"
          @on-change="cityChange">
          <Option v-for="(option,index) in allCityList" :value="option.id" :key="index">{{option.name}}</Option>
        </Select>
      </FormItem>
      <FormItem label="区域：" required>
        <Select  v-model="formData.areaId" @on-change="changeArea" style="width:120px" filterable>
          <Option v-for="item in areaList" :value="item.schoolId" :key="item.schoolId">{{ item.name }}</Option>
        </Select>
      </FormItem>
      <FormItem label="服务中心：" required>
        <Select v-model="formData.serviceCenterId" @on-change="changeSC" style="width:120px" filterable>
          <Option v-for="(item,index) in serviceCenterList" :value="item.schoolId" :key="index">{{ item.name }}</Option>
        </Select>
      </FormItem>
      <FormItem>
        <span slot="label" class='blod-font'>综合评分：</span>
        <el-rate
          v-model="baseInfo.star"
          class="service-rate"
          disabled>
        </el-rate>
        <!--<Rate allow-half disabled v-model="baseInfo.star" />-->
      </FormItem>
      <FormItem>
        <span slot="label" class='blod-font'>评价数：</span>
        <span class='blod-font'>
          {{baseInfo.size || '--'}}
        </span>
      </FormItem>
      <FormItem>
        <span slot="label" class='blod-font' >匿名率：</span>
        <span class='blod-font'>
          {{ baseInfo.anonymityRate ? (baseInfo.anonymityRate * 100).toFixed(0) + '%' : '--' }}
        </span>
      </FormItem>
    </Form>
    <div class="tag-wrapper">
      <div class="tag" v-for="item in baseInfo.tag" :key="item.id">{{item.desc + ' ' + item.count}}</div>
    </div>
    <Form ref="form" :model="formData" label-position="right" :label-width="100" inline style="padding-top: 18px">
      <FormItem label="评分：">
        <!--:disabled="allCityList.length === 1"-->
        <Select
          v-model="formData.star"
          clearable
          style="width: 120px;">
          <Option v-for="(s,index) in scoreList" :value="s" :key="index">{{ s + '星'}}</Option>
        </Select>
      </FormItem>
      <FormItem label="有无文字：">
        <Select  v-model="formData.hasContent" style="width:120px" clearable>
          <Option :value="1">有文字</Option>
          <Option :value="0">无文字</Option>
        </Select>
      </FormItem>
      <FormItem label="创建时间：">
        <DatePicker :editable="false" v-model="formData.startDateVal" @on-change="startDateChange" type="date" style="width:120px"></DatePicker>
        &nbsp;&nbsp;至&nbsp;&nbsp;
        <DatePicker :editable="false" v-model="formData.endDateVal" @on-change="endDateChange" type="date" style="width:120px"></DatePicker>
      </FormItem>
      <FormItem>
        <Button type="primary" style="margin-right: 10px" @click="search" :loading="tableLoading">查询</Button>
        <Button @click="reset">重置</Button>
      </FormItem>
    </Form>
    <Table :columns="cols" :data="tableData" :loading="tableLoading" @on-expand="tableExpand">
    </Table>
    <div style="margin: 10px;" :class="{ 'page-fix': tableData.length < 3 }">
      <div style="float: right;">
        <Page
          :total="totalCount"
          :page-size="currentPageSize"
          :current="currentPageNum"
          show-elevator
          show-sizer
          show-total
          @on-page-size-change="pageSizeChange"
          @on-change="changePage"></Page>
      </div>
    </div>
  </div>
</template>

<script>
import { depClone } from '@/utils/utils'
import BaseDataMixin from '@/mixins/baseData'
import { Rate } from 'element-ui'
import expandRow from './table-expand'
import {
  queryServiceCenterByEmpNo
} from '@/api/clazz'
import {
  getAggregateInfo,
  getEvaluateList
} from '@/api/serviceCenter'
import { setFrom } from '@/api/bd'
export default {
  name: 'serviceCenterEvaluation',
  mixins: [BaseDataMixin],
  components: {
    expandRow
  },
  data () {
    return {
      formData: {
        cityCode: '',
        areaId: '',
        serviceCenterId: '',
        star: '',
        hasContent: '',
        startTime: '',
        startDateVal: '',
        endTime: '',
        endDateVal: ''
      },
      formData2: {
        cityCode: '',
        areaId: '',
        serviceCenterId: '',
        star: '',
        hasContent: '',
        startTime: '',
        startDateVal: '',
        endTime: '',
        endDateVal: ''
      },
      tableLoading: false,
      currentPageNum: 1, // 当前页
      currentPageSize: 10, // 显示条数
      totalCount: 0,
      allCityList: [],
      areaList: [],
      serviceCenterList: '',
      baseInfo: {},
      scoreList: [1, 2, 3, 4, 5],
      tableData: [],
      cols: [
        {
          type: 'expand',
          width: 50,
          render: (h, params) => {
            return h(expandRow, {
              props: {
                row: params.row
              }
            })
          }
        },
        { title: '教学点', key: 'venueName', align: 'center' },
        { title: '学员姓名',
          align: 'center',
          render: (h, params) => {
            return h('a', {
              on: {
                click: () => {
                  this.routerTo(params.row)
                }
              }
            }, params.row.studentRealName)
          }
        },
        { title: '学员编号', key: 'studentCard', align: 'center' },
        { title: '注册时间', key: 'createDate', align: 'center' },
        { title: '是否匿名',
          key: 'anonymous',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.anonymous === '1' ? '是' : '否')
          }
        },
        { title: '评分',
          key: 'star',
          align: 'center',
          render: (h, params) => {
            return h(Rate, {
              props: {
                disabled: true,
                // 'allow-half': true,
                value: params.row.star
              }
            })
          }
        },
        { title: '操作',
          align: 'center',
          render: (h, params) => {
            return h('a', {
              on: {
                click: () => {
                  this.$set(this.tableData[params.index], '_expanded', !this.tableData[params.index]._expanded)
                }
              }
            }, this.tableData[params.index]._expanded ? '收起详情' : '展开详情')
          }
        }
      ]

    }
  },
  methods: {
    search () {
      this.currentPageNum = 1
      this.getTableData()
    },
    getTableData () {
      if (!this.formData.cityCode) {
        this.$Message.warning('请选择所在城市')
        return
      }
      if (!this.formData.areaId) {
        this.$Message.warning('请选择区域')
        return
      }
      if (!this.formData.serviceCenterId) {
        this.$Message.warning('请选择服务中心')
        return
      }
      this.formData.pageSize = this.currentPageSize
      this.formData.pageNo = this.currentPageNum
      this.tableLoading = true
      getEvaluateList(this.formData).then(res => {
        if (res.records.length === 0) {
          this.tableData = []
          this.$Message.warning('暂无数据')
        } else {
          this.totalCount = res.totalCount
          this.tableData = res.records.map((r, index) => {
            r._index = index
            return r
          })
        }
        this.tableLoading = false
      }).catch(err => {
        this.tableData = []
        this.totalCount = 0
        this.currentPageSize = 10
        this.currentPageNum = 1
        this.tableLoading = false
        this.$Message.warning({ content: '获取数据失败:' + err.message, duration: 5 })
      })
    },
    // 分页
    changePage (num) {
      this.currentPageNum = num
      this.getTableData()
    },
    pageSizeChange (size) {
      this.currentPageNum = 1
      this.currentPageSize = size
      this.getTableData()
    },
    cityChange (val) { // 改变城市
      this.formData.areaId = ''
      this.formData.serviceCenterId = ''
      this.areaList = []
      this.serviceCenterList = []
      this.baseInfo = {}
      if (!val) return
      this.getAreaListHandler({
        cityCode: val
      }).then(res => {
        this.areaList = res
        queryServiceCenterByEmpNo({
          cityCode: this.formData.cityCode,
          empNo: this.$store.state.user.userInfo.id
        }).then(data => {
          this.formData.areaId = data.areaId
          this.formData2.areaId = data.areaId
          this.changeArea(data.areaId, data.serviceCenterId)
        }).catch(err => {
          this.$Message.error('获取当前所在服务中心失败:' + err.message)
        })
      }).catch(err => {
        this.$Message.error('获取区域失败:' + err.message)
      })
    },
    changeArea (val, serviceCenterId) { // 改变区域
      this.serviceCenterList = []
      this.formData.serviceCenterId = ''
      this.baseInfo = {}
      if (!val) {
        return false
      }
      let req = {
        cityCode: this.formData.cityCode,
        areaId: this.formData.areaId
      }
      this.getServiceCenterListHandler(req).then(res => {
        this.serviceCenterList = res
        this.serviceCenterList.forEach((value) => {
          if (!value.address) {
            value.address = ''
          }
        })
        if (serviceCenterId) {
          this.formData.serviceCenterId = serviceCenterId
          this.formData2.serviceCenterId = serviceCenterId
          this.getBaseInfo()
        }
      }).catch(err => {
        this.$Message.error('获取服务中心失败:' + err.message)
      })
    },
    getBaseInfo () {
      getAggregateInfo({
        cityCode: this.formData.cityCode,
        serviceCenterId: this.formData.serviceCenterId
      }).then(data => {
        this.baseInfo = data
      }).catch(err => {
        this.$Message.error('获取服务中心综合信息失败:' + err.message)
      })
    },
    changeSC (val) {
      if (!val) return
      this.getBaseInfo()
    },
    // 开课日期、结课日期改变
    startDateChange (val) {
      if (this.formData.endTime) {
        if (new Date(val).getTime() > new Date(this.formData.endTime).getTime()) {
          this.$Message.warning({ content: '结束时间不得小于开始时间', duration: 5 })
          this.formData.startTime = ''
          this.formData.startDateVal = ''
          return
        }
      }
      this.formData.startTime = val
    },
    endDateChange (val) {
      if (this.formData.startTime) {
        if (new Date(this.formData.startTime).getTime() > new Date(val).getTime()) {
          this.$Message.warning({ content: '结束时间不得小于开始时间', duration: 5 })
          this.formData.endTime = ''
          this.formData.endDateVal = ''
          return
        }
      }
      this.formData.endTime = val
    },
    reset () {
      depClone(this.formData, this.formData2)
    },
    routerTo (item) {
      this.$router.push({
        name: 'applyWorkbenchForMulti',
        query: {
          cityCode: this.formData.cityCode,
          card: item.studentCard,
          tab: 'classRecord'
        },
        params: {
          id: item.studentCard
        }
      })
    },
    tableExpand (row, status) {
      this.$set(this.tableData[row._index], '_expanded', status)
    }
  },
  watch: {
    allCityList (val) {
      this.formData.cityCode = val[0].id
      this.formData2.cityCode = val[0].id
      this.cityChange(this.formData.cityCode)
    }
  },
  mounted () {
    this.getCityListHandler()
    setFrom({
      apiName: 'serviceComment'
    })
  }
}
</script>

<style scoped lang="less">
  .blod-font {
    font-weight: bold;
    font-size: 16px;
  }
  .service-rate {
    height: 36px;
    display: flex;
    align-items: center;
    /deep/ .el-rate__icon {
      font-size: 25px;
    }
  }
  .tag-wrapper {
    display: flex;
    flex-wrap: wrap;
    padding-left: 30px;
    padding-bottom: 20px;
    border-bottom: 1px solid #ccc;
    .tag {
      height: 30px;
      line-height: 28px;
      padding: 0 10px;
      border-radius: 5px;
      margin-right: 20px;
      border: 1px solid #2D8cF0;
      color: #2D8cF0;
    }
  }
</style>
