<template>
  <!-- 攒班班级列表 -->
  <div>
    <Form inline :label-width="80" >
      <FormItem label="是否关班">
        <Select v-model="formData.status" style="width: 150px;" clearable @on-change="search">
          <Option
            v-for="item in assessStatusMap"
            :value="item.value"
            :key="item.value"
          >{{ item.label }}</Option>
        </Select>
      </FormItem>
    </Form>

    <!-- 列表 -->
    <Table
      border
      ref="selection"
      :loading="loading"
      :columns="columns"
      :data="tableData"
      @on-expand="onExpand"
    ></Table>

    <!-- 分页 -->
    <div
      style="margin: 10px;"
      class="flex-page"
      v-show="!tableLoading"
    >
      <Page
        :total="formData.totalCount"
        :page-size="formData.pageSize"
        :current.sync="formData.pageNo"
        show-elevator
        show-sizer
        show-total
        transfer
        @on-page-size-change="pageSizeChange"
        @on-change="changePage"
      ></Page>
    </div>
    <Modal
      title="查找报名学员"
      width='800'
      :footer-hide="true"
      v-model="searchStuModal">
      <TalStuSearch
        v-if="searchStuModal"
        ref="stuSearchForApply"
        :attendanceSearch='false'
        :showDetail='false'
        :showCrmInfo='false'
        choice-btn-text='报名'
        @stuInfo="setStuInfoForApplyHandle"></TalStuSearch>
    </Modal>
    <Modal
      title="关联攒班记录"
      width='80%'
      :footer-hide="true"
      v-model="relatedModal">
      <Table
        border
        :columns="relatedCols"
        :data="relatedData"
      ></Table>
    </Modal>
    <!--报名-->
    <TalApplyModal
      :show="applyShow"
      :classId="applyParams.classId"
      :stuId="applyParams.studentId"
      :stuCityCode="applyParams.cityCode"
      :creatorEmpNo="applyParams.empNo"
      @success="applySuccess"
      @close="applyShow = false">
    </TalApplyModal>

  </div>
</template>

<script>
import { queryPage, assembleRecordList, queryStudentDemandListByAssembleId, addSingleDemandRelate } from '@/api/assembleClass'
import Cookie from 'js-cookie'
import StuDetailExpand from './stuDetailExpand'
export default {
  name: 'CuanbanList',
  props: {
    parentFormData: {
      type: Object,
      default: function () {
        return {}
      }
    }
  },
  data () {
    return {
      loading: false,
      searchStuModal: false,
      formData: {
        status: '',
        totalCount: 0,
        pageSize: 10,
        pageNo: 1
      },
      applyShow: false,
      applyParams: {
        classId: '',
        studentId: '',
        cityCode: '',
        empNo: ''
      },
      //   审批状态
      assessStatusMap: [
        {
          value: 1,
          label: '是'
        },
        {
          value: 0,
          label: '否'
        }
      ],
      columns: [
        {
          type: 'expand',
          width: 50,
          render: (h, params) => {
            return h(StuDetailExpand, {
              props: {
                stuList: params.row.stuList,
                loading: params.row.stuListLoading
              },
              on: {
                apply: row => {
                  this.detailRow = params.row
                  this.applyParams.classId = row.classId
                  this.applyParams.studentId = row.studentId
                  this.applyParams.cityCode = row.areaCode
                  this.applyShow = true
                }
              }
            })
          }
        },
        {
          title: '操作',
          key: 'name',
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('a', {
                on: {
                  click: () => {
                    this.detailRow = params.row
                    this.applyParams.classId = params.row.clazzDetail.classId
                    this.assembleId = params.row.id
                    this.searchStuModal = true
                  }
                }
              }, '报名'),
              h('br'),
              h('a', {
                on: {
                  click: () => {
                    const row = this.tableData[params.index]
                    this.$set(row, '_expanded', !row._expanded)
                    if (row._expanded) {
                      this.queryStudents(row)
                    }
                  }
                }
              }, this.tableData[params.index]._expanded ? '收起学员详情' : '查看学员详情')
            ])
          }
        },
        {
          title: '班级名称',
          key: 'className',
          align: 'center',
          render: (h, params) => {
            return params.row.clazzDetail.className ? h('a', {
              on: {
                click: () => {
                  this.$emit('showClassDetail', params.row.clazzDetail.classId)
                }
              }
            }, params.row.clazzDetail.className) : h('span', '--')
          }
        },
        {
          title: '授课类型',
          key: 'categoryTypeName',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.clazzDetail.categoryTypeName)
          }
        },
        {
          title: '学期',
          key: 'termName',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.clazzDetail.termName)
          }
        },
        {
          title: '学科',
          key: 'subjectName',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.clazzDetail.subjectName)
          }
        },
        {
          title: '主讲教师',
          key: 'teacherNames',
          align: 'center',
          render: (h, params) => {
            return h('div', params.row.clazzDetail.teachers.map(t => {
              return h('a', {
                on: {
                  click: () => {
                    this.$emit('showTeacherDetail', params.row.cityCode, t.teacherId, t.teacherAccountType)
                  }
                }
              }, t.teacherName)
            }))
          }
        }, {
          title: '开课日期',
          key: 'startDate',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.clazzDetail.startDate)
          }
        }, {
          title: '结课日期',
          key: 'endDate',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.clazzDetail.endDate)
          }
        }, {
          title: '上课时间',
          key: 'classTimeDisplay',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.clazzDetail.classTimeDisplay)
          }
        }, {
          title: '服务中心',
          key: 'serviceCenterName',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.clazzDetail.classroomServiceCenterName)
          }
        }, {
          title: '教室',
          key: 'classroomName',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.clazzDetail.classroomName)
          }
        }, {
          title: '上课进度',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.clazzDetail.passedCount + '/' + params.row.clazzDetail.classCount)
          }
        }, {
          title: '班级限额',
          key: 'maxPersons',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.clazzDetail.maxPersons)
          }
        }, {
          title: '已报人数',
          key: 'registPersons',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.clazzDetail.registPersons)
          }
        }, {
          title: '剩余名额',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.clazzDetail.maxPersons - params.row.clazzDetail.registPersons)
          }
        }, {
          title: '是否关班',
          align: 'center',
          render: (h, params) => {
            return h('span', String(params.row.clazzDetail.isClose) === '1' ? '是' : '否')
          }
        }
      ],
      tableLoading: false,
      tableData: [],
      detailRow: null,
      relatedModal: false,
      assembleStatusMap: {
        1: '待评估',
        2: '评估中',
        3: '开班成功',
        4: '开班失败',
        5: '攒班成功',
        6: '攒班失败',
        7: '取消'
      },
      relatedCols: [
        {
          title: '操作',
          align: 'center',
          render: (h, params) => {
            return h('a', {
              on: {
                click: () => {
                  addSingleDemandRelate({
                    assembleClassDemandId: params.row.id,
                    assembleId: this.assembleId
                  }).then(() => {
                    this.$Message.success('关联成功')
                    this.relatedModal = false
                    this.applyShow = true
                  }).catch(err => {
                    this.$Message.error({
                      content: '关联失败：' + err.message,
                      duration: 5
                    })
                  })
                }
              }
            }, '关联攒班记录')
          }
        },
        {
          title: '攒班状态',
          key: 'status',
          align: 'center',
          render: (h, params) => {
            return h('span', this.assembleStatusMap[params.row.status])
          }
        },
        {
          title: '学员姓名',
          key: 'name',
          align: 'center'
        },
        {
          title: '接待顾问',
          key: 'receptionAdvisorEmpName',
          align: 'center'
        },
        {
          title: '年级',
          key: 'gradeName',
          align: 'center'
        },
        {
          title: '创建人',
          key: 'creatorName',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.creatorName || '用户')
          }
        },
        {
          title: '创建时间',
          key: 'createDate',
          align: 'center'
        },
        {
          title: '推荐学员',
          key: 'recommendName',
          align: 'center'
        },
        {
          title: '攒班年份',
          key: 'year',
          align: 'center'
        },
        {
          title: '意向学科',
          key: 'subjectName',
          align: 'center'
        },
        {
          title: '意向学期',
          key: 'termName',
          align: 'center'
        },
        {
          title: '班次级别',
          key: 'classLevelName',
          align: 'center'
        },
        {
          title: '意向服务中心',
          key: 'serviceCenterName',
          align: 'center'
        },
        {
          title: '意向上课时间',
          key: 'classtimeTimeClassifys',
          align: 'center'
        },
        {
          title: '意向上课时段',
          key: 'classtimeTimePeriods',
          align: 'center'
        },
        {
          title: '意向度',
          key: 'intention',
          align: 'center'
        }
      ],
      relatedData: []
    }
  },
  methods: {
    setStuInfoForApplyHandle (data) {
      this.searchStuModal = false
      this.applyParams.studentId = data.studentId
      this.applyParams.cityCode = data.cityCode
      this.showRelatedAssembleClass(data.uid)
      // this.applyShow = true
    },
    showRelatedAssembleClass (uid) {
      queryStudentDemandListByAssembleId({
        uid,
        assembleId: this.assembleId
      }).then(data => { // 查询出关联攒班记录时：关联后再报名，否则直接报名
        if (data && data.length) {
          this.relatedData = data
          this.relatedModal = true
        } else {
          this.applyShow = true
        }
      }).catch(err => {
        this.$Message.error({
          content: '攒班记录条件查询单个学员需求列表错误：' + err.message,
          duration: 5
        })
      })
    },
    async getTableData () {
      this.loading = true
      const params = Object.assign({}, this.parentFormData, this.formData, {
        startCreateDate: '',
        endCreateDate: ''
      })
      if (params.classType === 1 || !params.classType) {
        delete params.serviceCenterId
      }
      try {
        const data = await queryPage(params)
        this.tableData = data.records.map(d => {
          d.stuList = []
          return d
        })
        this.formData.totalCount = data.totalCount
      } catch (e) {
        this.$Message.error({
          content: '查询学员详情错误：' + e.message,
          duration: 5
        })
      } finally {
        this.loading = false
      }
    },
    // 分页
    changePage (num) {
      this.formData.pageNo = num
      this.getTableData()
    },
    pageSizeChange (size) {
      this.formData.pageNo = 1
      this.formData.pageSize = size
      this.getTableData()
    },
    search () {
      this.formData.pageNo = 1
      this.getTableData()
    },
    async applySuccess () {
      try {
        // await this.getTableData()
        const row = this.tableData.find(d => d.id === this.detailRow.id)
        this.queryStudents(row)
        this.$set(row, '_expanded', true)
      } catch (e) {
        this.$Message.error({
          content: '刷新列表错误：' + e.message || e,
          duration: 5
        })
      }
    },
    onExpand (row, status) {
      const expandRow = this.tableData.find(d => d.id === row.id)
      this.$set(expandRow, '_expanded', status)
      if (status) {
        this.queryStudents(expandRow)
      }
    },
    queryStudents (row) {
      row.stuListLoading = true
      assembleRecordList({
        id: row.id,
        classId: row.clazzDetail.classId,
        cityCode: row.cityCode
      }).then(data => {
        row.stuList = data
      }).catch(err => {
        this.$Message.error({
          content: '查询学员详情错误：' + err.message,
          duration: 5
        })
      }).finally(() => {
        row.stuListLoading = false
      })
    }
  },
  mounted () {
    this.applyParams.empNo = JSON.parse(Cookie.get('columbusUserInfo')).id
  }
}
</script>

<style lang="less" scoped>
.flex-page {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
</style>
