          render: (h, params) => {
            return h('div', {
              style: {
                color: params.row.readNumber > 0 ? '#4DA2FF' : '',
                cursor: 'pointer'
              },
              on: {
                click: () => {
                  if (!params.row.readNumber) return
                  this.showClass(params.row.readList)
                }
              }
            }, params.row.readNumber > 0 ? params.row.readNumber : '--')
          }
        },
        {
          title: '在线班级',
          key: 'onlineNumber',
          align: 'center',
          render: (h, params) => {
            return h('div', {
              style: {
                color: params.row.onlineNumber > 0 ? '#4DA2FF' : '',
                cursor: 'pointer'
              },
              on: {
                click: () => {
                  if (!params.row.onlineNumber) return
                  this.showClass(params.row.onlineList)
                }
              }
            }, params.row.onlineNumber > 0 ? params.row.onlineNumber : '--')
          }
        },
        {
          title: '面授班级',
          key: 'offlineNumber',
          align: 'center',
          render: (h, params) => {
            return h('div', {
              style: {
                color: params.row.offlineNumber > 0 ? '#4DA2FF' : '',
                cursor: 'pointer'
              },
              on: {
                click: () => {
                  if (!params.row.offlineNumber) return
                  this.showClass(params.row.offlineList)
                }
              }
            }, params.row.offlineNumber > 0 ? params.row.offlineNumber : '--')
          }
        },
        {
          title: '双师班级',
          key: 'doubleNumber',
          align: 'center',
