      levelList: [], // 年部列表
      checkboxLen: 0, // 学校列表长度
      checkLen: 0,
      //  统计汇总数量标示
      statisticsMark: false,
      registerNum: '-1', // 注册学员
      onlineReaderNum: '-1', // 在读学员
      faceNum: '-1', // 面授人次
      teachNum: '-1', // 双师人次
      onlineNum: '-1', // 在线人次
      serverCenterNum: '-1', // 服务中心在读
      statisticsTxt: '', // 表格题目
      // 注册学员详情
      current: 1, // 当前页码
      size: 10, // 单页数据量
      uids: [], // 根据条件查询统计人数的uid
      handleFunction: '', // 统计汇总查看详情的函数
      pageTotal: 0, // 列表的数据总数
      // 注册学员loading
      registerMark: false, // 注册学员loading
      onlineReaderNumMark: false, // 在读学员loading
      faceNumMark: false, // 面授学员loading
      teachNumMark: false, // 双师学员loading
      onlineNumMark: false, // 在线学员loading
      serverCenterNumMark: false, // 服务中心学员loading
      // 分校，地区，服务中心状态值
      schoolCode: '', // 分校
      districtId: '', // 地区
      serverId: '', // 服务中心schoolId
      bindServerId: '', // 服务中心id
      schoolData: {}, // 编辑学校人数数据
      totalNumber: '-1', // 批量查询分校总量
      coverRate: '-1'// 覆盖率
    }
  },
  mounted () {
    this.getEmpServiceCenter()// 获取服务中心
    this.handleGetGrade()// 获取年部
    // 获取当前年份
    const year = new Date().getFullYear()
    this.year = year
  },
  watch: {
    registerNum: function () {
      if (this.registerNum >= 0 && this.totalNumber > 0) {
        if ((this.registerNum / this.totalNumber).toFixed(2) > 1) {
          this.$Message.warning({ content: '覆盖率=选定年份年级下，注册学员数/学校的人数，请检查学校人数信息是否正确。', duration: 5 })
        }
        this.coverRate = this.registerNum / this.totalNumber > 0 ? (this.registerNum / this.totalNumber * 100).toFixed(2) : (this.registerNum / this.totalNumber).toFixed(2) * 100// 覆盖率
      } else {
        this.coverRate = '-1'
      }
    },
    totalNumber: function () {
      if (this.registerNum >= 0 && this.totalNumber > 0) {
        if ((this.registerNum / this.totalNumber).toFixed(2) > 1) {
          this.$Message.warning({ content: '覆盖率=选定年份年级下，注册学员数/学校的人数，请检查学校人数信息是否正确。', duration: 5 })
        }
        this.coverRate = this.registerNum / this.totalNumber > 0 ? (this.registerNum / this.totalNumber * 100).toFixed(2) : (this.registerNum / this.totalNumber).toFixed(2) * 100// 覆盖率
      } else {
        this.coverRate = '-1'
      }
    }
  },
  computed: {
    // coverRate () {
    //   if (this.registerNum >= 0 && this.totalNumber > 0) {
    //     if ((this.registerNum / this.totalNumber).toFixed(2) > 1) {
    //       this.coverRateNum = (this.registerNum / this.totalNumber).toFixed(2)
    //     } else {
    //       this.coverRateNum = 1
    //     }
    //     return this.registerNum / this.totalNumber > 0 ? (this.registerNum / this.totalNumber * 100).toFixed(2) : (this.registerNum / this.totalNumber).toFixed(2) * 100// 覆盖率
    //   } else {
    //     return '-1'
    //   }
    // },
    inversionRate () {
      if (this.onlineReaderNum >= 0 && this.registerNum > 0) {
        return this.onlineReaderNum / this.registerNum > 0 ? (this.onlineReaderNum / this.registerNum * 100).toFixed(2) : (this.onlineReaderNum / this.registerNum).toFixed(2) * 100// 转化率
      } else {
        return '-1'
      }
    },
    coverRateMark () {
      if (this.registerNum >= 0 && this.totalNumber > 0) {
        return true
      } else {
        return false
      }
    },
    inversionRateMark () {
      if (this.onlineReaderNum >= 0 && this.registerNum > 0) {
        return true
      } else {
        return false
      }
    },
    matchNum () {
      if (this.faceNum >= 0 && this.teachNum >= 0 && this.onlineNum >= 0 && this.onlineReaderNum > 0) {
        return ((this.faceNum + this.teachNum + this.onlineNum) / this.onlineReaderNum).toFixed(2)// 匹配度
      } else {
        return '-1'
      }
    },
    matchNumMark () {
      if (this.faceNum >= 0 && this.teachNum >= 0 && this.onlineNum >= 0 && this.onlineReaderNum > 0) {
        return true
      } else {
        return false
      }
    }
  },
  methods: {
    // 获取服务中心
    getEmpServiceCenter: function () {
      getEmpServiceCenter().then(res => {
        if (res.rlt !== true || !res.data) return
        this.serviceSiteMark = true
        this.deptId = res.data && res.data.id
        this.schoolCode = res.data && res.data.cityCode
        this.districtId = res.data && res.data.districtId
        this.serverId = res.data && res.data.schoolId
        this.bindServerId = res.data && res.data.id
        this.serviceSite = `${res.data && res.data.districtName}-${res.data && res.data.name}`
        this.handleSchoolBackList()// 显示学校列表
      })
    },
    // 获取年份
    handleGetYear: function () {
      let promise = new Promise((resolve, reject) => {
        getYear().then(res => {
          if (res.rlt !== true || !res.data) return
          this.yearList = res.data
          resolve()
        })
      })
      return promise
    },
    // 获取学期
    handleGetTerm: function () {
      let promise = new Promise((resolve, reject) => {
        getTerm({ schoolCode: this.schoolCode, termType: 1, status: 0 }).then(res => {
          if (res.rlt !== true || !res.data) return
          this.termList = res.data
          resolve()
          // 默认春季班
        })
      })
      return promise
    },
    // 获取年部
    handleGetGrade (schoolCode) {
      getGrade({ schoolCode: schoolCode, status: 0 }).then(res => {
        if (res.rlt !== true || !res.data) return
        this.levelList = res.data
      })
    },
    // 获取授课类型
    handleGetClassTypes: function () {
      getClassTypes().then(res => {
        if (res.rlt !== true || !res.data) return
        const classTypes = res.data
        classTypes.unshift({ 'id': '$', 'name': '全部' })
        this.classTypes = classTypes
      }).catch((err) => {
        this.$Message.warning({ content: '获取授课类型失败:' + err.message, duration: 5 })
      })
    },
    // 获取年级
    handleGetGradeList () {
      let promise = new Promise((resolve, reject) => {
        getGradeList({ cityCode: this.schoolCode, empNo: this.$store.state.user.userInfo.id, gradeTypeId: this.level }).then(res => {
          if (res.rlt !== true || !res.data) return
          const gradeList = res.data
          const gradeIdArr = []
          gradeList.forEach(item => {
            gradeIdArr.push(item.standardId)
          })
          this.gradeIdAll = gradeIdArr
          gradeList.unshift({ 'standardId': '$', 'name': '全部' })
          this.gradeList = gradeList
          resolve()
        })
      })
      return promise
    },
    // 获取学科
    handleGetSubjectList () {
      let promise = new Promise((resolve, reject) => {
        getSubjectList({ schoolCode: this.schoolCode, status: 0, gradeTypeId: this.level }).then(res => {
          if (res.rlt !== true || !res.data) return
          const subjectList = res.data
          const subjectArr = []
          subjectList.forEach(item => {
            subjectArr.push(item.standardId)
          })
          this.subjectIdAll = subjectArr
          subjectList.unshift({ 'standardId': '$', 'name': '全部' })
          this.subjectList = subjectList
          resolve()
        })
      })
      return promise
    },
    // 获取批量学校学员人数
    handleSchoolListNum: function () {
      let promise = new Promise((resolve, reject) => {
        let params = {
          empNo: this.$store.state.user.userInfo.id,
          cityCode: this.schoolCode,
          gradeTypeId: this.level,
          schoolIdList: this.checkAllGroupIds
        }
        searchSchoolGradeList(params).then(res => {
          if (res.rlt !== true || !res.data) return
          this.totalNumber = res.data.map(item => item.totalNumber).reduce((total, value) => total + value, 0)
          this.handleGetReadingCount()
          resolve()
        })
      })
      return promise
    },
    // 获取分校回显
    handleSchoolBackList (deptId) {
      if (deptId) {
        this.deptId = deptId
        this.bindServerId = deptId
      }
      this.selectList = []// 初始化学校列表
      getSchoolBackList({ schoolCode: this.schoolCode, deptId: this.deptId }).then(res => {
        if (res.rlt !== true || !res.data) return
        let levelArr = []
        const selectList = []
        res.data.forEach(item => {
          levelArr.push(item.level)
        })
        levelArr = new Set(levelArr)
        levelArr.forEach(item => {
          const obj = {}
          const thisArr = []
          res.data.forEach(each => {
            if (each.level === item) {
              thisArr.push(each)
            }
          })
          obj.list = thisArr
          obj.level = item
          obj.name = this.levelList.find(each => each.id === item).name
          selectList.push(obj)
        })
        this.resetStaticies()
        this.resetCheckBox()
        this.selectList = selectList
      }).catch((err) => {
        this.$Message.warning({ content: '获取学校列表失败:' + err.message, duration: 5 })
      })
    },
    // 取消学校列表勾选状态
    resetCheckBox: function () {
      this.checkboxLen = 0
      this.checkAllGroupIds = []
      this.checkAllGroupList = []
      this.checkAll = false
      this.checkAllGroup = []
      this.checkLen = 0
    },
    // 学校列表下查看按钮点击
    handleScan: function () {
      this.statisticsMark = true
      this.year = 2019 // 年份
      this.term = '2' // 学期
      this.gradeId = '$' // 年级
      this.classType = '$' // 授课类型
      this.handleLoading()
      this.resetStaticies()
      // 获取统计汇总下数据
      let prmiseYear = this.handleGetYear()// 获取年份
      let prmiseTerm = this.handleGetTerm()// 获取学期
      let prmiseGrade = this.handleGetGradeList()// 获取年级
      let prmiseSubject = this.handleGetSubjectList()// 获取学科
      this.handleSchoolListNum()// 批量查询学校学员人数
      this.handleGetClassTypes()
      Promise.all([prmiseYear, prmiseTerm, prmiseGrade, prmiseSubject]).then((result) => {
        this.handleStatisticsAll()// 获取统计汇总数据
        this.handleGetReadingCount()// 获取注册学员
      })
    },
    resetStaticies: function () {
      this.registerNum = -1
      this.onlineReaderNum = -1
      this.faceNum = -1
      this.teachNum = -1
      this.onlineNum = -1
      this.serverCenterNum = -1
      this.detailList = []// 清空右侧数据详情列表
      this.pageTotal = 0// 初始化分頁
      this.current = 1
      this.size = 10
    },
    // 增加数据loading
    handleLoading: function () {
      this.registerMark = true
      this.onlineReaderNumMark = true
      this.faceNumMark = true
      this.teachNumMark = true
      this.onlineNumMark = true
      this.serverCenterNumMark = true
    },
    handleSearch: function () { // 查询统计数据
      this.resetStaticies()
      this.handleStatisticsAll()
      // this.handleGetReadingCount()// 获取注册学员
      this.handleLoading()
      this.handleSchoolListNum()// 批量查询学校学员人数
    },
    // 绑定服务中心
    handleChoose: function (params) {
      this.serviceSite = params
      this.serviceSiteMark = true
    },
    // 地区回显
    handleDistrict: function (districtId) {
      this.districtId = districtId
    },
    // 服务中心schoold
    handleServer: function (serverId) {
      this.serverId = serverId
    },
    // 分校
    handleBindSchoolCode: function (schoolCode) {
      this.schoolCode = schoolCode
    },
    // 绑定学校人数
    handleSchoolNum: function (item, levelName) {
      let schoolData = { ...item, levelName: levelName }
      let params = {
        empNo: this.$store.state.user.userInfo.id,
        cityCode: this.schoolCode,
        gradeTypeId: this.level,
        schoolId: item.schoolId
      }
      schoolData.params = params
      schoolData.checkAllGroupIds = this.checkAllGroupIds
      this.schoolData = schoolData
      this.$store.state.service.schoolEditState = true
    },
    // 切换服务中心模态框
    handleService: function () {
      this.$store.commit('service/HANDLE_SERVICECENTER', true)
    },
    // 学校管理模态框
    handleSchoolManage: function () {
      if (!this.serviceSiteMark) {
        this.$Message.info('请先绑定服务中心')
        return
      }
      this.$store.commit('service/HANDLE_SCHOOLMANAGE', true)
    },
    // 展示在读班级
    showClass: function (ids) {
      this.showClassState = true
      this.handleGetfindClassByIds(ids)
    },
    // 获取学员统计数据统一方法
    handleStatisticsAll: function () {
      this.handleStatisticsByConditions(1)// 获取在线人次
      this.handleStatisticsByConditions(2)// 获取双师人次
      this.handleStatisticsByConditions(4)// 获取面授人次
      this.handleGetCount()// 获取在线学员在读人数
      this.handleGetCount(this.serverId)// 获取服务中心在读人数
      // this.handleGetReadingCount()// 获取注册学员
    },
    // 获取面授、双师、在线的人次
    handleStatisticsByConditions (classType) {
      // 判断年级
      let gradeId = this.handleGradeId()
      let classTypes = this.handleClassType()
      let payloads = {
        cityCode: this.schoolCode,
        orgCode: this.schoolCode,
        year: this.year,
        termId: this.term,
        gradeIds: this.gradeIdAll, // 年级id
        schoolIds: this.checkAllGroupIds,
        schoolGradeIds: gradeId,
        classType: String(classType),
        classTypes: classTypes
      }
      if (this.subjectId !== '$') {
        payloads.subjectId = String(this.subjectId)
      }
      getStatisics(payloads).then(res => {
        if (res.rlt !== true) return
        if (classType === 1) { // 在线
          this.onlineNum = res.data
          this.onlineNumMark = false
        } else if (classType === 2) { // 双师
          this.teachNum = res.data
          this.teachNumMark = false
        } else { // 4 面授
          this.faceNum = res.data
          this.faceNumMark = false
        }
      }).catch((err) => {
        this.$Message.warning({ content: '获取人次数据失败:' + err.message, duration: 5 })
      })
    },
    // 获取在读学员、服务中心在读数据
    handleGetCount (serviceCenterId) {
      // 判断授课类型
      let classType = this.handleClassType()
      // 判断年级
      let gradeId = this.handleGradeId()
      let payloads = {
        cityCode: this.schoolCode,
        orgCode: this.schoolCode,
        year: this.year,
        termId: this.term,
        gradeIds: this.gradeIdAll, // 年级id
        schoolIds: this.checkAllGroupIds,
        classTypes: classType,
        schoolGradeIds: gradeId,
        serviceCenterId: serviceCenterId || ''
      }
      if (this.subjectId !== '$') {
        payloads.subjectId = String(this.subjectId)
      }
      getCount(payloads).then(res => {
        if (res.rlt !== true) return
        if (serviceCenterId) { // 服务中心在读
          this.serverCenterNum = res.data
          this.serverCenterNumMark = false
        } else { // 在读学员
          this.onlineReaderNum = res.data
          this.onlineReaderNumMark = false
        }
      }).catch((err) => {
        this.$Message.warning({ content: '获取在读数据失败:' + err.message, duration: 5 })
      })
    },
    // 获取注册学员人数
    handleGetReadingCount: function () {
      const payloads = {
        year: this.year,
        schoolIds: this.checkAllGroupIds,
        gradeType: this.level,
        loginCity: this.schoolCode,
        cityCode: this.schoolCode
      }
      if (this.gradeId !== '$') {
        payloads.gradeId = Number(this.gradeId)
      }
      getReadingCount(payloads).then(res => {
        if (res.rlt !== true) return
        this.registerMark = false
        // 注册学员
        this.registerNum = res.data
      }).catch((err) => {
        this.$Message.warning({ content: '获取注册学员数据失败:' + err.message, duration: 5 })
      })
    },
    // 点击注册学员获取详情
    handleRegister: function (mark) {
      if (this.registerNum === 0) return
      if (mark) {
        this.initPagePayloads()
        this.pageTotal = this.registerNum
        this.statisticsTxt = '注册学员'
        this.handleFunction = this.handleRegister
      }
      console.log(this.size, '注册学员')
      let payloads = {
        year: this.year,
        cityCode: this.schoolCode,
        schoolIds: this.checkAllGroupIds,
        gradeType: this.level,
        loginCity: this.schoolCode,
        current: this.current,
        size: this.size
      }
      if (this.gradeId !== '$') {
        payloads.gradeId = Number(this.gradeId)
      }
      let uids = []
      getSchoolReadingDetail(payloads).then(res => {
        if (res.rlt !== true) return
        this.detailList = res.data
        res.data.forEach(item => uids.push(item.uid))
        this.uids = uids
        this.handleGetUidsCountByConditions(this.detailList)
      }).catch((err) => {
        this.$Message.warning({ content: '获取详情失败:' + err.message, duration: 5 })
      })
    },
    // 点击在读学员
    handleonlineReader: function (mark) {
      if (this.onlineReaderNum === 0) return
      if (mark) {
        this.initPagePayloads()
        this.handleFunction = this.handleonlineReader
      }

      this.pageTotal = this.onlineReaderNum
      this.statisticsTxt = '在读学员'
      // 判断授课类型
      let classType = this.handleClassType()
      // 判断年级
      let gradeId = this.handleGradeId()
      let payloads = {
        cityCode: this.schoolCode,
        orgCode: this.schoolCode,
        year: this.year,
        termId: this.term,
        classTypes: classType,
        gradeIds: this.gradeIdAll, // 年级id
        schoolIds: this.checkAllGroupIds,
        schoolGradeIds: gradeId,
        pageNo: this.current,
        pageSize: this.size
      }
      if (this.subjectId !== '$') {
        payloads.subjectId = String(this.subjectId)
      }
      getUidsCountByConditions(payloads).then(res => {
        if (res.rlt !== true) return
        this.uids = res.data
        this.handleGetUidsCountByConditions()
      }).catch((err) => {
        this.$Message.warning({ content: '获取详情失败:' + err.message, duration: 5 })
      })
    },
    // 根据uids查询不同类别统计人数
    handleGetUidsCountByConditions: function (detailList) {
      // 判断年级
      let gradeId = this.handleGradeId()
      const payloads = {
        cityCode: this.schoolCode,
        orgCode: this.schoolCode,
        year: this.year,
        termId: this.term,
        gradeIds: this.gradeIdAll, // 年级id
        schoolIds: this.checkAllGroupIds,
        schoolGradeIds: gradeId,
        uids: this.uids,
        serviceCenterId: this.serverId
      }
      if (this.subjectId !== '$') {
        payloads.subjectId = String(this.subjectId)
      }
      getClassCountByUids(payloads).then(res => {
        if (res.rlt !== true) return
        // 点击的是注册学员
        if (detailList) {
          this.detailList = detailList.map((item, index) => {
            let obj = {}
            res.data.forEach(each => {
              if (Number(item.uid) === Number(each.uid)) {
                obj = Object.assign({}, item, each)
              }
            })

            return obj
          })
        } else {
          this.detailList = res.data
          this.handleGetListBasicUsers()
        }
      }).catch((err) => {
        this.$Message.warning({ content: '获取统计人数失败:' + err.message, duration: 5 })
      })
    },
    // 通过uid批量获取学员基本信息
    handleGetListBasicUsers: function () {
      const payloads = {
        cityCode: this.schoolCode,
        uids: this.uids,
        loginCity: this.schoolCode,
        year: this.year
      }
      getListBasicUsers(payloads).then(res => {
        if (res.rlt !== true || !res.data) return
        this.detailList = this.detailList.map((item, index) => {
          let obj = {}
          res.data.forEach(each => {
            if (Number(item.uid) === Number(each.uid)) {
              obj = Object.assign({}, item, each)
            }
          })
          return obj
        })
      }).catch((err) => {
        this.$Message.warning({ content: '获取学员基本信息失败:' + err.message, duration: 5 })
      })
    },
    // 点击服务中心在读
    handleServerCenter: function (mark) {
      if (this.serverCenterNum === 0) return
      if (mark) {
        this.statisticsTxt = '服务中心在读学员'
        this.pageTotal = this.serverCenterNum
        this.initPagePayloads()
        this.handleFunction = this.handleServerCenter
      }
      // 判断授课类型
      let classType = this.handleClassType()
      // 判断年级
      let gradeId = this.handleGradeId()
      let payloads = {
        cityCode: this.schoolCode,
        orgCode: this.schoolCode,
        year: this.year,
        termId: this.term,
        classTypes: classType,
        gradeIds: this.gradeIdAll, // 年级id
        schoolIds: this.checkAllGroupIds,
        schoolGradeIds: gradeId,
        pageNo: this.current,
        pageSize: this.size,
        serviceCenterId: this.serverId
      }
      if (this.subjectId !== '$') {
        payloads.subjectId = String(this.subjectId)
      }
      getUidsCountByConditions(payloads).then(res => {
        if (res.rlt !== true) return
        this.uids = res.data
        this.handleGetUidsCountByConditions()
      }).catch((err) => {
        this.$Message.warning({ content: '获取服务中心详情失败:' + err.message, duration: 5 })
      })
    },
    // 根据班级id(多个逗号分隔)查询班级详情
    handleGetfindClassByIds: function (ids) {
      const payloads = {
        cityCode: this.schoolCode,
        ids: ids
      }
      getfindClassByIds(payloads).then(res => {
        if (res.rlt !== true && !res.data) return
        this.classList = res.data
      }).catch((err) => {
        this.$Message.warning({ content: '获取班级详情失败:' + err.message, duration: 5 })
      })
    },
    // 初始化获取详情列表分页参数
    initPagePayloads: function () {
      this.current = 1
      // this.size = 10
    },
    // 每页条数改变
    handleSizeChange: function (size) {
      this.size = size
      console.log(this.size)
      if (this.handleFunction) {
        this.handleFunction()
      }
    },
    // 当前页面改变
    handleCurrentSearch: function (current) {
      this.current = current
      if (this.handleFunction) {
        this.handleFunction()
      }
    },
    // 学校全选
    handleCheckAll (item) {
      this.checkAll = !this.checkAll
      if (this.checkAll) {
        const list = item.list
        const group = []
        const groupIds = []
        list.forEach(item => {
          group.push(item.name)
          groupIds.push(item.schoolId)
        })
        this.checkAllGroup = group
        this.checkAllGroupIds = groupIds
        this.checkLen = group.length
      } else {
        this.checkAllGroup = []
        this.checkAllGroupIds = []
        this.checkLen = 0
      }
      this.resetStaticies()
    },
    // 学校单选
    checkGroupChange (data) {
      if (data.length === this.checkboxLen) {
        this.checkAll = true
      } else {
        this.checkAll = false
      }
      this.checkLen = data.length
      const checkAllGroupIds = []
      // 筛选出要查看的id
      this.checkAllGroupList.forEach(each => {
        data.forEach(item => {
          if (item === each.name) {
            checkAllGroupIds.push(each.schoolId)
          }
        })
      })
      this.checkAllGroupIds = checkAllGroupIds
      this.resetStaticies()
    },
    // 年部展开合并
    handleOpenChange: function (arr) {
      const key = arr[0]
      if (key) {
        const checkAllGroupList = this.selectList.find(item => item.level === key).list
        this.checkboxLen = checkAllGroupList.length
        this.checkAllGroupList = checkAllGroupList
      } else {
        this.checkboxLen = 0
        this.checkAllGroupIds = []
        this.checkAllGroupList = []
      }
      this.statisticsMark = false
      this.checkAll = false
      this.checkAllGroup = []
      this.checkLen = 0
      this.level = key
      this.resetStaticies()
    },
    // 返回授课类型
    handleClassType: function () {
      // 判断授课类型
      let classType = this.classType
      if (classType === '$') {
        classType = ['1', '2', '4']
      } else {
        classType = [this.classType]
      }
      return classType
    },
    // 返回年级
    handleGradeId: function () {
      let gradeId
      if (this.gradeId === '$') {
        gradeId = this.gradeIdAll
      } else {
        gradeId = [this.gradeId]
      }
      return gradeId
    },
    // 覆盖率提示
    handleCoverRateNotice: function () {
      if (this.checkLen === 0) return
      this.$Message.warning({ content: '请编辑学校人数', duration: 5 })
    }
  }
}
</script>

