<template>
  <Layout class="layout">
    <header class="header">
      <h2>专属二维码</h2>
      <p class="header_p">以下二维码中带有您的个人信息，将二维码转发至微信，学员通过二维码进行注册，您将获得绩效奖</p>
    </header>
    <div class="content">
      <div class="content_border">
        <TalTab style="width: 207px;">
          <template v-if="activityLabelList.length">
            <TalTabItem :border-bottom="false" :active="item.name === activeActivity.name" v-for="(item, index) in activityLabelList" :key="index" @on-click="handleChangeActivity(index, item)">
              <span class="f-14">{{ item.name }}</span>
            </TalTabItem>
          </template>
          <span style="color: gray;text-align: center;margin-top: 100px;" v-else>暂无数据</span>
        </TalTab>
        <div class="display" v-if="activityLabelList.length && activeActivity.id !== 'loginMinapp'">
          <div class="poster" v-if="hasPosterArea">
            <img v-if="activeActivity.posterUrl" :src="activeActivity.posterUrl"
              alt="海报图加载失败" class="poster_img" @error="onImageLoadError('posterUrl')">
            <div class="spin-container" v-else>
                <Spin fix size="large"></Spin>
            </div>
            <Button v-if="activeActivity.posterUrl === 'failed'"
              @click="setCodeUrl('posterUrl')"
              type="primary" class="download_button">重新加载</Button>
            <Button v-if="activeActivity.posterUrl && activeActivity.posterUrl !== 'failed'"
              type="primary" class="download_button"
              :loading="posterDownloading"
              @click="downloadImg(activeActivity.posterUrl, 'poster')">下载</Button>
          </div>
          <div class="qrCode">
            <img v-if="activeActivity.qrCodeUrl" :src="activeActivity.qrCodeUrl"
              alt="二维码加载失败" class="poster_img" @error="onImageLoadError('qrCodeUrl')">
            <div class="spin-container" v-else>
                <Spin fix size="large"></Spin>
            </div>
            <Button v-if="activeActivity.qrCodeUrl === 'failed'"
              @click="setCodeUrl('qrCodeUrl')"
              type="primary" class="download_button">重新加载</Button>
            <Button v-if="activeActivity.qrCodeUrl && activeActivity.qrCodeUrl !== 'failed'"
              type="primary" class="download_button"
              :loading="qrCodeDownloading"
              @click="downloadImg(activeActivity.qrCodeUrl, 'qrcode')">下载</Button>
          </div>
        </div>
        <!-- 小程序拉流账号 -->
        <div class="display f12" style="flex-direction: column;" v-if="activeActivity.id === 'loginMinapp'">
          <h5>小程序账号: {{ minappInfo.studentCard }}</h5>
          <h5 class="mgt-16">小程序密码: <Base64Encode :content="minappInfo.initPassword" /></h5>
          <p class="mgt-16" style="color: gray;">*学而思培优App上能进行密码修改，以修改后的密码为准</p>
          <div class="instructions-container mgt-28">
            <p>使用说明：</p>
            <p>1.使用账号密码登录小程序，并分享页面，小程序链接中带有您的个人信息。</p>
            <p>2.用户进入链接后完成注册，您将获得绩效。注册用户转化长期班也能计入绩效。</p>
            <p>3.可分享的页面为：到店有礼活动页面、预约诊断页面、预约试听页面、我的专属顾问页面、课程列表页面。</p>
          </div>
        </div>
      </div>
    </div>
  </Layout>
</template>

<script>
import TalTab from '@/components/tab/tal-tab'
import TalTabItem from '@/components/tab/tal-tab-item'
import Base64Encode from '@/components/base64-encode'
import {
  queryDimensionCode,
  queryStoreActivityLabelList,
  getChannelUserInfo
} from '../../../api/user'
import { mapState } from 'vuex'
export default {
  name: 'QRCode',
  components: { TalTab, TalTabItem, Base64Encode },
  computed: {
    ...mapState({
      cityCode: (state) => state.roleRelativeData.userInfo.cityCode
    }),
    activeActivity: function () {
      return this.activityLabelList[this.activeActivityIndex] || {}
    }
  },
  data () {
    return {
      activityLabelList: [],
      posterDownloading: false,
      qrCodeDownloading: false,
      // activeActivity: { name: '', id: '', loaded: false },
      activeActivityIndex: -1, // 当前选中tab的index
      minappInfo: {},
      hasPosterArea: true
    }
  },
  watch: {
    cityCode () {
      if (this.cityCode && this.activityLabelList.length === 0) {
        this.queryStoreActivityLabelList()
        this._getChannelUserInfo()
      }
    },
    async activeActivityIndex () {
      if (!this.activityLabelList[this.activeActivityIndex].loaded && this.activityLabelList[this.activeActivityIndex].id !== 'loginMinapp') {
        this.activityLabelList[this.activeActivityIndex].loaded = true
        this.setCodeUrl('posterUrl')
        this.setCodeUrl('qrCodeUrl')
      }
    }
  },
  mounted () {
    if (this.cityCode) {
      this.queryStoreActivityLabelList()
      this._getChannelUserInfo()
    }

    this.setBuriedPoint('init')
  },

  methods: {
    async _getChannelUserInfo () {
      try {
        const params = {
          cityCode: this.cityCode
        }
        const data = await getChannelUserInfo(params)
        this.minappInfo = { ...data }
      } catch (error) {
        this.$Message({ content: '获取推广顾问账号失败: ' + error.message, duration: 5 })
      }
    },
    onImageLoadError (fieldName) {
      this.activityLabelList[this.activeActivityIndex][fieldName] = 'failed'
      console.log('海报图加载失败')
    },
    // 设置埋点
    setBuriedPoint (type) {
      let trackId
      switch (type) {
        case 'init':
          trackId = '202-4926'
          break
        // 到店有礼
        case 'shop-tab':
          trackId = '202-4928'
          break
        case 'shop-poster':
          trackId = '202-4929'
          break
        case 'shop-qrcode':
          trackId = '202-4930'
          break
        // 预约诊断
        case 'diagnosis-tab':
          trackId = '202-4931'
          break
        case 'diagnosis-poster':
          trackId = '202-4932'
          break
        case 'diagnosis-qrcode':
          trackId = '202-4933'
          break
        // 预约试听
        case 'audition-tab':
          trackId = '202-4934'
          break
        case 'audition-poster':
          trackId = '202-4935'
          break
        case 'audition-qrcode':
          trackId = '202-4936'
          break
        // 攒班
        case 'assembleClass-tab':
          trackId = '202-4937'
          break
        case 'assembleClass-poster':
          trackId = '202-4938'
          break
        case 'assembleClass-qrcode':
          trackId = '202-4939'
          break
        // 老带新
        case 'recommend-tab':
          trackId = '202-4940'
          break
        case 'recommend-poster':
          trackId = '202-4941'
          break
        case 'recommend-qrcode':
          trackId = '202-4942'
          break
        // 专属顾问二维码
        case 'consultant-tab':
          trackId = '202-4943'
          break
        case 'consultant-poster':
          trackId = '202-4944'
          break
        case 'consultant-qrcode':
          trackId = '202-4945'
          break
        // 顾问个人二维码
        case 'classList-tab':
          trackId = '202-4946'
          break
        case 'classList-qrcode':
          trackId = '202-4947'
          break
        // case 'classList-qrcode':
        //   trackId = '202-4948'
        //   break
      }
      // console.log('trackId', trackId, type)
      if (trackId) {
        XesAnalytics.track(trackId, {
          adviser: this.$store.state.roleRelativeData.userInfo.crmRoleId,
          adviser_departmentID: this.$store.state.roleRelativeData.userInfo
            .crmDeptId,
          adviser_empNo: this.$store.state.roleRelativeData.roleInfo
            .userWorkCode,
          adviser_citycode: this.$store.state.roleRelativeData.userInfo
            .cityCode
        })
      }
    },
    setCodeUrl (fieldName) {
      this.activityLabelList[this.activeActivityIndex][fieldName] = ''
      const codeType = fieldName === 'qrCodeUrl' ? 1 : 2 // 1 二维码 2 海报图
      const currentIndex = this.activeActivityIndex
      return this.queryDimensionCode(this.activeActivity.id, codeType).then(
        (url) => {
          setTimeout(() => {
            if (!url) {
              return (this.activityLabelList[currentIndex][fieldName] =
                'failed')
            }
            this.activityLabelList[currentIndex][fieldName] = url
          }, 200)
        }
      )
    },
    queryDimensionCode (id, codeType) {
      return queryDimensionCode({
        trigPageTag: id,
        codeType,
        cityCode: this.cityCode
      })
        .then((res) => {
          const blob = new Blob([res.data], {
            type: 'image/jpeg;charset=UTF-8'
          })
          return URL.createObjectURL(blob)
        })
        .catch((error) => {
          console.log('接口失败', id, codeType)
          this.$Message.error({
            content: `获取二维码失败: ${error.message}`,
            duration: 5
          })
        })
    },
    async queryStoreActivityLabelList () {
      try {
        const res = await queryStoreActivityLabelList({
          cityCode: this.cityCode
        })
        res.forEach((item) => {
          item.posterUrl = ''
          item.qrCodeUrl = ''
          item.loaded = false
        })
        this.activityLabelList = res || []
        this.activityLabelList.unshift({
          id: 'loginMinapp',
          name: '小程序拉流账号',
          loaded: false
        })
        if (this.activityLabelList.length) {
          this.activeActivityIndex = 0
        }
      } catch (error) {
        this.$Message.error({
          content: `获取活动二维码列表失败: ${error.message}`,
          duration: 5
        })
      }
    },
    handleChangeActivity (index, activity) {
      this.activeActivityIndex = index
      if (activity.name === '课程列表页' || activity.name === '签到页') {
        this.hasPosterArea = false
      } else {
        this.hasPosterArea = true
      }
      this.setBuriedPoint(activity.id + '-tab')
    },
    async downloadImg (imageURL, type) {
      const downloading =
        type === 'poster' ? 'posterDownloading' : 'qrCodeDownloading'
      try {
        this[downloading] = true
        const link = document.createElement('a')
        link.href = imageURL
        link.download =
          type === 'poster'
            ? `${this.activeActivity.name}海报图`
            : `${this.activeActivity.name}二维码`
        document.body.appendChild(link)
        link.click()
        document.body.removeChild(link)
        this.setBuriedPoint(this.activeActivity.id + '-' + type)
      } catch (error) {
        this.$Message.error({
          content: `图片下载失败: ${error.message}`,
          duration: 5
        })
      }
      this[downloading] = false
    }
  }
}
</script>
