  margin-top: 20px;
  min-height: 645px;
}

.content_border {
  height: 100%;
  border: 1px solid rgba(0, 0, 0, 0.1);
  display: flex;
}

.display {
  display: flex;
  // justify-content: space-around;
  flex-grow: 1;
  padding: 24px 48px;
  box-sizing: border-box;
}

.poster,
.qrCode {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 48px;
