<template>
  <div style="width: 100%;padding: 15px;" >
    <div class="stu-search">
      <TalStuSearch
      ref="stuSearch"
      :stu-card="queryData.card"
      :stu-city-code="queryData.cityCode"
      :showCrmInfo="false"
      @stuInfo="setStuInfo"></TalStuSearch>
    </div>
    <div v-if="scoreRecordShow" style="margin-top: 10px;padding:10px;background: #fff">
      <SubscribeDiagnosis :operateType="1" v-if="scoreRecordShow" :studentDetail="studentDetail" @success="$emit('success')" @close="$emit('close')"></SubscribeDiagnosis>
    </div>
    <div v-else style="margin-top: 10px;padding:20px;background: #fff; font-size: 13px">
      请选择学员
    </div>
  </div>
</template>

<script>
import getUserInfoByUid from '@/components/mixin/getUserInfoByUid'
import SubscribeDiagnosis from './subscribeDiagnosis'

export default {
  name: 'addSubscribeDiagnosis',
  mixins: [getUserInfoByUid],
  data () {
    return {
      studentDetail: {},
      scoreRecordShow: false
    }
  },
  components: {
    SubscribeDiagnosis
  },
  methods: {
    setStuInfo (stuInfo) {
      this.scoreRecordShow = false
      this.studentDetail = stuInfo
      setTimeout(() => {
        this.scoreRecordShow = true
      }, 50)
    }
  }
}
</script>
<style lang="less" scoped>
#TalScoreRecording {
  overflow-x: hidden;
  padding: 10px;
}

.stu-search {
  /deep/ .ivu-col {
    white-space: nowrap;
  }
}
</style>
