export default class GroupMultiple {
  // data是聚合的数据， name是聚合的字段
  constructor (data, name) {
    this.data = data
    this.name = name
  }

  // 获取key
  getKeys (data, names) {
    return data.reduce((prev, cur) => {
      // 首先拿到name对应的值， 将key组成 {key: value}形式
      let key = this.getObject(cur, names)
      // 判断数组里是否已经含有了key，有的话就跳过，否则把key放入到数组里
      // 把判断方法封装成一个函数 isHasObject
      if (!this.isHasObject(prev, key)) {
        prev.push(key)
      }
      return prev
    }, [])
  }

  getObject (obj, keys = this.name) {
    let aa = {}
    for (let i = 0, length = keys.length; i < length; i++) {
      aa[keys[i]] = obj[keys[i]]
    }
    return aa
  }

  // 判断数组中是否有了某个对象
  isHasObject (prev, keys) {
    // 数组长度为0的时候直接返回
    if (prev.length === 0) return false
    // 判断数组中的每一项是否与keys相等， 这里用到数组的find方法，当数组的每一项与keys都不想等的时候（也就是数组中没有这个对象）返回undefined，
    // isObjectValueEqual 是判断两个对象是否相等的方法
    return prev.find(item => {
      return this.isObjectValueEqual(item, keys)
    })
  }

  // 判断两个对象是否相等
  isObjectValueEqual (obj1, obj2) {
    // 只有两个对象的key和value完全相等时这两个对象才相等
    const keys1 = Object.keys(obj1)
    const keys2 = Object.keys(obj2)
    if (keys1.length !== keys2.length) {
      return false
    }
    for (let i = 0; i < keys1.length; i++) {
      const key = keys1[i]
      if (obj1[key] !== obj2[key]) {
        return false
      }
    }
    return true
  }

  // 返回一个数组中包含某个对象的元素
  hasObjectData (data, obj) {
    return data.filter(item => {
      return Object.keys(obj).reduce((pr, cr) => {
        if (pr) {
          return obj[cr] === item[cr]
        } else {
          return false
        }
      }, true)
    })
  }
  // 返回结果
  group () {
    // 拿到keys
    const keys = this.getKeys(this.data, this.name)
    // console.log(keys)
    return keys.map(item => {
      // 拿到keys对应的值
      const vals = this.hasObjectData(this.data, item)
      return {
        key: item,
        value: vals
      }
    })
  }
}

