<template>
  <span>
    <span class="content">{{ decoded ? decodedStr : '*************'}}</span>
    <Icon @click="decode" v-show="!decoded" class="icon" type="ios-eye" />
    <Icon v-show="decoded" @click="decoded = false" class="icon" type="ios-eye-off" />
  </span>
</template>

<script>
export default {
  props: {
    content: {
      type: String
    }
  },
  data () {
    return {
      decoded: false,
      decodedStr: ''
    }
  },
  methods: {
    decode () {
      if (!this.decodedStr) {
        const dec = window.atob(this.content)
        this.$nextTick(() => {
          this.decodedStr = dec
        })
      }
      this.decoded = true
    }
  }
}
</script>

<style lang="less" scoped>
.icon {
  cursor: pointer;
  font-size: 20px;
}

.content{
  display: inline-block;
  min-width: 100px;
}
</style>
