import {
  getAreaList,
  getCityList,
  getYearList,
  getLinkage,
  getServiceCenterList,
  getVenueList,
  getClassType,
  getTeacherByNameFuzzy
} from '@/api/bd'
import Cookie from 'js-cookie'
import {
  queryServiceCenterByEmpNo
} from '@/api/clazz'

// import { getRefundFistLevel, getRefundSecondLevel } from '@/api/refund'
// import {
//   getRefundType,
//   operationPlatform,
//   changeClassCourseOperationPlatform
// } from '@/api/reason'
// import { getChangeClassType, queryChangeCourseType } from '@/api/register'

export default {
  data () {
    return {
      allCityList: [], // 分校
      yearList: [], // 年份
      termList: [], // 学期
      subjectList: [], // 学科
      gradeTypeList: [], // 年部
      gradeList: [], // 年级
      levelList: [], // 班次
      areaList: [], // 区域
      serviceList: [], // 服务中心
      teacherDotList: [], // 教学点
      classTypeList: [], // 授课类型
      teacherNameList: [], // 老师名字
      tutorNameList: [], // 辅导老师
      loading1: false, // 级联加载中
      // 退费相关
      refundReason: [], // 一级退费原因
      secodeRefundReason: [], // 二级退费原因
      refundType: [], // 退费类型
      operationPlatform: [], // 平台类型
      changeClassType: [], // 转班原因类型
      changeCourseType: [], // 调课类型
      classFlag: [
        // 原班、现班级
        {
          value: 0,
          label: '原班'
        },
        {
          value: 1,
          label: '现班'
        }
      ],
      // 表单字段，可在页面重新定义覆盖
      formData: {
        type: 0,
        typeName: '退费原因',
        cityCode: '', // 必填
        year: new Date().getFullYear() + '', // 必填
        termId: '', // 学期
        gradeTypeId: '', // 年部
        gradeId: '', // 年级
        subjectId: '', // 学科
        levelId: '', // 班次
        areaId: '', // 区域
        serviceCenterId: '', // 服务中心
        venueId: '', // 教学点
        teacherId: '', // 主讲老师
        tutorId: '', // 辅导老师
        startDate: '', // 开课日期
        startDateVal: '',
        endDate: '',
        endDateVal: '',
        secondReasonId: '', // 二级原因
        firstReasonId: '', // 一级原因
        refundType: '', // 退费类型
        operatePlatform: '', // 平台
        operateStartDate: '',
        operateStartDateVal: '',
        operateEndDate: '',
        operateEndDateVal: '',
        classFlag: '', // 原班、现班
        totalCount: 0,
        pageSize: 10,
        pageNo: 1
      },
      formDataSource: {
        type: 0,
        typeName: '退费原因',
        cityCode: '', // 必填
        year: new Date().getFullYear() + '', // 必填
        termId: '', // 学期
        gradeTypeId: '', // 年部
        gradeId: '', // 年级
        subjectId: '', // 学科
        levelId: '', // 班次
        areaId: '', // 区域
        serviceCenterId: '', // 服务中心
        venueId: '', // 教学点
        teacherId: '', // 主讲老师
        tutorId: '', // 辅导老师
        startDate: '', // 开课日期
        startDateVal: '',
        endDate: '',
        endDateVal: '',
        secondReasonId: '', // 二级原因
        firstReasonId: '', // 一级原因
        refundType: '', // 退费类型
        operatePlatform: '', // 平台
        operateStartDate: '',
        operateStartDateVal: '',
        operateEndDate: '',
        operateEndDateVal: '',
        classFlag: '', // 原班、现班
        totalCount: 0,
        pageSize: 10,
        pageNo: 1
      },
      setDefaultServiceCenter: false // 设置默认值
    }
  },
  methods: {
    // 日期范围选择
    optDateChange (val, start, end) {
      this.formData[start] = val[0]
      this.formData[end] = val[1]
    },
    // 调课原因类型
    getChangeCourseTypeHandler () {
      const params = {
        cityCode: this.formData.cityCode
      }
      queryChangeCourseType(params)
        .then(res => {
          let arr = []
          res.forEach(item => {
            arr.push({
              value: item.chanageCourseTypeId,
              label: item.changeCourseTypeName,
              desc: item.description
            })
          })
          this.changeCourseType = Object.freeze(arr)
        })
        .catch(err => {
          this.$Message.warning({
            content: '获取转班原因失败:' + err.message,
            duration: 3
          })
        })
    },
    // 转班原因类型
    getChangeClassTypeHandler () {
      const params = {
        cityCode: this.formData.cityCode
      }
      getChangeClassType(params)
        .then(res => {
          let arr = []
          res.forEach(item => {
            arr.push({
              value: item.chanageClassTypeId,
              label: item.changeClassTypeName,
              desc: item.description
            })
          })
          this.changeClassType = Object.freeze(arr)
        })
        .catch(err => {
          this.$Message.warning({
            content: '获取转班原因失败:' + err.message,
            duration: 3
          })
        })
    },
    // 平台类型
    operationPlatformHandler () {
      let apiOperationPlatform
      if (!this.formFields) {
        return
      } else {
        if (this.formFields.has('refundType')) {
          apiOperationPlatform = operationPlatform
        } else {
          apiOperationPlatform = changeClassCourseOperationPlatform
        }
      }

      apiOperationPlatform()
        .then(res => {
          this.operationPlatform = Object.freeze(res)
        })
        .catch(err => {
          this.$Message.warning({
            content: '获取平台类型失败:' + err.message,
            duration: 3
          })
        })
    },
    // 退费类型
    getRefundTypeHandler () {
      getRefundType()
        .then(res => {
          this.refundType = Object.freeze(res)
        })
        .catch(err => {
          this.$Message.warning({
            content: '获取退费类型失败:' + err.message,
            duration: 3
          })
        })
    },
    changeFirstReason (firstReasonId) {
      this.secodeRefundReason = []
      this.formData.secondReasonId = ''
      if (!firstReasonId) return false

      const items = this.refundReason.filter(
        item => item.value === firstReasonId
      )

      if (items && items[0]) {
        if (!items[0].children) {
          this.getRefundSecondLevelHandler(items[0])
        } else {
          this.secodeRefundReason = [...items[0].children]
        }
      }
    },
    buildReason (res, hasChild) {
      let _res = []
      res.forEach(obj => {
        let _d = {
          value: obj.id,
          label: obj.reason,
          desc: obj.description
        }
        _res.push(_d)
      })
      return _res
    },
    // 二级退费原因
    getRefundSecondLevelHandler (item) {
      getRefundSecondLevel({
        parentId: item.value,
        cityCode: this.formData.cityCode
      })
        .then(res => {
          if (res && res.length) {
            item.children = this.buildReason(res)
            this.secodeRefundReason = [...item.children]
          }
        })
        .catch(err => {
          this.$Message.warning({
            content: '获取二级退费原因失败:' + err.message,
            duration: 3
          })
        })
    },
    // 一级退费原因
    getRefundFistLevelHandler () {
      getRefundFistLevel({
        cityCode: this.formData.cityCode
      })
        .then(res => {
          if (res && res.length) {
            this.refundReason = this.buildReason(res, true)
          }
        })
        .catch(err => {
          this.$Message.warning({
            content: '获取一级退费原因失败:' + err.message,
            duration: 3
          })
        })
    },
    //   表单查询所需数据
    // 获取所有城市
    getCitys () {
      getCityList()
        .then(data => {
          this.allCityList = data
          if (this.allCityList.length && this.setDefaultServiceCenter) {
            this.formData.cityCode = data[0].id
            this.getBaseData()
            this.cityYearChange(this.formData.cityCode)
          }
        })
        .catch(err => {
          this.$Message.warning({
            content: '获取所有城市失败:' + err.message,
            duration: 5
          })
        })
    },
    // 获取年份
    getYears () {
      getYearList()
        .then(data => {
          this.yearList = data
        })
        .catch(err => {
          this.$Message.warning({
            content: '获取年份失败:' + err.message,
            duration: 5
          })
        })
    },
    // 授课类型
    getClassTypeList () {
      let req = {}
      getClassType(req)
        .then(res => {
          this.classTypeList = res
        })
        .catch(err => {
          this.$Message.warning({
            content: '获取授课类型失败:' + err.message,
            duration: 5
          })
        })
    },
    // 基本数据
    getBaseData () {
      // 查询类型：t 学期，gt 年部，g 年级，s 学科,cd 班次级别，c 班次
      // cityCode
      let toNum = {
        cityCode: this.formData.cityCode
      }
      getAreaList(toNum)
        .then(res => {
          this.areaList = res
          // 带入当前顾问所属区域、服务中心
          if (this.setDefaultServiceCenter) {
            queryServiceCenterByEmpNo({
              cityCode: this.formData.cityCode,
              empNo: JSON.parse(Cookie.get('columbusUserInfo')).id
            }).then(data => {
              if (this.areaList.filter(a => a.schoolId === data.areaId).length > 0) {
                this.formData.areaId = data.areaId
                this.changeArea(data.areaId, data.serviceCenterId)
              } else {
                this.formData.areaId = ''
                this.serviceCenterList = []
                this.formData.serviceCenterId = ''
              }
            }).catch(err => {
              this.$Message.error('获取当前所在服务中心失败:' + err.message)
            })
          }
        })
        .catch(err => {
          this.$Message.error('获取区域失败:' + err.message)
        })
    },
    // 改变城市
    cityYearChange (data) {
      console.log('city change', data)
      this.formData.termId = ''
      this.formData.gradeTypeId = ''
      this.formData.gradeId = ''
      this.formData.levelId = ''
      this.formData.classType = null
      this.formData.teacherId = ''
      this.formData.tutorId = ''
      this.formData.subjectId = ''
      this.formData.areaId = ''
      this.formData.serviceCenterId = ''
      this.formData.venueId = ''
      this.formData.secondReasonId = ''
      this.formData.firstReasonId = ''
      this.formData.refundType = ''
      this.formData.operatePlatform = ''
      if (!data) {
        return
      }
      this.getBaseData()
      let baseTerm = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        queryType: 't'
      }
      getLinkage(baseTerm)
        .then(res => {
          this.termList = res
        })
        .catch(err => {
          this.$Message.error('获取学期失败:' + err.message)
        })

      if (this.formFields) {
        if (this.formFields.has('refundType')) {
          this.getRefundFistLevelHandler()
          this.getRefundTypeHandler()
        }

        if (this.formFields.has('changeClassType')) {
          this.getChangeClassTypeHandler()
        }

        if (this.formFields.has('changeCourseType')) {
          this.getChangeCourseTypeHandler()
        }
      }

      // 获取操作平台类型- 退/转/调查询使用
      // this.operationPlatformHandler()
    },
    // 改变年份
    changeYear (val) {
      this.gradeTypeList = []
      this.formData.gradeTypeId = ''
      this.termList = []
      this.formData.termId = ''
      this.formData.gradeId = ''
      this.gradeList = []
      this.formData.subjectId = ''
      this.subjectList = []
      this.formData.levelId = ''
      this.levelList = []
      if (!val) {
        return
      }
      let baseTerm = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        queryType: 't'
      }
      getLinkage(baseTerm)
        .then(res => {
          this.termList = res
        })
        .catch(err => {
          this.$Message.error('获取学期失败:' + err.message)
        })
    },
    // 改变学期
    changeTerm (val) {
      this.formData.gradeTypeId = ''
      this.formData.gradeId = ''
      this.formData.subjectId = ''
      this.formData.levelId = ''
      this.gradeTypeList = []
      this.gradeList = []
      this.subjectList = []
      this.levelList = []
      if (!val) {
        return false
      }
      let req = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        termIds: this.formData.termId,
        queryType: 'gt'
      }
      getLinkage(req)
        .then(res => {
          this.gradeTypeList = res
        })
        .catch(err => {
          this.$Message.error('获取年部失败:' + err.message)
        })
    },
    // 改变年部
    gradeTypeChange (val) {
      this.formData.gradeId = ''
      this.formData.subjectId = ''
      this.formData.levelId = ''
      this.gradeList = []
      this.subjectList = []
      this.levelList = []
      if (!val) {
        return false
      }
      let req = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        termIds: this.formData.termId,
        gtIds: this.formData.gradeTypeId,
        queryType: 'g'
      }
      getLinkage(req)
        .then(res => {
          this.gradeList = res
        })
        .catch(err => {
          this.$Message.error('获取年级失败:' + err.message)
        })
    },
    // 改变年级
    changeClasses (val) {
      this.formData.subjectId = ''
      this.subjectList = []
      this.formData.levelId = ''
      this.levelList = []
      if (!val) {
        return
      }
      let baseSubject = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        termIds: this.formData.termId,
        gradeIds: this.formData.gradeId,
        queryType: 's'
      }
      getLinkage(baseSubject)
        .then(res => {
          this.subjectList = res
        })
        .catch(err => {
          this.$Message.error('获取学科失败:' + err.message)
        })
    },
    // 改变学科
    changeSubject (val) {
      this.formData.levelId = ''
      this.levelList = []
      if (!val) {
        return
      }
      let baseClassCount = {
        cityCode: this.formData.cityCode,
        years: this.formData.year,
        termIds: this.formData.termId,
        gradeIds: this.formData.gradeId,
        subjectIds: this.formData.subjectId,
        queryType: 'c'
      }
      getLinkage(baseClassCount)
        .then(res => {
          this.levelList = res
        })
        .catch(err => {
          this.$Message.error('获取班次失败:' + err.message)
        })
    },
    // 改变区域
    changeArea (val, serviceCenterId) {
      this.serviceList = []
      this.formData.serviceCenterId = ''
      if (val === 'undefined' || !this.formData.areaId) {
        return false
      }
      let ser = {
        cityCode: this.formData.cityCode,
        areaId: this.formData.areaId
      }
      getServiceCenterList(ser)
        .then(res => {
          this.serviceList = res
          this.serviceList.forEach((value, index) => {
            if (!value.address) {
              value.address = ''
            }
          })

          if (serviceCenterId) {
            this.formData.serviceCenterId = serviceCenterId
          }
        })
        .catch(err => {
          this.$Message.warning('获取服务中心失败:' + err.message)
        })
    },
    // 改变服务中心
    changeService (val) {
      this.teacherDotList = []
      this.formData.venueId = ''
      if (
        val === 'undefined' ||
        !this.formData.serviceCenterId ||
        !this.formData.areaId
      ) {
        return false
      }
      let serChange = {
        cityCode: this.formData.cityCode,
        serviceId: this.formData.serviceCenterId
      }
      getVenueList(serChange)
        .then(res => {
          this.teacherDotList = res
        })
        .catch(err => {
          this.$Message.warning('获取教学点失败:' + err.message)
        })
    },
    // 主讲老师
    getTeacherName (query) {
      if (!this.formData.cityCode) {
        this.$Message.error('未选择城市')
        return
      }
      query = query.trim()
      if (!query || query === this.oldSearchName) {
        return
      }
      this.loading1 = true
      this.teacherNameList = []
      let req = {
        sysName: query,
        teacherType: 0,
        cityCode: this.formData.cityCode,
        businessType: this.formData.classType,
        pageNo: 1,
        pageSize: 20
      }
      getTeacherByNameFuzzy(req)
        .then(res => {
          this.teacherNameList = res.records
          this.loading1 = false
        })
        .catch(err => {
          this.loading1 = false
          this.$Message.error('获取主讲老师姓名失败:' + err.message)
        })
    },
    // 辅导老师
    getTutorName (query) {
      if (!this.formData.cityCode) {
        this.$Message.error('未选择城市')
        return
      }
      query = query.trim()
      if (!query || query === this.oldSearchName) {
        return
      }
      this.loading1 = true
      this.tutorNameList = []
      let req = {
        sysName: query,
        teacherType: 1,
        cityCode: this.formData.cityCode,
        businessType: this.formData.classType,
        pageNo: 1,
        pageSize: 20
      }
      getTeacherByNameFuzzy(req)
        .then(res => {
          this.tutorNameList = res.records
          this.loading1 = false
        })
        .catch(err => {
          this.loading1 = false
          this.$Message.error('获取辅导老师姓名失败:' + err.message)
        })
    },
    optionChange (val) {
      if (val && val.label) {
        this.oldSearchName = val.label
      }
    },

    getClassTypeName (val) {
      return this.classTypeMap[val]
    },
    // 开课日期、结课日期改变
    startDateChange (val, startName = 'startDate', endName = 'endDate') {
      if (this.formData[endName]) {
        if (
          new Date(val).getTime() > new Date(this.formData[endName]).getTime()
        ) {
          this.$Message.warning({
            content: '结束时间不得小于开始时间',
            duration: 5
          })
          this.formData[startName] = ''
          this.formData[startName + 'Val'] = ''
          return
        }
      }
      this.formData[startName] = val
    },
    endDateChange (val, startName = 'startDate', endName = 'endDate') {
      if (this.formData[startName]) {
        if (
          new Date(this.formData[startName]).getTime() > new Date(val).getTime()
        ) {
          this.$Message.warning({
            content: '结束时间不得小于开始时间',
            duration: 5
          })
          this.formData[endName] = ''
          this.formData[endName + 'Val'] = ''
          return
        }
      }
      this.formData[endName] = val
    }
  }
}
