// 基础数据筛选项初始化
import {
  // queryTermListForBaseData,
  getYearList,
  getGradeList,
  getSubjectList,
  getServiceCenterList,
  getVenueList,
  getTimeTypeList,
  getTimeList,
  getTeacherByNameFuzzy,
  getCityList
} from '@/api/bd'
// import { queryLevelDegreeList } from '@/api/assembleClass'
import {
  getOne,
  // courseLinkageLink,
  combineList
} from '@/api/baseData'

export default {
  data () {
    return {
      cityList: [], // 城市分校
      yearList: [], // 年份
      seasonList: [], // 学季
      gradeList: [], // 年级
      subjectList: [], // 学科
      districtList: [], // 地区
      serviceCenterList: [], // 服务中心
      advisorEmpNos: [], // 顾问列表
      classLevelList: [], // 班次级别
      timeTypeList: [], // 上课时间
      timeList: [], // 上课时段
      sClassWayList: {}, // 上课模式-small-小班
      lClassWayList: {} // 上课模式-large-大班
      // combineListData: [] // 全量数据
    }
  },
  methods: {
    // 获取所在城市
    async getCityListHandler (cb) {
      try {
        const data = await getCityList()
        this.cityList = data
        cb && cb(data)
      } catch (err) {
        this.$Message.warning({ content: '获取所有城市失败:' + err.message, duration: 5 })
      }
    },
    // 获取年级
    async getGradeList () {
      try {
        const params = {
          cityCode: this.formData.cityCode
        }
        const data = await getGradeList(params)
        this.gradeList = Object.freeze(data)
      } catch (error) {
        this.$Message.warning({
          content: '获取年级失败:' + error.message,
          duration: 5
        })
      }
    },
    // 获取学科
    async getSubjectList () {
      try {
        const params = {
          cityCode: this.formData.cityCode
        }
        const data = await getSubjectList(params)
        this.subjectList = Object.freeze(data)
      } catch (error) {
        this.$Message.warning({ content: '获取学科失败:' + error.message, duration: 5 })
      }
    },
    getTimeList () {
      getTimeList().then(res => {
        this.timeList = Object.freeze(res)
      }).catch(error => {
        this.$Message.error({ content: `获取上课时段失败: ${error.message}`, duration: 5 })
      })
    },
    // 获取上课时间类型 / 上课时间(星期) 周一 / 周二 ...
    getTimeTypeList () {
      getTimeTypeList().then(res => {
        this.timeTypeList = Object.freeze(res)
      }).catch(error => {
        this.$Message.error({ content: `获取上课时间类型失败: ${error.message}`, duration: 5 })
      })
    },
    // 获取服务中心
    async getServiceCenterList (likeName, cityCode) {
      try {
        const params = {
          cityCode,
          likeName
        }
        const data = await getServiceCenterList(params)
        this.serviceCenterList = Object.freeze(data) || []
      } catch (error) {
        this.$Message.warning({
          content: '获取服务中心失败:' + error.message,
          duration: 5
        })
      }
    },
    // 获取主讲教师
    getTeacherByNameFuzzyHandler (params) {
      return getTeacherByNameFuzzy(params)
    },
    // 获取教学点
    getVenueListHandler (params) {
      return getVenueList(params)
    },
    // 获取年份
    async getYears () {
      try {
        const data = await getYearList()
        this.yearList = Object.freeze(data)
      } catch (error) {
        this.$Message.warning({
          content: '获取年份失败:' + error.message,
          duration: 5
        })
      }
    },
    async getBaseDataSeasonList () {
      try {
        const params = {
          enName: 'season'
        }
        const data = await getOne(params)
        this.seasonList = Object.freeze(JSON.parse(data.value))
      } catch (error) {
        this.$Message.error({ content: '获取学期失败:' + error.message, duration: 5 })
      }
    },
    // 字典接口-获取基础数据
    async getCombineList () {
      try {
        const params = {
          schoolCode: this.formData.cityCode,
          // yearsReq: {}, // 获取年份
          // gradesReq: {},
          // subjectsReq: {},
          levelDegreesReq: {},
          propertiesReq: {
            enNames: [ // 字典
              // 'period', // 上课时段
              // 'categoryType', // 业务类型
              // 'productType', // 产品类型
              // 'season', // 产品类型
              'classWay' // 上课模式
            ]
          }
        }
        const data = await combineList(params)
        // console.log('$$$ data combineLIst ', data)
        this.classLevelList = data.levelDegrees
        const properties = data.properties
        if (properties) {
          this.sClassWayList = JSON.parse(properties[0].value).splice(0, 2)
          this.sClassWayList.forEach(item => {
            item.id = item.id.toString()
          })
          this.lClassWayList = JSON.parse(properties[0].value).splice(0, 3)
          this.lClassWayList.forEach(item => {
            item.id = item.id.toString()
          })
          // console.log('%%%% sClassWayList', this.sClassWayList)
          // console.log('%%%% lClassWayList', this.lClassWayList)
        }
      } catch (err) {
        this.$Message.error({ content: '获取combineList失败:' + err.message, duration: 5 })
      }
    }
  }
}
