      // this.getBaseDataTermList()
      this.getBaseDataSeasonList()
      this.getYears()
      this.getSubjectList()
      this.getGradeList()
      // this.queryLevelDegreeList()
      this.getTimeTypeList()
      this.getTimeList()
      this.getAreaList()
      this.getCombineList()
      this.queryClassModelList()
      this.queryClassModelLevelList()
    },
    // 授课类型切换-移除校验规则
    handleCategoryTypeChange () {
      if (this.formData.categoryType === 1) { // 在线
        this.ruleValidate.classNum.unshift({ required: true, message: '请选择班级个数', trigger: 'change', type: 'number' })
        this.ruleValidate.classWay.unshift({ required: true, message: '请选择上课模式', trigger: 'change' })
      } else if (this.formData.categoryType === 2) { // 双师
        this.ruleValidate.classNum.unshift({ required: true, message: '请选择班级个数', trigger: 'change', type: 'number' })
        this.ruleValidate.classWay.shift()
      } else if (this.formData.categoryType === 4) { // 面授
        this.ruleValidate.classNum.shift()
        this.ruleValidate.classWay.shift()
      }
      this.formData.teacherEmpNo = ''
      this.formData.classroomId = ''
      this.teacherNameList = []
      this.classroomList = []
      clearTimeout(this.timer)
      this.timer = setTimeout(() => {
        this.classType = 1
        this.classroomLoading = true
        // 1-在线 2-双师 4-面授
        // classType 1:普通教室 2:双师教室 3:直播间
        if (this.formData.categoryType === 4) {
          // this.isLive = 0
          this.classType = 1
        } else if (this.formData.categoryType === 1) {
          // this.isLive = 1
          this.classType = 3
        } else if (this.formData.categoryType === 2) {
          // this.isLive = 0
          this.classType = 2
        } else {
          this.classType = 1
        }
        try {
          queryClassroomList({
            schoolCode: this.formData.cityCode,
            serviceid: this.formData.serviceCenterId,
            venueId: this.formData.teachingPointCode,
            // isLive: this.isLive,
            classType: this.classType
          }).then(res => {
            this.classroomList = res
            this.classroomLoading = false
          })
        } catch (error) {
          this.$Message.error({ content: '获取所选教学点下教室失败: ' + error.message, duration: 5 })
        }
      }, 400)
    },
    handleCatChangeToReset () {
      this.formData.teacherEmpNo = ''
      this.formData.classroomId = ''
      this.teacherNameList = []
      this.classroomList = []
    },
    // 产品类型改变
    handleProductTypeChange () {
      if (this.formData.productType === '1') {
        this.formData.productTypeName = '长期班'
      } else if (this.formData.productType === '2') {
        this.formData.productTypeName = '短期班'
      }
    },
