        this.getClassTimeList(currentId)
      }
    },
    getClassTimeList (currentId) { // 获取上课时间列表
      this.classTimeListLoading = true
      queryClassTimeList({
        schoolCode: this.formData.cityCode,
        periodType: currentId,
        status: 0
      }).then(res => {
        this.classTimeList = res
      }).catch(() => {
        this.$Message.error('调用上课时间接口失败')
      }).finally(() => {
        this.classTimeListLoading = false
      })
    },
    // 改变班次级别
    changeClassLevel () {
      this.levelDegreeList = [] // 版本数组
      this.formData.classLevel = ''
      classlevelList({
        schoolCode: this.formData.cityCode,
        status: 0,
        degree: this.formData.levelDegree,
        current: 1,
        disable: 0,
        size: 100,
        name: ''
      }).then(res => {
        this.levelDegreeList = res.records
      }).catch(err => {
        this.$Message.error('获取班次级别下版本失败：' + err.message)
      })
    },
    // 模糊查询主讲老师
    remoteListTeacherName (name) {
      clearTimeout(this.lazyTimer)
      this.lazyTimer = setTimeout(() => {
        this.getTeacher(name)
      }, 400)
    },
    // 获取班型难度
    queryClassModelLevelList () {
      getClassModelLevelList({
        schoolCode: this.formData.cityCode,
        likeName: this.formData.classModelLevelName,
        current: this.pageCurrent,
        size: this.pageSize
      }).then(res => {
        this.classModelLevelList = res.records || []
      }).catch(() => {
        this.$Message.error('请求班型难度列表接口失败')
      }).finally(() => {
        this.tableLoading = false
      })
    },
    // 获取班型名称
    queryClassModelList () {
      getClassModelList({
        schoolCode: this.formData.cityCode,
        likeName: this.formData.classModelName,
        current: this.pageCurrent,
        size: this.pageSize
      }).then(res => {
        this.classModelNameList = res.records || []
      }).catch(() => {
        this.$Message.error('请求班型名称列表接口失败')
      }).finally(() => {
        this.tableLoading = false
      })
    },
    // 主讲老师搜索
    getTeacher (query) {
      if (query) {
        query = query.trim()
      } else {
        return
      }
      this.loadingTeacherName = true
      this.teacherNameList = []
      let req = {
        sysName: query,
        teacherType: 0,
        cityCode: this.formData.cityCode,
        businessType: this.formData.categoryType,
        pageNo: 1,
        pageSize: 20
      }
      this.getTeacherByNameFuzzyHandler(req).then(res => {
        if (res.records && res.records.length) {
          this.teacherNameList = res.records
        }
        this.loadingTeacherName = false
      }).catch(err => {
        this.loadingTeacherName = false
        this.$Message.error('获取主讲老师姓名失败:' + err.message)
      })
    },
    // 改变地区/区域
    changeArea () {
      this.serviceCenterList = []
      this.formData.serviceCenterId = ''
      this.serviceCenterLoading = true
      try {
        getServiceCenterList({
          cityCode: this.formData.cityCode,
          areaId: this.formData.areaId
        }).then(res => {
          this.serviceCenterList = res
          this.serviceCenterLoading = false
        })
      } catch (error) {
        this.$Message.error({ content: '获取所选区域下服务中心失败: ' + error.message, duration: 5 })
      }
    },
    // 改变服务中心
    changeService (val) {
      this.venueList = []
      this.formData.teachingPointCode = ''
      this.teachingPointLoading = true
      if (!val) {
        return false
      }
      let req = {
        cityCode: this.formData.cityCode,
        serviceId: this.formData.serviceCenterId
      }
      this.getVenueListHandler(req).then(res => {
        this.venueList = res
        this.teachingPointLoading = false
      }).catch(err => {
        this.$Message.warning('获取所选服务中心下教学点失败:' + err.message)
      })
    },
    changeVenue () {
      clearTimeout(this.lazyTimer)
      this.lazyTimer = setTimeout(() => {
        this.changeVenueSearch()
      }, 400)
    },
    // 改变教学点
    changeVenueSearch () {
      this.classType = 1
      this.classroomList = []
      this.formData.classroomId = ''
      this.classroomLoading = true
      // 1-在线 2-双师 4-面授
      // classType 1:普通教室 2:双师教室 3:直播间
      if (this.formData.categoryType === 4) {
        this.isLive = 0
        this.classType = 1
      } else if (this.formData.categoryType === 1) {
        this.isLive = 1
        this.classType = 3
      } else {
        this.isLive = 0
        this.classType = 2
      }
      try {
        queryClassroomList({
          schoolCode: this.formData.cityCode,
          serviceid: this.formData.serviceCenterId,
          venueId: this.formData.teachingPointCode,
          // isLive: this.isLive,
          classType: this.classType
        }).then(res => {
          this.classroomList = res
          this.classroomLoading = false
        })
      } catch (error) {
        this.$Message.error({ content: '获取所选教学点下下教室失败: ' + error.message, duration: 5 })
      }
    },
    // 获取所有地区/区域
    async getAreaList () {
      try {
        this.areaLoading = true
        const params = { cityCode: this.formData.cityCode }
        const data = await getAreaList(params)
        this.areaLoading = false
        this.areaList = Object.freeze(data)
      } catch (error) {
        this.$Message.error({
          content: `获取区域列表失败: ${error.message}`,
          duration: 5
        })
      }
    },
    // 模糊查询版本
    remoteListLinkName (name) {
      clearTimeout(this.lazyTimer)
      this.lazyTimer = setTimeout(() => {
        this.remoteList(name)
      }, 400)
    },
    remoteList (name) { // 模糊搜索
      if (!name || name === '') return
      // val = val || this.formData.classLevelName
      classlevelList({
        schoolCode: this.formData.cityCode,
        status: 0,
        degree: this.formData.levelDegree,
        current: 1,
        disable: 0,
        size: 100,
        name: name || this.formData.classLevelName
      }).then(res => {
        this.levelDegreeList = res.records
      }).catch(err => {
        this.$Message.error('获取班次级别下版本失败：' + err.message)
      })
    },
    // 设置传参数据中的 categoryTypeName
    setCategoryTypeName (name) {
      this.formData.categoryTypeName = name
    },
    // 设置班型名称
    setCurrentClassModelId (id) {
      this.formData.classModel = id
    },
    // 设置班型难度
    setCurrentClassModelLevelId (id) {
      this.formData.classModelLevelId = id
    },
    // 设置传参数据中的 gradeName
    setCurrentGradeName (standardName) {
      this.formData.gradeName = standardName
    },
    // 设置传参数据中的 subjectName
    setCurrentSubjectName (standardName) {
      this.formData.subjectName = standardName
    },
    // 设置传参数据中的 classLevelName
    setCurrentClassLevelName (name) {
      this.formData.levelDegreeName = name
    },
    // 设置传参数据中的 levelDegreeName
    setCurrentLevelDegreeName (name) {
      this.formData.classLevelName = name
    },
    // 设置传参数据中的 teacherEmpName
    setCurrentTeacherName (teacherEmpName, teaId) {
      this.formData.teacherEmpName = teacherEmpName
      this.formData.teaId = teaId
    },
    // 设置传参数据中的 seasonName
    setCurrentSeasonName (standardName) {
      this.formData.seasonName = standardName
    },
    // 设置传参数据中的 ClassBeginTime 以及 classTimeTypeId 以及 classTimeTypeName
    setCurrentClassBeginTime () {
      if (this.formData.originalClassBeginTime !== '') {
        this.formData.classBeginTime = this.formData.originalClassBeginTime
        let year = this.formData.classBeginTime.getFullYear()
        let month = this.formData.classBeginTime.getMonth() + 1
        let day = this.formData.classBeginTime.getDate()
        if (month < 10) month = '0' + month
        if (day < 10) day = '0' + day
        this.formData.classBeginTime = year.toString() + '-' + month.toString() + '-' + day.toString()
        this.formData.classTimeTypeId = this.classTimeTypeIdMap[this.weekFromStartTime]
        this.formData.classTimeTypeName = this.classTimeTypeNameMap[this.weekFromStartTime]
        let date = this.formData.originalClassBeginTime
        if (date) {
          let week = date.toString().slice(0, 3)
          switch (week) {
            case 'Mon':
              this.weekFromStartTime = '星期一'
              break
            case 'Tue':
              this.weekFromStartTime = '星期二'
              break
            case 'Wed':
              this.weekFromStartTime = '星期三'
              break
            case 'Thu':
              this.weekFromStartTime = '星期四'
              break
            case 'Fri':
              this.weekFromStartTime = '星期五'
              break
            case 'Sat':
              this.weekFromStartTime = '星期六'
              break
            case 'Sun':
              this.weekFromStartTime = '星期日'
              break
            default:
              break
          }
        }
      }
    },
    // 设置传参数据中的 period 上课时段（上午、下午）
    setCurrentPeriod (name) {
      this.formData.period = name
    },
    // 设置上课时间：开始时间、默认时间
    setCurrentCourseTime (startTime, endTime) {
      this.formData.startTime = startTime
      this.formData.endTime = endTime
    },
    // 设置传参数据中的 areaName 区域名称
    setCurrentAreaName (name) {
      this.formData.areaName = name
    },
    // 设置传参数据中的 serviceCenterName 服务中心名称
    setCurrentServiceCenterName (name) {
      this.formData.serviceCenterName = name
    },
    // 设置传参数据中的 teachingPointName 教学点名称
    setCurrentTeachingPointName (name) {
      this.formData.teachingPointName = name
    },
    // 设置传参数据中的 classroomName 教室名称
    setCurrentClassroomName (name) {
      this.formData.classroomName = name
    },
    // 设置传参数据中的 capacityLabelName 容量类别名称
    setCurrentCapacityLabelName () {
      this.formData.capacityLabelName = this.capacityLabelNameMap[this.formData.capacityLabel]
    },
    // 设置传参数据中的 assembleEndTime 攒班截止日期
    setCurrentAssembleEndTime () {
      if (this.formData.deadLine !== '') {
        let assembleYear = this.formData.deadLine.getFullYear()
        let assembleMonth = this.formData.deadLine.getMonth() + 1
        let assembleDay = this.formData.deadLine.getDate()
        let hour = this.formData.deadLine.getHours()
        let minute = this.formData.deadLine.getMinutes()
        let second = this.formData.deadLine.getSeconds()
        if (assembleMonth < 10) assembleMonth = '0' + assembleMonth
        if (assembleDay < 10) assembleDay = '0' + assembleDay
        if (hour < 10) hour = '0' + hour
        if (minute < 10) minute = '0' + minute
        if (second < 10) second = '0' + second
        this.formData.assembleEndTime = assembleYear.toString() + '-' + assembleMonth.toString() + '-' + assembleDay.toString() + ' ' + hour.toString() + ':' + minute.toString() + ':' + second.toString()
      }
    },
    // 设置传参数据中的 classWayName 上课模式名称
    setCurrentClassWayName (name) {
      this.formData.classWayName = name
    },
    // 校验表单所有必填项
    confirm () {
      // console.log(this.formData,'mm')
      this.$refs.formValidate.validate(valid => {
        if (valid) {
          this.handlecreationConfirmed()
        }
      })
    },
    // 点击取消-提示用户
    cancel () {
      this.$Modal.confirm({
        title: '确认取消？',
        content: '<p>取消编辑将不会保存数据</p>',
        onOk: () => {
          this.closeModal()
        }
      })
    },
    // 确认 新建班级
    handlecreationConfirmed () {
      this.formData.type = this.type // 1 - 新增 2 - 修改
      this.formData.empNo = this.userInfo.empNo
      this.formData.empName = this.userInfo.empName
      // this.formData.levelDegree = this.formData.levelDegree.toString()
      // this.formData.classWay = this.formData.classWay.toString()
      this.setCurrentClassBeginTime()
      this.modalLoading = true
      addOrInsertAssemble(this.formData).then(res => {
        if (this.formData.type === 1) {
          this.$Message.success({ content: '新建班级成功', duration: 5 })
        } else {
          this.$Message.success({ content: '修改班级成功', duration: 5 })
        }
        console.log('修改班级成功 ', this.formData)
        this.showAddClassModal = false
        this.$emit('updateTableData')
      }).catch(error => {
        if (this.formData.type === 1) {
          this.$Message.error({ content: '新建班级失败:' + error.message, duration: 5 })
        } else {
          this.$Message.error({ content: '修改班级失败:' + error.message, duration: 5 })
        }
      })
      this.modalLoading = false
    },
    // 修改/编辑班级 获取老数据展示在新建班级页面 回显接口数据
    async getOldAssembleInfoById (val) {
      this.modalLoading = true
      try {
        const params = {
          id: val.id,
          cityCode: val.cityCode
        }
        getAssembleInfo(params).then(res => {
          this.formData = { ...res }
          this.formData.seasonId = Number(res.seasonId)
          this.initCityList()
          this.formData.originalClassBeginTime = new Date(res.classBeginTime)
          this.setCurrentClassBeginTime()
          this.formData.deadLine = new Date(res.assembleEndTime)
          this.formData.periodId = this.periodNameMap[res.period]
          // 直接回显上课时间
          this.classTimeList = [
            { name: res.period + res.classTime }
          ]
          this.formData.courseTime = res.period + res.classTime
          // changeArea方法复写
          this.serviceCenterLoading = true
          try {
            getServiceCenterList({
              cityCode: this.formData.cityCode,
              areaId: this.formData.areaId
            }).then(res => {
              this.serviceCenterList = res
              this.serviceCenterLoading = false
            })
          } catch (error) {
            this.$Message.error({ content: '获取所选区域下服务中心失败: ' + error.message, duration: 5 })
          }
          // changeService方法复写
          this.teachingPointLoading = true
          let req = {
            cityCode: this.formData.cityCode,
            serviceId: this.formData.serviceCenterId
          }
          this.getVenueListHandler(req).then(res => {
            this.venueList = res
            this.teachingPointLoading = false
          }).catch(err => {
            this.$Message.warning('获取所选服务中心下教学点失败:' + err.message)
          })
          // 重新查询教室
          clearTimeout(this.timer)
          // debugger
          this.timer = setTimeout(() => {
            this.classType = 1
            this.classroomLoading = true
            // 1-在线 2-双师 4-面授
            // classType 1:普通教室 2:双师教室 3:直播间
            if (this.formData.categoryType === 4) {
              this.isLive = 0
              this.classType = 1
            } else if (this.formData.categoryType === 1) {
              this.isLive = 1
              this.classType = 3
            } else {
              this.isLive = 0
              this.classType = 2
            }
            try {
              queryClassroomList({
                schoolCode: this.formData.cityCode,
                serviceid: this.formData.serviceCenterId,
                venueId: this.formData.teachingPointCode,
                // isLive: this.isLive,
                classType: this.classType
              }).then(resRoom => {
                this.classroomList = resRoom
                // 如果教室不在该教学点下，则清空教室相关数据，重新查询
                this.isExisted = this.classroomList.some(item => {
                  if (item.name === res.classroomName) return true
                  else return false
                })
                if (!this.isExisted) {
                  this.formData.classroomName = ''
                  this.formData.classroomId = ''
                  this.changeVenue()
                }
                this.classroomLoading = false
              })
            } catch (error) {
              this.$Message.error({ content: '获取所选教学点下下教室失败: ' + error.message, duration: 5 })
            }
          }, 400)
          // 设置版本
          // this.levelDegreeList = [
          //   { schoolId: res.classLevel, name: res.classLevelName }
          // ]
          // 重新查询版本
          if (res.classLevelName) {
            try {
              classlevelList({
                schoolCode: this.formData.cityCode,
                status: 0,
                degree: this.formData.levelDegree,
                current: 1,
                disable: 0,
                size: 100,
                name: res.classLevelName || ''
              }).then(resClassLevel => {
                this.levelDegreeList = resClassLevel.records
                this.isClassLevelExisted = this.levelDegreeList.some(item => {
                  if (item.name === res.classLevelName) return true
                  else return false
                })
                if (!this.isClassLevelExisted) {
                  this.formData.classLevelName = ''
                  this.formData.classLevel = ''
                  this.remoteListLinkName(res.classLevelName)
                }
              })
            } catch (err) {
              this.$Message.error('获取班次级别下版本失败：' + err.message)
            }
          }
          // 设置主讲教师
          this.teacherNameList = [
            { empNo: res.teacherEmpNo, realName: res.teacherEmpName }
          ]
        })
      } catch (error) {
        this.$Message.error({ content: `获取之前攒班详情失败: ${error.message}`, duration: 5 })
      }
      this.modalLoading = false
    },
    clearSelect () {
      this.changeClassLevel()
    }
  }
}
</script>

