<template>
  <!-- 顾问绩效目标管理 -->
  <div class="layout">
    <Tabs :animated="false">
        <TabPane label="服务中心目标" v-if="roleId === 2">
          <TargetTable ref="targetTable1" :type="1" @batch-setting="handleBatchSetting(1)" @setting-record="handleSettingRecord(1)" />
        </TabPane>
        <TabPane label="顾问目标" v-if="roleId === 2 || roleId === 4">
          <TargetTable ref="targetTable2" :type="2" @batch-setting="handleBatchSetting(2)" @setting-record="handleSettingRecord(2)" @advsior-deptid-change="currentService = $event" />
        </TabPane>
    </Tabs>
    <Modal
      v-model="openSettingsRecord"
      title="设置记录"
      width="1100px"
      height="650px"
      :mask-closable="false"
      >
        <div v-if="operateLog.length" style="max-height: 60vh;overflow: auto;">
          <Timeline>
            <TimelineItem v-for="(item, index) in operateLog" :key="index">
              <Icon type="ios-radio-button-on" slot="dot" color="#3b7eff"/>
              <div class="setting-record">
                <p class="operator">
                    {{ item.createEmpName }}（{{ item.createEmpNo }}）
                  <Time style="font-size: 12px; color: rgba(0, 0, 0, 0.45); font-weight: normal;" :time="item.createDate" type="datetime"></Time>
                </p>
                <Table
                  :columns="recordColumns"
                  :data="item.batchLogs"
                  :show-header="false"
                  class="table-no-border"
                >
                <template slot="header">
                  <th class="service-name ivu-table-column-left">
                    <div class="ivu-table-cell" style="width: 1042px;height: 40px;transform: translate(0, -5px);">
                      <span>{{ item.operateType === 1 ? '批量设置' : '编辑'}}</span></div> </th>
                </template>
                </Table>
                <div class="more" v-if="item.batchLogsMore && item.batchLogs.length !== item.batchLogsMore.length">
                  <a href="javascript: void(0);" @click="item.batchLogs = item.batchLogsMore">查看更多</a>
                </div>
                <p class="change-remark" v-if="item.remark">变更备注:{{ item.remark }}</p>
              </div>
            </TimelineItem>
          </Timeline>
        </div>
        <div class="no-data" v-else>
          <img src="@/assets/images/no_data.png" alt="" class="no-data-pic">
        </div>
        <div class="demo-spin-container" v-if="tableConfig.tableLoading">
            <Spin fix></Spin>
        </div>
        <div v-if="operateLog.length" class="flex" style="margin-top: 16px;">
          <span></span>
          <Page
            :total="tableConfig.totalCount"
            :page-size="tableConfig.pageSize"
            :current.sync="tableConfig.pageNo"
            show-elevator
            show-sizer
            show-total
            transfer
            @on-page-size-change="pageSizeChange"
            @on-change="changePage" />
        </div>
        <div slot="footer">
          <Button type="primary" @click="openSettingsRecord = false">关闭</Button>
        </div>
    </Modal>

    <Modal
      v-model="openBatchSettings"
      :title="currentType === 1 ? '服务中心目标设置' : '顾问目标设置'"
      width="650px"
      height="650px"
      :closable="false"
      :mask-closable="false"
      >
        <div class="uploadIndications">
          <h4 class="uploadTitle">上传说明</h4>
          <p>1.模版表头不可更改和删除，上传过程请不要关闭弹窗</p>
          <p>2.上传过程中系统会对内容参数的合法性进行校验，如失败则整个文件上传失败</p>
          <p>3.模板初始化数据，会带有之前已经设置过的数据，未设置过默认为0</p>
          <p>4.已经设置过目标的，如果目标数据发生变更，再次批量设置会覆盖之前数据，请仔细核对</p>
          <p>5.设置时效：仅支持设置“2021年 {{nearlyMonth[0]}}月、2021年{{ nearlyMonth[1] }}月、2021年{{ nearlyMonth[2] }}月”，招新目标。</p>
        </div>
        <Button type="primary" style="marginTop: 12px; marginBottom: 24px;" :loading="downloading" @click="downloadExcel">下载模板</Button>
        <div slot="footer" style="display:flex;justify-content: center;">
          <template v-if="!errorTargetData.length">
            <Button :disabled="uploading" size="large" @click="closeUploadModal" style="margin-right: 20px;">关闭</Button>
            <Button :loading="uploading" :disabled="!(files.length && remark.trim())" size="large" type="primary" @click="handleUploadTargetData">确认上传</Button>
          </template>
          <template v-else>
            <Button size="large" @click="closeUploadModal" style="margin-right: 20px;">关闭</Button>
            <Button size="large" type="primary" @click="errorTargetData = []">重新导入</Button>
          </template>

        </div>
        <template v-if="errorTargetData.length">
          <div class="error-box">
            <p class="flex" style="justify-content: flex-start;align-items: center;">
              <Icon style="color: red;font-size: 20px;font-weight: bold;margin-right: 8px;" type="ios-close-circle-outline" />
              <b>校验失败（{{errorTargetData.length}}）</b>
            </p>
            <div v-for="(item, index) in errorTargetData" :key="index">
              <div class="error-name" v-if="item.log">{{ item.message }}（{{item.log.length}}）</div>
              <ErrorTargetTable v-if="item.log" :type="currentType" :data="item.log" />
            </div>
          </div>
        </template>
        <template v-else>
          <div class="file-upload-box">
            <FileUpload :multiSelection="false" extensions="xls,xlt,xlsx,xltx,xlsb,xlsm,xltm" :maxLength="1" v-model="files" @on-success="handleUploadSuccess"/>
          </div>
          <div class="ivu-form-item-required" style="margin-top: 16px;">
            <b class="ivu-form-item-label" style="font-size: 14px;">变更备注:</b>
            <Input type="textarea" v-model="remark" style="margin-top: 16px;" placeholder="请输入此次变更原因" />
          </div>
        </template>
    </Modal>
  </div>
</template>

<script>
import TargetTable from './target-table.vue'
import FileUpload from './file-upload.vue'
import { getOperateLog, upLoadFile, downloadExcel } from '@/api/targetTask'
import { mapState } from 'vuex'
import Cookie from 'js-cookie'
import util from '@/common/util'
import ErrorTargetTable from './error-target-table.vue'
import { XesAnalyticsTrack } from '@/libs/tal-sensors'

export default {
  name: 'targetMgt',
  components: {
    TargetTable,
    FileUpload,
    ErrorTargetTable
  },
  computed: {
    ...mapState({
      userInfo: (state) => state.roleRelativeData.userInfo,
      roleId: (state) => state.roleRelativeData.userInfo.crmRoleId,
      cityCode: (state) => state.roleRelativeData.userInfo.cityCode
    })
  },
  watch: {
    cityCode () {
      this.initView()
    }
  },
  data () {
    return {
      openBatchSettings: false,
      openSettingsRecord: false,
      recordColumns: [
        {
          title: '批量设置',
          key: 'target',
          align: 'left',
          width: 400,
          className: 'service-name'
          // render: (h, params) => {
          //   return h(
          //     'span',
          //     params.row.serviceName + '-' + params.row.serviceNo
          //   )
          // },
        },
        {
          title: ' ',
          key: 'message',
          align: 'left',
          render: (h, params) => {
            return h('span', params.row.yearMonth + ' ' + params.row.message)
          }
        }
      ],
      uploading: false,
      files: [], // 文件
      operateLog: [], // 操作日志
      remark: '', // 批量上传变更备注
      currentType: null, // 1服务中心  2顾问
      currentService: {}, // 当前顾问目标选择的服务中心
      downloading: false, // 模板下载
      tableConfig: {
        pageSize: 10,
        pageNo: 1,
        totalCount: 0,
        tableLoading: false
      },
      errorTargetData: [],
      nearlyMonth: [1, 2, 3] // 近三个月
    }
  },
  mounted () {
    this.initView()
    XesAnalyticsTrack('15-5010', {
      title: '招新目标管理',
      tab_name: 'tab_name',
      duration_time: 0
    })
    this.getNearlyMonth()
  },
  methods: {
    // 初始化
    initView () {
      this.$nextTick(() => {
        if (this.cityCode && this.roleId) {
          if (this.roleId === 2) {
            // this.$refs.targetTable1.getTableData()
          }

          if (this.roleId === 4 || this.roleId === 2) {
            // this.$refs.targetTable2.getTableData()
          }
        }
      })
    },
    // 获取近3个月
    getNearlyMonth () {
      const currentMonth = new Date().getMonth() + 1
      let nextMonth = currentMonth + 1 > 12 ? 1 : currentMonth + 1
      let nextNextMonth = nextMonth + 1 > 12 ? 1 : nextMonth + 1
      this.nearlyMonth = [currentMonth, nextMonth, nextNextMonth]
    },
    handleUploadSuccess (file) {
      this.$Message.success('上传成功')
    },
    // 下载模板文件
    async downloadExcel () {
      if (this.currentType === 1) {
        XesAnalyticsTrack('15-5012')
      } else if (this.currentType === 2) {
        XesAnalyticsTrack('15-5016')
      }

      try {
        const params = {
          cityCode: this.cityCode,
          type: this.currentType,
          deptId:
            this.roleId === 2
              ? this.currentService.id
              : this.userInfo.crmDeptId,
          empNo: JSON.parse(Cookie.get('columbusUserInfo')).id
        }
        this.downloading = true

        const data = await downloadExcel(params)
        const fileName =
          this.currentType === 1
            ? `${this.userInfo.authDeptName}-服务中心目标模版.xlsx`
            : `${
              this.currentService.deptName || this.userInfo.authDeptName
            }-顾问目标模版.xlsx`
        util.downloadBolbFile(
          data,
          fileName,
          'application/vnd.ms-excel;charset=utf-8'
        )
      } catch (error) {
        this.$Message.error({
          content: `模版下载失败: ${error.message}`,
          duration: 5
        })
      }

      this.downloading = false
    },
    closeUploadModal () {
      this.openBatchSettings = false
      this.remark = ''
      this.files = []
      this.errorTargetData = []
    },
    // 上传目标
    async handleUploadTargetData () {
      if (this.currentType === 1) {
        XesAnalyticsTrack('15-5013')
      } else if (this.currentType === 2) {
        XesAnalyticsTrack('15-5017')
      }

      const files =
        this.files.filter((item) => item.status === 'finished') || []
      if (files.length === 0) {
        return this.$Message.warning({
          content: '请上传附件',
          duration: 5
        })
      }
      if (files.length < this.files.length) {
        return this.$Message.warning({
          content: '附件上传中，请稍后提交',
          duration: 5
        })
      }

      const params = {
        fileUrl: files[0].path,
        remark: this.remark.trim(),
        type: this.currentType,
        cityCode: this.cityCode
      }

      if (!params.remark) {
        return this.$Message.warning({
          content: '请填写变更备注',
          duration: 5
        })
      }

      try {
        this.uploading = true
        const data = await upLoadFile(params)
        if (data.execResult === '2') {
          this.errorTargetData = data.messageData
        } else {
          this.$refs[`targetTable${this.currentType}`].getTableData()
          this.closeUploadModal()
          this.$Message.success('上传成功')
        }
      } catch (error) {
        this.$Message.error({
          content: `批量设置失败: ${error.message}`,
          duration: 5
        })
      }
      this.uploading = false
    },
    handleBatchSetting (type) {
      this.openBatchSettings = true
      this.currentType = type
      if (type === 1) {
        XesAnalyticsTrack('15-5011')
      } else if (type === 2) {
        XesAnalyticsTrack('15-5015')
      }
    },
    handleSettingRecord (type) {
      this.openSettingsRecord = true
      this.currentType = type
      this.tableConfig.pageNo = 1
      this.tableConfig.pageSize = 10
      this.getOperateLog()
    },
    changePage (num) {
      this.tableConfig.pageNo = num
      this.getOperateLog()
    },
    pageSizeChange (size) {
      this.tableConfig.pageNo = 1
      this.tableConfig.pageSize = size
      this.getOperateLog()
    },
    // 查询操作日志
    async getOperateLog () {
      try {
        const params = {
          cityCode: this.cityCode,
          ...this.tableConfig
        }
        this.tableConfig.tableLoading = true
        this.operateLog = []
        const data = await getOperateLog(params)
        const mySorter = (a, b) => {
          if (/^\d/.test(a) ^ /^\D/.test(b)) return a > b ? 1 : a === b ? 0 : -1
          return a > b ? -1 : a === b ? 0 : 1
        }
        data.records.forEach((item) => {
          const targetMap = {}
          // 排序
          console.log('before', item.batchLogs.length)
          item.batchLogs =
            item.batchLogs.sort((a, b) => {
              let arr1 = a.target.split('-')
              let arr2 = b.target.split('-')
              return mySorter(arr1[arr1.length - 1], arr2[arr2.length - 1])
            }) || []

          // 重名抹去target
          item.batchLogs.forEach((item) => {
            if (targetMap[item.target]) {
              item.targetCopy = item.target
              item.target = ''
            } else {
              targetMap[item.target] = true
            }
          })

          if (item.batchLogs.length > 5) {
            item.batchLogsMore = [...item.batchLogs]
            item.batchLogs = item.batchLogs.slice(0, 5)
          }
        })
        this.operateLog = data.records
        this.tableConfig.totalCount = data.totalCount
      } catch (error) {
        this.$Message.error({
          content: `获取操作日志失败: ${error.message}`,
          duration: 5
        })
      }
      this.tableConfig.tableLoading = false
    }
  }
}
</script>

