<template>
    <!-- 取消试听 -->
    <div>
        <Form :label-width="120">
            <FormItem label="下次跟进时间: ">
                <DatePicker
                type="datetime" placeholder="请选择" style="width: 200px;"
                format="yyyy-MM-dd HH:mm:ss"
                v-model="formValidate.nextFollowTimeVal"
                :options="dateOptions"
                @on-change="dateChange($event, 'nextFollowTime')"
                ></DatePicker>
            </FormItem>
            <FormItem label="取消原因: " required>
                <Select v-model="formValidate.cancelReason" placeholder="请选择" style="width: 200px;" @on-change="onReasonChange">
                    <Option :value="item.id" v-for="(item, index) in cancelReasons" :key="index">{{ item.label }}</Option>
                </Select>
            </FormItem>
            <FormItem label=" " v-if="isShowOtherReason">
                <Input type="textarea" v-model="formValidate.cancelReasonExtra" placeholder="请输入其他原因" style="width: 200px" />
            </FormItem>
        </Form>
        <Row type="flex" justify="center" :gutter="20">
            <Col>
                <Button @click="cancel">取消</Button>
            </Col>
            <Col>
                <Button type="primary" @click="confirm" :loading="btnLoading">提交</Button>
            </Col>
        </Row>
    </div>
</template>

<script>
import { cancel } from '@/api/appointment'
export default {
  name: 'CancelAudition',
  props: {
    businessId: { // 记录id
      type: [String, Number],
      default: ''
    },
    appointmentId: { // 预约id
      type: [String, Number],
      default: ''
    },
    operationType: { // 类型 1: 取消试听预约
      type: Number,
      default: null
    },
    pyUid: { // 学员uid
      type: [String, Number],
      default: ''
    },
    cityCode: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      formValidate: {
        nextFollowTime: '', // 下次跟进时间
        nextFollowTimeVal: '', // 下次跟进时间
        cancelReason: '', // 取消原因
        cancelReasonExtra: '' // 其他取消原因
      },
      dateOptions: {
        disabledDate (date) {
          return date && date.valueOf() < Date.now() - 86400000
        }
      },
      btnLoading: false,
      isShowOtherReason: false, // 是否展示其他原因详细内容输入框
      cancelReasons: [
        { id: 1, label: '时间原因' },
        { id: 2, label: '距离远' },
        { id: 3, label: '已报名学而思课程' },
        { id: 4, label: '已报名其他机构课程' },
        { id: 5, label: '孩子不想来' },
        { id: 6, label: '其他原因' }
      ]
    }
  },
  methods: {
    onReasonChange (data) {
      if (data === 6) {
        this.isShowOtherReason = true
      } else {
        this.isShowOtherReason = false
      }
    },
    dateChange (val, typeName) {
      this.formValidate[typeName] = val
    },
    async confirm () {
      if (!this.formValidate.cancelReason) {
        this.$Message.warning({ content: '请选择取消原因' })
        return
      }

      if (this.formValidate.cancelReason === 6 && !this.formValidate.cancelReasonExtra.trim()) {
        this.$Message.warning({ content: '请填写其他原因详细描述' })
        return
      }
      const params = {
        ...this.formValidate,
        businessId: this.businessId,
        appointmentId: this.appointmentId,
        operationType: this.operationType,
        cancelType: this.operationType,
        pyUid: this.pyUid,
        cityCode: this.cityCode
      }

      try {
        this.btnLoading = true
        await cancel(params)
        setTimeout(() => {
          this.btnLoading = false
          this.$Message.success({ content: `取消试听成功` })
          this.$emit('success')
          this.cancel()
        }, 2000)
      } catch (error) {
        this.btnLoading = false
        this.$Message.error({ content: `取消试听失败: ${error.message}` })
      }
    },
    cancel () {
      this.$emit('cancel')
    }
  }
}
</script>
