<template>
  <Layout id="map">
    <TalIframe :content-src="dataSrc" />
    <div style="height: 50px"></div>
  </Layout>
</template>

<script>
import { generateImportWakeUp } from '@/api/diagnosis'
import TalIframe from '@/components/iframe'
export default {
  name: 'diagnosisImport',
  data () {
    return {
      dataSrc: ''
    }
  },
  components: { TalIframe },
  mounted () {
    generateImportWakeUp({
    }).then(data => {
      this.dataSrc = data.url
    }).catch(err => {
      this.$Message.error({
        content: err.message,
        duration: 5
      })
    })
  }
}
</script>
