  mounted () {
    this.searchFromNotice(this.$route)
    this.initView()
  },
  activated () {
    if (this.formData.cityCode && this.tableData.length) {
      this.getTableData()
    }
  },
  methods: {
    // 渠道标签列表
    queryDiagnosisChannels () {
      queryDiagnosisChannels({}).then(res => {
        this.diagnosisChannels = Object.freeze(res)
      }).catch(err => {
        this.$Message.error({ content: err.message, duration: 5 })
      })
    },

    // 导出数据
    exportDataClick () {
      let cityName = this.allCityList.filter(item => item.id === this.formData.cityCode)[0].name
      const params = Object.assign({}, this.formData, {
        areaCode: this.formData.cityCode,
        diagnosisStartTime: this.formData.diagnosisStartTime ? this.formData.diagnosisStartTime + ' ' + '00:00:00' : '',
        diagnosisEndTime: this.formData.diagnosisEndTime ? this.formData.diagnosisEndTime + ' ' + '23:59:59' : '',
        createStartTime: this.formData.createStartTime ? this.formData.createStartTime + ' ' + '00:00:00' : '',
        createEndTime: this.formData.createEndTime ? this.formData.createEndTime + ' ' + '23:59:59' : '',
        cityName,
        crmRoleId: this.$store.state.roleRelativeData.userInfo.crmRoleId
      })

      if (!params.createStartTime || !params.createEndTime) {
        return this.$Message.warning({ content: '请选择创建时间，最多支持选择三个月', duration: 3 })
      }

      this.btnLoading = true

      dataPath(params).then(({ id }) => {
        this.$Message.info({ content: '正在导出excel，请勿关闭当前标签页', duration: 5 });
        (function getFilePath (id, that) {
          exprotPerformanceById({ id }).then(data => {
            if (data.filePath) {
              setTimeout(() => {
                window.open(data.filePath)
                that.btnLoading = false
              }, 500)
            } else {
              setTimeout(() => {
                getFilePath(id, that)
              }, 2000)
            }
          }).catch(err => {
            that.btnLoading = false
            that.$Message.error({ content: err.message, duration: 5 })
          })
        })(id, this)
      }).catch(err => {
        this.btnLoading = false
        this.$Message.error({ content: err.message, duration: 5 })
      })
    },
    // 更新查询
    refreshSearch () {
      this.stuDetail.scoreRecordShow = false
      this.fllowUp.showModal = false
      this.showAddSubscribeModal = false
      setTimeout(() => {
        this.getTableData()
      }, 1500)
    },
    // 通过通知中心查询
    searchFromNotice (router) {
      const query = router.query
      if (query.cityCode && query.studentCard) {
        this.formData.stuCode = query.studentCard
        this.formData.cityCode = query.cityCode
        this.setDefaultServiceCenter = false
        this.cityYearChange(this.formData.cityCode)
        this.getTableData()
      }
    },
    // 远程搜索接待顾问
    getTreatAdvisorEmpNos (userName) {
      const params = {
        userName
      }
      getUserList(params).then(res => {
        this.treatAdvisorEmpNos = Object.freeze(res)
      }).catch(err => {
        this.$Message.error({ content: err.message, duration: 5 })
      })
    },
    // 获取搜索条件
    getCondition () {
      const params = {}
      condition(params).then(res => {
        this.conditions = Object.freeze(res)
      }).catch(err => {
        this.$Message.error({ content: err.message, duration: 5 })
      })
    },
    // 初始化
    initView () {
      this.getCitys()
      this.getYears()
      this.getCondition()
      this.queryDiagnosisChannels()
    },
    // 新增预约诊断
    onSubscribe () {
      this.$router.push({
        name: `addSubscribeDiagnosis`
      })
    },
    onSearch () {
      if (!this.formData.cityCode) {
        return this.$Message.warning({ content: '请选择城市', duration: 5 })
      }
      this.formData.pageNo = 1
      this.formData.pageSize = 10
      this.getTableData()
    },
    // 分页
    changePage (num) {
      this.formData.pageNo = num
      this.getTableData()
    },
    pageSizeChange (size) {
      this.formData.pageNo = 1
      this.formData.pageSize = size
      this.getTableData()
    },
    // 散诊记录查询
    getTableData () {
      const params = Object.assign({}, this.formData, {
        areaCode: this.formData.cityCode,
        diagnosisStartTime: this.formData.diagnosisStartTime ? this.formData.diagnosisStartTime + ' ' + '00:00:00' : '',
        diagnosisEndTime: this.formData.diagnosisEndTime ? this.formData.diagnosisEndTime + ' ' + '23:59:59' : '',
        createStartTime: this.formData.createStartTime ? this.formData.createStartTime + ' ' + '00:00:00' : '',
        createEndTime: this.formData.createEndTime ? this.formData.createEndTime + ' ' + '23:59:59' : ''
      })
      this.tableLoading = true
      queryDiagnosisRecord(params).then(res => {
        this.formData.totalCount = res.totalCount
        this.tableData = Object.freeze(res.records) || []
      }).catch(err => {
        this.formData.totalCount = 0
        this.$Message.error({ content: err.message, duration: 5 })
      }).finally(() => {
        this.tableLoading = false
      })
    },
    diagnosisImport () {
      this.$Modal.confirm({
        title: '提示',
        content: '<p>1、关联诊断报告，系统会按照“预约意向科目”匹配。</p><p>2、为避免转化数据遗漏，请在录入线下报告之前在散诊管理页面完成同学科的散诊预约。</p>',
        onOk: () => {
          this.$router.push('/operative/diagnosisImport')
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
/deep/ .filter-search .ivu-form-item-required .ivu-form-item-label {
  width: 10px !important;
  transform: translate(5px);
}
</style>
