<template>
    <div>
      <Modal
        title="查找报名学员"
        width='800'
        :footer-hide="true"
        v-model="searchStuModal">
          <TalStuSearch
            v-if="searchStuModal"
            ref="stuSearchForApply"
            :attendanceSearch='false'
            :showDetail='false'
            :showCrmInfo='false'
            :choice-btn-text='choiceBtnText'
            @stuInfo="setStuInfoForApplyHandle">
          </TalStuSearch>
      </Modal>
      <TalApplyModal
          :show="applyShow"
          :classId="applyParams.classId"
          :stuId="applyParams.studentId"
          :stuCityCode="applyParams.cityCode"
          :creatorEmpNo="applyParams.empNo"
          @success="applySuccess"
          @close="applyShow = false">
      </TalApplyModal>
     <Modal
        title="关联攒班记录"
        width='80%'
        :footer-hide="true"
        v-model="relatedModal">
        <Table
          border
          :columns="relatedCols"
          :data="relatedData"
        ></Table>
      </Modal>
    </div>
</template>

<script>
import { addStu, addSingleDemandRelate } from '@/api/assembleClass'
import { mapState } from 'vuex'
import Cookie from 'js-cookie'
export default {
  props: ['choiceBtnText', 'rowData'],
  data () {
    return {
      searchStuModal: false,
      applyParams: {
        classId: '',
        studentId: '',
        cityCode: '',
        empNo: ''
      },
      assembleStatusMap: {
        1: '待评估',
        2: '评估中',
        3: '开班成功',
        4: '开班失败',
        5: '攒班成功',
        6: '攒班失败',
        7: '取消'
      },
      applyShow: false,
      relatedCols: [
        {
          title: '操作',
          align: 'center',
          render: (h, params) => {
            return h('a', {
              on: {
                click: () => {
                  addSingleDemandRelate({
                    assembleClassDemandId: params.row.id,
                    assembleId: this.rowData.id
                  }).then(() => {
                    this.$Message.success('关联成功')
                    this.relatedModal = false
                    this.applyShow = true
                  }).catch(err => {
                    this.$Message.error({
                      content: '关联失败：' + err.message,
                      duration: 5
                    })
                  })
                }
              }
            }, '关联攒班记录')
          }
        },
        {
          title: '攒班状态',
          key: 'status',
          align: 'center',
          render: (h, params) => {
            return h('span', this.assembleStatusMap[params.row.status])
          }
        },
        {
          title: '学员姓名',
          key: 'name',
          align: 'center'
        },
        {
          title: '接待顾问',
          key: 'receptionAdvisorEmpName',
          align: 'center'
        },
        {
          title: '年级',
          key: 'gradeName',
          align: 'center'
        },
        {
          title: '创建人',
          key: 'creatorName',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.creatorName || '用户')
          }
        },
        {
          title: '创建时间',
          key: 'createDate',
          align: 'center'
        },
        {
          title: '推荐学员',
          key: 'recommendName',
          align: 'center'
        },
        {
          title: '攒班年份',
          key: 'year',
          align: 'center'
        },
        {
          title: '意向学科',
          key: 'subjectName',
          align: 'center'
        },
        {
          title: '意向学期',
          key: 'termName',
          align: 'center'
        },
        {
          title: '班次级别',
          key: 'classLevelName',
          align: 'center'
        },
        {
          title: '意向服务中心',
          key: 'serviceCenterName',
          align: 'center'
        },
        {
          title: '意向上课时间',
          key: 'classtimeTimeClassifys',
          align: 'center'
        },
        {
          title: '意向上课时段',
          key: 'classtimeTimePeriods',
          align: 'center'
        },
        {
          title: '意向度',
          key: 'intention',
          align: 'center'
        }
      ],
      relatedData: [],
      relatedModal: false
    }
  },
  created () {

  },
  mounted () {

  },
  watch: {
    ...mapState('roleRelativeData', ['userInfo'])
  },
  methods: {
    setStuInfoForApplyHandle (data) {
      this.searchStuModal = false
      this.applyParams.studentId = data.studentId
      this.applyParams.cityCode = data.cityCode
      this.applyParams.classId = this.rowData.classId
      this.applyParams.empNo = JSON.parse(Cookie.get('columbusUserInfo')).id
      if (this.choiceBtnText === '报名') {
        this.showRelatedAssembleClass(data.uid)
      } else {
        this.queryAddStu(data)
      }
      // this.applyShow = true
    },
    showRelatedAssembleClass (uid) {
      this.applyShow = true
      // queryStudentDemandListByAssembleId({
      //   uid,
      //   assembleId: this.rowData.assembleId
      // }).then(data => { // 查询出关联攒班记录时：关联后再报名，否则直接报名
      //   if (data && data.length) {
      //     this.relatedData = data
      //     this.relatedModal = true
      //   } else {
      //     this.applyShow = true
      //   }
      // }).catch(err => {
      //   this.$Message.error({
      //     content: '攒班记录条件查询单个学员需求列表错误：' + err.message,
      //     duration: 5
      //   })
      // })
    },
    queryAddStu (stuData) {
      let { uid, card, realName } = stuData
      let { groupWorkId, authDeptId, authDeptName, cityCode } = this.rowData
      let params = {
        groupWorkId: groupWorkId,
        uid,
        studentCard: card,
        studentName: realName,
        recommendServiceCenterId: authDeptId,
        recommendServiceCenterName: authDeptName,
        cityCode
      }
      addStu(params).then(res => {
        this.$Message.success({
          content: '添加学员成功',
          duration: 5
        })
        this.$emit('onSuccess', true)
      }).catch(err => {
        this.$Message.error({
          content: '添加学员失败：' + err.message,
          duration: 5
        })
      })
    },
    open () {
      this.searchStuModal = true
    },
    applySuccess () {
      this.$emit('onSuccess', true)
    }
  }
}
</script>

